import { useEffect } from '@wordpress/element';

import {
	rangeDeviceStyle,
	mainWrapperStyle,
	softMinifyCssStrings
} from '../../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		colWidth
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes,
		cssSelector: '.rtcl-block-column'
	})

	const {
		rangeDeviceDesk: colWidthStyleDesk,
		rangeDeviceTab: colWidthStyleTab,
		rangeDeviceMob: colWidthStyleMob
	} = rangeDeviceStyle({
		controlValue: colWidth,
		property: 'width'
	});


	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}{
			${colWidthStyleDesk}
			max-width: 100%;
		}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
		.${blockId}{
			${colWidthStyleTab}
		}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
		.${blockId}{
			${colWidthStyleMob}
		}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and (max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and (max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;