const { useEffect } = wp.element;
import Inspector from "./inspector";
import {
	InnerBlocks,
	useBlockProps
} from "@wordpress/block-editor";
import { select } from "@wordpress/data";
import { uniqueBlockId, blockEditorWrapperClass } from '../../helpers';
import styles from './styles';
import { Icons } from "../../components";
import './editor.scss';

function Edit(props) {
	const { isSelected, attributes, setAttributes, clientId, className } = props;
	const {
		blockId,
		preview,
		wrapperTag
	} = attributes;

	useEffect(() => {
		const BLOCK_PREFIX = 'rtcl-wrap-';
		uniqueBlockId(BLOCK_PREFIX, clientId, attributes, setAttributes);
	}, []);

	//generate all style
	const allStyles = styles({
		attributes,
		setAttributes
	})

	const { getBlockOrder } = select('core/block-editor');
	const editorWrapClass = blockEditorWrapperClass(useBlockProps, attributes, 'rtcl-block-wrapper');
	const WrapperTag = wrapperTag;

	//set preview
	if (preview) {
		return Icons.title_preview;
	}


	//render
	return [
		isSelected && (
			<Inspector
				attributes={attributes}
				setAttributes={setAttributes}
			/>
		),
		<style>{`${allStyles}`}</style>,
		<WrapperTag {...editorWrapClass}>
			<div className="rtcl-wrapper-inner">
				<InnerBlocks
					renderAppender={(
						getBlockOrder(clientId).length > 0 ? undefined :
							() => {
								if (getBlockOrder(clientId).length > 0) {
									return null;
								} else {
									return <InnerBlocks.ButtonBlockAppender />
								}
							}
					)}
				/>
			</div>
		</WrapperTag>
	]
}

export default Edit;