import GridStyle1 from "./layout/grid/style1";
import GridStyle2 from "./layout/grid/style2";
import GridStyle3 from "./layout/grid/style3";
import GridStyle4 from "./layout/grid/style4";
import GridStyle5 from "./layout/grid/style5";

import ListStyle1 from "./layout/list/style1";
import ListStyle2 from "./layout/list/style2";
import ListStyle3 from "./layout/list/style3";
import ListStyle4 from "./layout/list/style4";
import ListStyle5 from "./layout/list/style5";

function RenderView(props) {
	const {
		resultCount,
		catalogOrder,
		viewSwitcher,
		postResults,
		topListings,
		layout,
		listStyle,
		gridStyle
	} = props;

	const postData = [...topListings, ...postResults];;

	return (
		<>
			{(resultCount || catalogOrder || viewSwitcher) && (
				<div className="rtcl-listings-actions">
					{resultCount && (
						<div className="rtcl-result-count">Showing all 0 results</div>
					)}

					{catalogOrder && (
						<form className="rtcl-ordering" method="get" onSubmit={(e) => e.preventDefault()}>
							<select name="orderby" className="orderby1">
								<option value="title-asc">A to Z ( title )</option>
								<option value="title-desc">Z to A ( title )</option>
								<option value="date-desc" selected="selected">Recently added ( latest )</option>
								<option value="date-asc">Date added ( oldest )</option>
								<option value="views-desc">Most viewed</option>
								<option value="views-asc">Less viewed</option>
								<option value="price-asc">Price ( low to high )</option>
								<option value="price-desc">Price ( high to low )</option>
							</select>
						</form>
					)}

					{viewSwitcher && (
						<div className="rtcl-view-switcher">
							<a className="rtcl-view-trigger" onClick={(e) => e.preventDefault()} data-type="list" href="#"><i className="rtcl-icon rtcl-icon-th-list"> </i></a>
							<a className="rtcl-view-trigger active" onClick={(e) => e.preventDefault()} data-type="grid" href="#"><i className="rtcl-icon rtcl-icon-th-large"> </i></a>
						</div>
					)}

				</div>
			)}

			{(layout == 'grid' && gridStyle == 'style-1') && (
				<GridStyle1 {...props} data={postData} topListings={topListings} />
			)}
			{(layout == 'grid' && gridStyle == 'style-2') && (
				<GridStyle2 {...props} data={postData} topListings={topListings} />
			)}
			{(layout == 'grid' && gridStyle == 'style-3') && (
				<GridStyle3 {...props} data={postData} topListings={topListings} />
			)}
			{(layout == 'grid' && gridStyle == 'style-4') && (
				<GridStyle4 {...props} data={postData} topListings={topListings} />
			)}
			{(layout == 'grid' && gridStyle == 'style-5') && (
				<GridStyle5 {...props} data={postData} topListings={topListings} />
			)}

			{(layout == 'list' && listStyle == 'style-1') && (
				<ListStyle1 {...props} data={postData} topListings={topListings} />
			)}
			{(layout == 'list' && listStyle == 'style-2') && (
				<ListStyle2 {...props} data={postData} topListings={topListings} />
			)}
			{(layout == 'list' && listStyle == 'style-3') && (
				<ListStyle3 {...props} data={postData} topListings={topListings} />
			)}
			{(layout == 'list' && listStyle == 'style-4') && (
				<ListStyle4 {...props} data={postData} topListings={topListings} />
			)}
			{(layout == 'list' && listStyle == 'style-5') && (
				<ListStyle5 {...props} data={postData} topListings={topListings} />
			)}
		</>
	)
}

export default RenderView; 