import { InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, ToggleControl, ButtonGroup, Button } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import {
	AdvancedControl,
	Alignment,
	BorderDimension,
	Dimension,
	Color,
	RangeDevice,
	Range,
	Typography
} from '../../components';
import { NORMAL_HOVER } from '../../globals/Constants';

function Inspector({ attributes, setAttributes }) {
	const {
		resDevice,
		showView,
		showFavourit,
		showAbuse,
		showSocialShare,
		inlineStyle,
		actionTypo,
		actionColor,
		actionAlign,
		actionBorder,
		actionPadding,
		socialShareType,
		socialColor,
		socialBgColor,
		socialMargin,
		socialIconDimension,
		socialIconSize,
		socialIconGap,
		socialHoverColor,
		socialHoverBgColor,
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	return (
		<InspectorControls key={'controls'}>
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "styles",
							title: "Styles",
							className: "rtrb-tab-btn styles",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>
					{(tabs) => (
						<div className="rtrb-tab-content">
							{tabs.name === 'general' && (
								<>
									<PanelBody title={__('General', 'rtcl-elementor-builder')} initialOpen={true}>


										<ToggleControl
											label={__('Show Views', "rtcl-elementor-builder")}
											onChange={(showView) => setAttributes({ showView })}
											checked={showView}
										/>
										<ToggleControl
											label={__('Show Favourites', "rtcl-elementor-builder")}
											onChange={(showFavourit) => setAttributes({ showFavourit })}
											checked={showFavourit}
										/>
										<ToggleControl
											label={__('Show Report Abuse', "rtcl-elementor-builder")}
											onChange={(showAbuse) => setAttributes({ showAbuse })}
											checked={showAbuse}
										/>
										<ToggleControl
											label={__('Show Social Share', "rtcl-elementor-builder")}
											onChange={(showSocialShare) => setAttributes({ showSocialShare })}
											checked={showSocialShare}
										/>
										<ToggleControl
											label={__('Inline Style', "rtcl-elementor-builder")}
											onChange={(inlineStyle) => setAttributes({ inlineStyle })}
											checked={inlineStyle}
										/>
									</PanelBody>
								</>
							)}
							{tabs.name === 'styles' && (
								<>
									<PanelBody title={__('Action', "rtcl-elementor-builder")} initialOpen={true}>
										<Typography
											label={__('Typography', 'rtcl-elementor-builder')}
											value={actionTypo}
											onChange={(val) => setAttributes({ actionTypo: val })}
											resRequiredProps={resRequiredProps}
										/>
										<Color
											label={__("Color", "rtcl-elementor-builder")}
											color={actionColor}
											onChange={(actionColor) => setAttributes({ actionColor })}
										/>
										<Alignment
											label={__("Alignment", "rtcl-elementor-builder")}
											value={actionAlign}
											responsive={true}
											options={['left', 'center', 'right']}
											onChange={val => setAttributes({ actionAlign: val })}
											resRequiredProps={resRequiredProps}
										/>
										<BorderDimension
											value={actionBorder}
											onChange={(value) => {
												setAttributes({ actionBorder: value })
											}}
											resRequiredProps={resRequiredProps}
										/>
										<Dimension
											label={__('Padding', 'rtcl-elementor-builder')}
											type="padding"
											responsive={true}
											value={actionPadding}
											onChange={(actionPadding) => {
												setAttributes({ actionPadding })
											}}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>

									<PanelBody title={__('Social Share')} initialOpen={true}>
										<Dimension
											label={__("Margin", "rtcl-elementor-builder")}
											type="margin"
											responsive={true}
											value={socialMargin}
											onChange={(socialMargin) => {
												setAttributes({ socialMargin })
											}}
											resRequiredProps={resRequiredProps}
										/>
										<RangeDevice
											label={__('Social Icon Dimension', "rtcl-elementor-builder")}
											responsive={true}
											value={socialIconDimension}
											min={1}
											max={200}
											step={1}
											onChange={(val) => setAttributes({ socialIconDimension: val })}
											resRequiredProps={resRequiredProps}
										/>
										<Range
											label={__("Icon Size", "rtcl-elementor-builder")}
											reset={true}
											value={socialIconSize}
											onChange={(socialIconSize) => setAttributes({ socialIconSize })}
											min={1}
											max={40}
											step={1}
										/>
										<RangeDevice
											label={__("Icon Gap", "rtcl-elementor-builder")}
											responsive={true}
											value={socialIconGap}
											min={1}
											max={200}
											step={1}
											onChange={(val) => setAttributes({ socialIconGap: val })}
											resRequiredProps={resRequiredProps}
										/>

										<ButtonGroup className="rtrb-btn-group rtrb-btn-group-state">
											{NORMAL_HOVER.map((item) => (
												<Button
													isLarge
													variant={socialShareType === item.value ? 'primary' : 'secondary'}
													onClick={() => setAttributes({
														socialShareType: item.value
													})}
												>
													{item.label}
												</Button>
											))}
										</ButtonGroup>

										{socialShareType === "normal" && (
											<>
												<Color
													label={__("Color", "rtcl-elementor-builder")}
													color={socialColor}
													onChange={(socialColor) => setAttributes({ socialColor })}
												/>
												<Color
													label={__("Background", "rtcl-elementor-builder")}
													color={socialBgColor}
													onChange={(socialBgColor) => setAttributes({ socialBgColor })}
												/>

											</>
										)}

										{socialShareType === "hover" && (
											<>
												<Color
													label={__("Hover Color", "rtcl-elementor-builder")}
													color={socialHoverColor}
													onChange={(socialHoverColor) => setAttributes({ socialHoverColor })}
												/>
												<Color
													label={__("Hover Background", "rtcl-elementor-builder")}
													color={socialHoverBgColor}
													onChange={(socialHoverBgColor) => setAttributes({ socialHoverBgColor })}
												/>
											</>
										)}
									</PanelBody>
								</>
							)}
							{tabs.name === 'advance' && (
								<>
									<AdvancedControl
										attributes={attributes}
										setAttributes={setAttributes}
									/>
								</>
							)}
						</div>
					)}
				</TabPanel>
			</div>
		</InspectorControls>
	)

}

export default Inspector;