import { RenderDynamicTag } from '../../helpers';
function RenderView(props) {
	const {
		postResults,
		titleTag
	} = props;

	return (
		(Object.keys(postResults).length > 0 && postResults.title) ? (
			<RenderDynamicTag
				tagName={titleTag}
				className="rtcl-listings-header-title page-title" >
				{postResults.title}
			</RenderDynamicTag>
		) : (<h3>{__('Title does not add to the listing')}</h3>)
	)
}

export default RenderView; 