import { useEffect } from '@wordpress/element';
import {
	typographyStyle,
	mainWrapperStyle,
	softMinifyCssStrings,
	dimensionStyle
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		showOpenStatus,

		statusTextTypo,
		openStatusTextColor,
		closeStatusTextColor,
		tableDataTypo,
		tableLabelTypo,
		tableLabelColor,
		tableDataColor,
		tableBorderColor,
		tablePadding,
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});

	const {
		deskTypography: statusTextTypoStyleDesk,
		tabTypography: statusTextTypoStyleTab,
		mobTypography: statusTextTypoStyleMob
	} = typographyStyle({
		controlValue: statusTextTypo
	});

	const {
		deskTypography: tableLabelTypoStyleDesk,
		tabTypography: tableLabelTypoStyleTab,
		mobTypography: tableLabelTypoStyleMob
	} = typographyStyle({
		controlValue: tableLabelTypo
	});

	const {
		deskTypography: tableDataTypoStyleDesk,
		tabTypography: tableDataTypoStyleTab,
		mobTypography: tableDataTypoStyleMob
	} = typographyStyle({
		controlValue: tableDataTypo
	});

	const {
		deskDimension: tablePaddingStyleDesk,
		tabDimension: tablePaddingStyleTab,
		mobDimension: tablePaddingStyleMob
	} = dimensionStyle({
		controlValue: tablePadding,
		property: 'padding',
		important: true
	});


	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}.rtcl-block-frontend{
			margin-top: 15px;
			margin-bottom: 15px;
		}
		.${blockId} .rtclbh-block{
			margin:0;
			padding:0;
			border:0;
			background-color:inherit;
		}
		.${blockId} .rtclbh-status{
			${statusTextTypoStyleDesk}
		}
		.${blockId} .rtclbh-status-open{
			${openStatusTextColor ? `color:${openStatusTextColor};` : ''}
		}
		.${blockId} .rtclbh-status-closed{
			${closeStatusTextColor ? `color:${closeStatusTextColor};` : ''}
		}
		.${blockId} .business-hours th{
			${tableLabelTypoStyleDesk}
			${tableLabelColor ? `color:${tableLabelColor};` : ''}
		}
		.${blockId} .business-hours td{
			${tableDataTypoStyleDesk}
			${tableDataColor ? `color:${tableDataColor};` : ''}
		}
		.${blockId} .business-hours th,
		.${blockId} .business-hours td{
			${tablePaddingStyleDesk}
			${tableBorderColor ? `border-color:${tableBorderColor};` : ''}
		}
		.${blockId} .business-hours tr:last-child th,
		.${blockId} .business-hours tr:last-child td{
			border-bottom:0;
		}
		.${blockId} .business-hours tr th:last-child,
		.${blockId} .business-hours tr td:last-child{
			border-right:0;
		}
		.${blockId} .rtclbh-block .rtclbh{
			${tableBorderColor ? `border-color:${tableBorderColor};` : ''}
		}

		${!showOpenStatus ?
			`.${blockId} .rtclbh-status{display:none;}`
			: ''}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
		.${blockId} .rtclbh-status{
			${statusTextTypoStyleTab}
		}
		.${blockId} .business-hours th{
			${tableLabelTypoStyleTab}
		}
		.${blockId} .business-hours td{
			${tableDataTypoStyleTab}
		}
		.${blockId} .business-hours th,
		.${blockId} .business-hours td{
			${tablePaddingStyleTab}
		}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
		.${blockId} .rtclbh-status{
			${statusTextTypoStyleMob}
		}
		.${blockId} .business-hours th{
			${tableLabelTypoStyleMob}
		}
		.${blockId} .business-hours td{
			${tableDataTypoStyleMob}
		}
		.${blockId} .business-hours th,
		.${blockId} .business-hours td{
			${tablePaddingStyleMob}
		}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and(max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and(max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;