import { __ } from '@wordpress/i18n';
import { Spinner } from "@wordpress/components";
import Inspector from './inspector';
import { useBlockProps } from '@wordpress/block-editor';
import { useEffect } from '@wordpress/element';
import styles from './styles';
import { uniqueBlockId, blockEditorWrapperClass } from '../../helpers';
import { Icons } from '../../components';
import ServerSideRender from '@wordpress/server-side-render';

function Edit({ isSelected, attributes, setAttributes, clientId }) {
	const {
		blockId,
		preview,
		listingId
	} = attributes;

	useEffect(() => {
		const BLOCK_PREFIX = 'rtcl-booking-';
		uniqueBlockId(BLOCK_PREFIX, clientId, attributes, setAttributes);
		setAttributes({ listingId: rtclBlockParams?.lastPostId || '' })
	}, []);

	const editorWrapClass = blockEditorWrapperClass(useBlockProps, attributes, 'rtcl rtcl-block-booking single-rtcl_listing widget');

	//generate styles
	const allStyle = styles({
		attributes,
		setAttributes
	})

	const hasPosts = listingId.length;

	if (!hasPosts) {
		return [
			isSelected && (
				<Inspector
					attributes={attributes}
					setAttributes={setAttributes}
				/>
			),
			<div className="rtrb-spinner"><Spinner /></div>
		];
	}

	//set preview
	if (preview) {
		return Icons.title_preview;
	}

	return [
		isSelected && (
			<Inspector
				attributes={attributes}
				setAttributes={setAttributes}
			/>
		),

		<style>{`${allStyle}`}</style>,

		<div {...editorWrapClass}>
			<ServerSideRender
				className="rtcl-booking-form-rendered"
				block="rtcl/listing-booking"
				attributes={{ listingId, wrapClass: false }}
			/>
		</div>
	]

}

export default Edit;