import { useEffect } from '@wordpress/element';
import {
	mainWrapperStyle,
	softMinifyCssStrings,
	dimensionStyle,
	typographyStyle,
	boxshadowStyle
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		colBGColor,
		colPadding,
		contentPadding,
		colBorder,
		colBoxShadow,
		colHoverBoxShadow,

		titleTypo,
		titleColor,
		titleHoverColor,
		titleMargin,

		metaTypo,
		metaPadding,
		metaColor,
		metaIconColor,
		metaCatColor,
		metaHoverColor,
		metaIconHoverColor,
		metaCatHoverColor,

		badgeTypo,
		badgePadding,
		badgeMargin,
		soldBGColor,
		soldColor,
		newBGColor,
		newColor,
		featuredBGColor,
		featuredColor,
		topBGColor,
		topColor,
		popularBGColor,
		popularColor,
		bumpBGColor,
		bumpColor,

		priceTypo,
		priceBGColor,
		priceColor,
		unitTypo,
		unitLabelColor,
		priceMargin,

		btnBGColor,
		btnColor,
		btnBorderColor,
		btnBGHoverColor,
		btnHoverColor,
		btnHoverBorderColor,
		descTypo,
		descMargin,
		descColor,
		arrowBGColor,
		arrowBGHoverColor,
		arrowIconColor,
		arrowIconHoverColor,
		dotSpacing,
		dotBGColor,
		dotActiveBGColor,
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});

	const {
		deskDimension: colPaddingStyleDesk,
		tabDimension: colPaddingStyleTab,
		mobDimension: colPaddingStyleMob
	} = dimensionStyle({
		controlValue: colPadding,
		property: 'padding',
		important: true
	});

	const {
		deskDimension: contentPaddingStyleDesk,
		tabDimension: contentPaddingStyleTab,
		mobDimension: contentPaddingStyleMob
	} = dimensionStyle({
		controlValue: contentPadding,
		property: 'padding',
		important: true
	});

	const {
		deskDimension: colBorderStyleDesk,
		tabDimension: colBorderStyleTab,
		mobDimension: colBorderStyleMob
	} = dimensionStyle({
		controlValue: colBorder,
		property: 'border-width',
		important: true
	});

	const {
		boxshadowStyles: colBoxShadowStyle,
	} = boxshadowStyle({
		controlValue: colBoxShadow,
	});

	const {
		boxshadowStyles: colHoverBoxShadowStyle,
	} = boxshadowStyle({
		controlValue: colHoverBoxShadow,
	});

	const {
		deskTypography: titleTypoStyleDesk,
		tabTypography: titleTypoStyleTab,
		mobTypography: titleTypoStyleMob
	} = typographyStyle({
		controlValue: titleTypo
	});

	const {
		deskDimension: titleMarginStyleDesk,
		tabDimension: titleMarginStyleTab,
		mobDimension: titleMarginStyleMob
	} = dimensionStyle({
		controlValue: titleMargin,
		property: 'margin',
		important: true
	});

	const {
		deskTypography: metaTypoStyleDesk,
		tabTypography: metaTypoStyleTab,
		mobTypography: metaTypoStyleMob
	} = typographyStyle({
		controlValue: metaTypo
	});

	const {
		deskDimension: metaPaddingStyleDesk,
		tabDimension: metaPaddingStyleTab,
		mobDimension: metaPaddingStyleMob
	} = dimensionStyle({
		controlValue: metaPadding,
		property: 'padding',
		important: true
	});

	const {
		deskTypography: badgeTypoStyleDesk,
		tabTypography: badgeTypoStyleTab,
		mobTypography: badgeTypoStyleMob
	} = typographyStyle({
		controlValue: badgeTypo
	});

	const {
		deskDimension: badgePaddingStyleDesk,
		tabDimension: badgePaddingStyleTab,
		mobDimension: badgePaddingStyleMob
	} = dimensionStyle({
		controlValue: badgePadding,
		property: 'padding',
		important: true
	});

	const {
		deskDimension: badgeMarginStyleDesk,
		tabDimension: badgeMarginStyleTab,
		mobDimension: badgeMarginStyleMob
	} = dimensionStyle({
		controlValue: badgeMargin,
		property: 'margin',
		important: true
	});

	const {
		deskTypography: priceTypoStyleDesk,
		tabTypography: priceTypoStyleTab,
		mobTypography: priceTypoStyleMob
	} = typographyStyle({
		controlValue: priceTypo
	});

	const {
		deskDimension: priceMarginStyleDesk,
		tabDimension: priceMarginStyleTab,
		mobDimension: priceMarginStyleMob
	} = dimensionStyle({
		controlValue: priceMargin,
		property: 'margin',
		important: true
	});

	const {
		deskTypography: unitTypoStyleDesk,
		tabTypography: unitTypoStyleTab,
		mobTypography: unitTypoStyleMob
	} = typographyStyle({
		controlValue: unitTypo
	});

	const {
		deskTypography: descTypoStyleDesk,
		tabTypography: descTypoStyleTab,
		mobTypography: descTypoStyleMob
	} = typographyStyle({
		controlValue: descTypo
	});

	const {
		deskDimension: descMarginStyleDesk,
		tabDimension: descMarginStyleTab,
		mobDimension: descMarginStyleMob
	} = dimensionStyle({
		controlValue: descMargin,
		property: 'margin',
		important: true
	});

	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}.rtcl-block-frontend{
			margin-top: 15px;
			margin-bottom: 15px;
		}
		.${blockId}.rtcl .rtcl-block-related-listing .rtcl-listings .listing-item{
			${colBGColor ? `background-color:${colBGColor};` : ''}
			${colBorderStyleDesk}
			${colPaddingStyleDesk}
			${colBoxShadowStyle}
		}
		.${blockId}.rtcl .rtcl-block-related-listing .rtcl-listings .listing-item:hover{
			${colHoverBoxShadowStyle}
		}
		.${blockId}.rtcl .rtcl-grid-view .item-content{
			${contentPaddingStyleDesk}
		}

		.${blockId}.rtcl .listing-item .listing-title a{
			${titleColor ? `color:${titleColor};` : ''}
			${titleTypoStyleDesk}
		}
		.${blockId}.rtcl .listing-item .listing-title{
			${titleMarginStyleDesk}
		}
		.${blockId}.rtcl .listing-item .listing-title a:hover{
			${titleHoverColor ? `color:${titleHoverColor};` : ''}
		}

		.${blockId}.rtcl .rtcl-listings .rtcl-listing-meta-data{
			${metaPaddingStyleDesk}
		}
		.${blockId}.rtcl .rtcl-listings .rtcl-listing-meta-data li,
		.${blockId}.rtcl .rtcl-listings .listing-item .category>a{
			${metaTypoStyleDesk}
		}
		.${blockId}.rtcl .rtcl-listings .rtcl-listing-meta-data li{
			${metaColor ? `color:${metaColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-listings .rtcl-listing-meta-data li>i{
			${metaIconColor ? `color:${metaIconColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-listings .listing-item .category>a{
			${metaCatColor ? `color:${metaCatColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-listings .rtcl-listing-meta-data li:hover>i{
			${metaIconHoverColor ? `color:${metaIconHoverColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-listings .listing-item .category>a:hover{
			${metaCatHoverColor ? `color:${metaCatHoverColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-listings .rtcl-listing-meta-data li:hover{
			${metaHoverColor ? `color:${metaHoverColor};` : ''}
		}

		.${blockId}.rtcl .rtcl-listings .listing-item .rtcl-listing-badge-wrap{
			${badgeMarginStyleDesk}
		}
		.${blockId}.rtcl .rtcl-listings .listing-item .rtcl-listing-badge-wrap .badge{
			${badgeTypoStyleDesk}
			${badgePaddingStyleDesk}
		}
		.${blockId}.rtcl .rtcl-listings .listing-item .rtcl-sold-out{
			${soldBGColor ? `background-color:${soldBGColor};` : ''}
			${soldColor ? `color:${soldColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-listings .listing-item .rtcl-listing-badge-wrap .badge.rtcl-badge-new{
			${newBGColor ? `background-color:${newBGColor};` : ''}
			${newColor ? `color:${newColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-listings .listing-item .rtcl-listing-badge-wrap .badge.rtcl-badge-featured{
			${featuredBGColor ? `background-color:${featuredBGColor};` : ''}
			${featuredColor ? `color:${featuredColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-listings .listing-item .rtcl-listing-badge-wrap .badge.rtcl-badge-_top{
			${topBGColor ? `background-color:${topBGColor};` : ''}
			${topColor ? `color:${topColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-listings .listing-item .rtcl-listing-badge-wrap .badge.rtcl-badge-_top{
			${topBGColor ? `background-color:${topBGColor};` : ''}
			${topColor ? `color:${topColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-listings .listing-item .rtcl-listing-badge-wrap .badge.rtcl-badge-_bump_up{
			${bumpBGColor ? `background-color:${bumpBGColor};` : ''}
			${bumpColor ? `color:${bumpColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-listings .listing-item .rtcl-listing-badge-wrap .badge.rtcl-badge-popular.popular-badge.badge-success{
			${popularBGColor ? `background-color:${popularBGColor};` : ''}
			${popularColor ? `color:${popularColor};` : ''}
		}

		.${blockId}.rtcl .rtcl-listings .listing-item .item-price .rtcl-price{
			${priceTypoStyleDesk}
			${priceMarginStyleDesk}
			${priceBGColor ? `background-color:${priceBGColor};` : ''}
			${priceColor ? `color:${priceColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-listings .listing-item .item-price .rtcl-price-meta{
			${unitTypoStyleDesk}
			${unitLabelColor ? `color:${unitLabelColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-listings .rtcl-meta-buttons-wrap .rtcl-el-button,
		.${blockId}.rtcl .rtcl-listings .rtcl-meta-buttons-wrap .rtcl-el-button a .rtcl-icon{
			${btnColor ? `color:${btnColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-listings .rtcl-meta-buttons-wrap .rtcl-el-button:hover,
		.${blockId}.rtcl .rtcl-listings .rtcl-meta-buttons-wrap .rtcl-el-button:hover a .rtcl-icon{
			${btnHoverColor ? `color:${btnHoverColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-listings .rtcl-meta-buttons-wrap .rtcl-el-button a{
			${btnBGColor ? `background-color:${btnBGColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-listings .rtcl-meta-buttons-wrap .rtcl-el-button a:hover{
			${btnBGHoverColor ? `background-color:${btnBGHoverColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-listings .listing-item .item-content p.rtcl-excerpt,
		.${blockId}.rtcl .rtcl-listings .listing-item .item-content .rtcl-short-description p{
			${descTypoStyleDesk}
			${descMarginStyleDesk}
			${descColor ? `color:${descColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-slider-btn{
			${arrowBGColor ? `background-color:${arrowBGColor};` : ''}
			${arrowIconColor ? `color:${arrowIconColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-slider-btn:hover{
			${arrowBGHoverColor ? `background-color:${arrowBGHoverColor};` : ''}
			${arrowIconHoverColor ? `color:${arrowIconHoverColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-slider-pagination  .swiper-pagination-bullet.swiper-pagination-bullet-active{
			${dotActiveBGColor ? `background-color:${dotActiveBGColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-slider-pagination  .swiper-pagination-bullet{
			${dotBGColor ? `background-color:${dotBGColor};` : ''}
		}
		.${blockId}.rtcl .rtcl-slider-pagination.swiper-pagination-bullets{
			${dotSpacing ? `bottom:${dotSpacing}px;` : ''}
		}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
	.${blockId}.rtcl .rtcl-block-related-listing .rtcl-listings .listing-item{
		${colBorderStyleTab}
		${colPaddingStyleTab}
	}
	.${blockId}.rtcl .rtcl-grid-view .item-content{
		${contentPaddingStyleTab}
	}
	.${blockId}.rtcl .listing-item .listing-title a{
		${titleTypoStyleTab}
	}
	.${blockId}.rtcl .listing-item .listing-title{
		${titleMarginStyleTab}
	}
	.${blockId}.rtcl .rtcl-listing-meta-data{
		${metaPaddingStyleTab}
	}
	.${blockId}.rtcl .rtcl-listing-meta-data li,
	.${blockId}.rtcl .listing-item .listing-cat>a{
		${metaTypoStyleTab}
	}
	.${blockId}.rtcl .rtcl-listings .listing-item .rtcl-listing-badge-wrap{
		${badgeMarginStyleTab}
	}
	.${blockId}.rtcl .rtcl-listings .listing-item .rtcl-listing-badge-wrap .badge{
		${badgeTypoStyleTab}
		${badgePaddingStyleTab}
	}
	.${blockId}.rtcl .rtcl-listings .listing-item .item-price .rtcl-price{
		${priceTypoStyleTab}
		${priceMarginStyleTab}
	}
	.${blockId}.rtcl .rtcl-listings .listing-item .item-price .rtcl-price-meta{
		${unitTypoStyleTab}
	}

	.${blockId}.rtcl .rtcl-listings .listing-item .item-content p.rtcl-excerpt,
	.${blockId}.rtcl .rtcl-listings .listing-item .item-content .rtcl-short-description p{
		${descTypoStyleTab}
		${descMarginStyleTab}
	}

		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
	.${blockId}.rtcl .rtcl-block-related-listing .rtcl-listings .listing-item{
		${colBorderStyleMob}
		${colPaddingStyleMob}
	}
	.${blockId}.rtcl .rtcl-grid-view .item-content{
		${contentPaddingStyleMob}
	}
	.${blockId}.rtcl .listing-item .listing-title a{
		${titleTypoStyleMob}
	}
	.${blockId}.rtcl .listing-item .listing-title{
		${titleMarginStyleMob}
	}
	.${blockId}.rtcl .rtcl-listing-meta-data{
		${metaPaddingStyleMob}
	}
	.${blockId}.rtcl .rtcl-listing-meta-data li,
	.${blockId}.rtcl .listing-item .listing-cat>a{
		${metaTypoStyleMob}
	}
	.${blockId}.rtcl .rtcl-listings .listing-item .rtcl-listing-badge-wrap{
		${badgeMarginStyleMob}
	}
	.${blockId}.rtcl .rtcl-listings .listing-item .rtcl-listing-badge-wrap .badge{
		${badgeTypoStyleMob}
		${badgePaddingStyleMob}
	}
	.${blockId}.rtcl .rtcl-listings .listing-item .item-price .rtcl-price{
		${priceTypoStyleMob}
		${priceMarginStyleMob}
	}
	.${blockId}.rtcl .rtcl-listings .listing-item .item-price .rtcl-price-meta{
		${unitTypoStyleMob}
	}
	.${blockId}.rtcl .rtcl-listings .listing-item .item-content p.rtcl-excerpt,
	.${blockId}.rtcl .rtcl-listings .listing-item .item-content .rtcl-short-description p{
		${descTypoStyleMob}
		${descMarginStyleMob}
	}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and(max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and(max-width: 767px) {
			${mobAllStyles}
		}
	`;
	return allStyle
}

export default styles;