import { InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, ToggleControl, SelectControl, TextControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import {
	Alignment,
	Typography,
	Dimension,
	Color,
	AdvancedControl,
	RangeDevice,
	BorderDimension,
	BoxShadow
} from '../../components';

function Inspector({ attributes, setAttributes }) {
	const {
		resDevice,
		style,
		showFilterTitle,
		filterTitle,
		showCategory,
		showLocation,
		showRadiusSearch,
		showAdType,
		showPrice,
		hideEmptyCatLocation,
		showCount,
		showAjaxLoad,
		showCatLocationLink,
		showCatImageIcon,
		showRating,
		showCustomField,

		itemBoxBGColor,
		itemBoxMargin,
		itemBoxPadding,
		itemBoxBorder,
		itemBoxBoxShadow,

		itemTitleTypo,
		itemTitleBgColor,
		itemTitleColor,
		itemTitlePadding,
		filterBtnTypo,
		filterBtnBgColor,
		filterBtnColor,
		filterBtnPadding,
		filterBtnMargin,
		filterBtnBorder,
		filterBtnRadius
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	return (
		<InspectorControls key={'controls'}>
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "styles",
							title: "Styles",
							className: "rtrb-tab-btn styles",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>
					{(tabs) => (
						<div className="rtrb-tab-content">
							{tabs.name === 'general' && (
								<>
									<PanelBody title={__('General', 'rtcl-elementor-builder')} initialOpen={true}>

										<ToggleControl
											label={__('Show Filter Title', "rtcl-elementor-builder")}
											checked={showFilterTitle}
											onChange={(showFilterTitle) => setAttributes({ showFilterTitle })}
										/>

										{showFilterTitle && (
											<TextControl
												label={__('Filter Title', "rtcl-elementor-builder")}
												value={filterTitle}
												onChange={(filterTitle) => setAttributes({ filterTitle })}
											/>
										)}

										{/* <SelectControl
											options={[{ label: 'Style-1', value: 'style-1' },
											{ label: 'Style-2', value: 'style-2' }]}
											value={style}
											onChange={(style) => setAttributes({ style })}
										/> */}

										<ToggleControl
											label={__('Show Category', "rtcl-elementor-builder")}
											checked={showCategory}
											onChange={(showCategory) => setAttributes({ showCategory })}
										/>
										<ToggleControl
											label={__('Show Location', "rtcl-elementor-builder")}
											checked={showLocation}
											onChange={(showLocation) => setAttributes({ showLocation })}
										/>
										<ToggleControl
											label={__('Show Radius Search', "rtcl-elementor-builder")}
											checked={showRadiusSearch}
											onChange={(showRadiusSearch) => setAttributes({ showRadiusSearch })}
										/>
										<ToggleControl
											label={__('Show Ad Types', "rtcl-elementor-builder")}
											checked={showAdType}
											onChange={(showAdType) => setAttributes({ showAdType })}
										/>
										<ToggleControl
											label={__('Show Price', "rtcl-elementor-builder")}
											checked={showPrice}
											onChange={(showPrice) => setAttributes({ showPrice })}
										/>

										<ToggleControl
											label={__('Hide empty Category / Location', "rtcl-elementor-builder")}
											checked={hideEmptyCatLocation}
											onChange={(hideEmptyCatLocation) => setAttributes({ hideEmptyCatLocation })}
										/>
										<ToggleControl
											label={__('Show count for Category / Location', "rtcl-elementor-builder")}
											checked={showCount}
											onChange={(showCount) => setAttributes({ showCount })}
										/>

										<ToggleControl
											label={__('Ajax load for Category / Location to increase PageSpeed.', "rtcl-elementor-builder")}
											checked={showAjaxLoad}
											onChange={(showAjaxLoad) => setAttributes({ showAjaxLoad })}
										/>
										<ToggleControl
											label={__('All Categories / All Locations link', "rtcl-elementor-builder")}
											checked={showCatLocationLink}
											onChange={(showCatLocationLink) => setAttributes({ showCatLocationLink })}
										/>

										<ToggleControl
											label={__('Show category image / icon', "rtcl-elementor-builder")}
											checked={showCatImageIcon}
											onChange={(showCatImageIcon) => setAttributes({ showCatImageIcon })}
										/>
										<ToggleControl
											label={__('Search by Rating', "rtcl-elementor-builder")}
											checked={showRating}
											onChange={(showRating) => setAttributes({ showRating })}
										/>
										<ToggleControl
											label={__('Search by Custom Fields', "rtcl-elementor-builder")}
											checked={showCustomField}
											onChange={(showCustomField) => setAttributes({ showCustomField })}
										/>

									</PanelBody>
								</>
							)}

							{tabs.name === 'styles' && (
								<>
									<PanelBody title={__('Item Box', "rtcl-elementor-builder")} initialOpen={true}>
										<Color
											label={__("Background", "rtcl-elementor-builder")}
											color={itemBoxBGColor}
											onChange={(itemBoxBGColor) => setAttributes({
												itemBoxBGColor
											})}
										/>

										<Dimension
											label={__("Content Spacing", "rtcl-elementor-builder")}
											type="padding"
											responsive={true}
											value={itemBoxPadding}
											onChange={(itemBoxPadding) => {
												setAttributes({ itemBoxPadding })
											}}
											resRequiredProps={resRequiredProps}
										/>

										<Dimension
											label={__("Spacing", "rtcl-elementor-builder")}
											type="margin"
											responsive={true}
											value={itemBoxMargin}
											onChange={(itemBoxMargin) => {
												setAttributes({ itemBoxMargin })
											}}
											resRequiredProps={resRequiredProps}
										/>

										<BorderDimension
											value={itemBoxBorder}
											onChange={(itemBoxBorder) => {
												setAttributes({ itemBoxBorder })
											}}
											resRequiredProps={resRequiredProps}
										/>

										<PanelBody title={__('Shadow', 'rtcl-elementor-builder')} initialOpen={false}>
											<BoxShadow
												value={itemBoxBoxShadow}
												onChange={val => setAttributes({ itemBoxBoxShadow: val })}
											/>
										</PanelBody>

									</PanelBody>

									<PanelBody title={__("Title", "rtcl-elementor-builder")} initialOpen={false}>
										<Typography
											label={__('Typography', 'rtcl-elementor-builder')}
											value={itemTitleTypo}
											onChange={(val) => setAttributes({ itemTitleTypo: val })}
											resRequiredProps={resRequiredProps}
										/>
										<Color
											label={__("Background", "rtcl-elementor-builder")}
											color={itemTitleBgColor}
											onChange={(itemTitleBgColor) => setAttributes({ itemTitleBgColor })}
										/>
										<Color
											label={__("Color", "rtcl-elementor-builder")}
											color={itemTitleColor}
											onChange={(itemTitleColor) => setAttributes({ itemTitleColor })}
										/>

										<Dimension
											label={__('Padding', 'rtcl-elementor-builder')}
											type="padding"
											responsive={true}
											value={itemTitlePadding}
											onChange={(itemTitlePadding) => {
												setAttributes({ itemTitlePadding })
											}}
											resRequiredProps={resRequiredProps}
										/>

									</PanelBody>

									<PanelBody title={__('Button')} initialOpen={false}>
										<Typography
											label={__('Typography', 'rtcl-elementor-builder')}
											value={filterBtnTypo}
											onChange={(val) => setAttributes({ filterBtnTypo: val })}
											resRequiredProps={resRequiredProps}
										/>
										<Color
											label={__("Background", "rtcl-elementor-builder")}
											color={filterBtnBgColor}
											onChange={(filterBtnBgColor) => setAttributes({ filterBtnBgColor })}
										/>
										<Color
											label={__("Color", "rtcl-elementor-builder")}
											color={filterBtnColor}
											onChange={(filterBtnColor) => setAttributes({ filterBtnColor })}
										/>

										<Dimension
											label={__('Padding', 'rtcl-elementor-builder')}
											type="padding"
											responsive={true}
											value={filterBtnPadding}
											onChange={(filterBtnPadding) => {
												setAttributes({ filterBtnPadding })
											}}
											resRequiredProps={resRequiredProps}
										/>
										<Dimension
											label={__("Margin", "rtcl-elementor-builder")}
											type="margin"
											responsive={true}
											value={filterBtnMargin}
											onChange={(filterBtnMargin) => {
												setAttributes({ filterBtnMargin })
											}}
											resRequiredProps={resRequiredProps}
										/>
										<BorderDimension
											value={filterBtnBorder}
											onChange={(filterBtnBorder) => {
												setAttributes({ filterBtnBorder })
											}}
											resRequiredProps={resRequiredProps}
										/>
										<Dimension
											label={__("Border Radius", "rtcl-elementor-builder")}
											type="borderRadius"
											responsive={true}
											value={filterBtnRadius}
											onChange={(value) => {
												setAttributes({ filterBtnRadius: value })
											}}
											resRequiredProps={resRequiredProps}
										/>

									</PanelBody>
								</>
							)}

							{tabs.name === 'advance' && (
								<>
									<AdvancedControl
										attributes={attributes}
										setAttributes={setAttributes}
									/>
								</>
							)}

						</div>
					)}
				</TabPanel>
			</div>
		</InspectorControls>
	)

}

export default Inspector;