import { __ } from '@wordpress/i18n';
import classnames from 'classnames';

function Style1(props) {
	const {
		layout,
		listStyle,
		contentVisibility,
		content_limit,
		data,
	} = props;

	const itemWrapperClass = classnames([
		'rtcl-listings',
		`rtcl-${layout}-view rtcl-${listStyle}-view`
	]);

	return [
		data.length ? (
			<div className={itemWrapperClass}>
				{data.map((listing, index) => {
					return (
						<div key={index} className={Object.values(listing.classes).join(" ")}>

							{(contentVisibility["thumbnail"] && listing.thumbnail) ? (
								<div className="listing-thumb">
									<div className="listing-thumb-inner">
										<a href={listing.post_link} className="rtcl-media" dangerouslySetInnerHTML={{ __html: listing.thumbnail }}></a>
										{contentVisibility["sold"] && listing.sold ? <span dangerouslySetInnerHTML={{ __html: listing.sold }}></span> : ''}
									</div>
								</div>
							) : ''}

							<div className="item-content">

								{(contentVisibility["badge"] && listing.badges) ? (
									<div className="rtcl-listing-badge-wrap" dangerouslySetInnerHTML={{ __html: listing.badges }}></div>
								) : ''}

								{(contentVisibility["category"] && listing.categories) ? (
									<div className='category' dangerouslySetInnerHTML={{ __html: listing.categories }}></div>
								) : ''}

								{(contentVisibility["title"] && listing.title) ? (
									<h3 className="listing-title rtcl-listing-title"><a href={listing.post_link}>{listing.title}</a></h3>
								) : ''}

								{(contentVisibility["custom_feild"] && listing?.custom_feilds) ? (
									<div className="rtcl-custom-field-warp" dangerouslySetInnerHTML={{ __html: listing?.custom_feilds }} />
								) : ''}

								{(contentVisibility["list_content"] && listing.excerpt) ? (
									<p className="rtcl-excerpt" dangerouslySetInnerHTML={{ __html: listing.excerpt.split(' ', content_limit).join(' ') }}></p>
								) : ''}

								<ul className="rtcl-listing-meta-data">
									{(contentVisibility["listing_type"] && listing.listing_type) ? (
										<li className="listing-type"><i className="rtcl-icon rtcl-icon-tags"></i>{listing.listing_type}</li>
									) : ''}

									{(contentVisibility["date"] && listing.time) ? (
										<li className="updated"><i className="rtcl-icon rtcl-icon-clock"></i>{listing.time}</li>
									) : ''}
									{(contentVisibility["author"] && listing.author) ? (
										<li className="author"><i className="rtcl-icon rtcl-icon-user"></i>{listing.author}</li>
									) : ''}

									{(contentVisibility["location"] && listing.locations) ? (
										<li className="rt-location"><i className="rtcl-icon rtcl-icon-location"></i><span dangerouslySetInnerHTML={{ __html: listing.locations }}></span></li>
									) : ''}

									{(contentVisibility["view"] && listing.views) ? (
										<li className="rt-views"><i className="rtcl-icon rtcl-icon-eye"></i>{listing.views} {__("views", "rtcl-elementor-builder")}</li>
									) : ''}

								</ul>

								{(contentVisibility["price"] && listing.price) ? (
									<div className="item-price" dangerouslySetInnerHTML={{ __html: listing.price }}></div>
								) : ''}

							</div>

							<div className="right-content">

								{(contentVisibility["details_btn"] && listing.post_link) ? (
									<a className="rtin-details-button" href={listing.post_link}>{__("Details", "rtcl-elementor-builder")}</a>
								) : ''}

								<div className="rtcl-meta-buttons-withtext meta-button-count-3">

									{contentVisibility["favourit_btn"] && listing.favourite_link ? (
										<div className="rtcl-fav rtcl-text-el-button" dangerouslySetInnerHTML={{ __html: listing.favourite_link }}></div>
									) : ''}
									{contentVisibility["quick_btn"] && listing.quick_view ? (
										<div className="rtcl-text-el-button" dangerouslySetInnerHTML={{ __html: listing.quick_view }}></div>
									) : ''}
									{contentVisibility["compare_btn"] && listing.compare ? (
										<div className="rtcl-text-el-button" dangerouslySetInnerHTML={{ __html: listing.compare }}></div>
									) : ''}

								</div>

							</div>

						</div>
					)
				})}
			</div>
		) : ''
	]
}

export default Style1