import { useEffect } from '@wordpress/element';
import {
	rangeDeviceStyle,
	typographyStyle,
	mainWrapperStyle,
	softMinifyCssStrings,
	dimensionStyle
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		showStatus,
		showAddress,
		showPhone,
		showSocialMedia,
		showEmail,

		contactTypo,
		contactMargin,
		contactPadding,
		contactSocialIconWidth,
		contactSocialIconSize,
		contactFieldHeight,

		btnTypo,
		btnTextColor,
		btnHoverTextColor,
		btnBgColor,
		btnHoverBgColor,
		btnPadding,
		btnBorder
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});

	const {
		deskDimension: contactMarginStyleDesk,
		tabDimension: contactMarginStyleTab,
		mobDimension: contactMarginStyleMob
	} = dimensionStyle({
		controlValue: contactMargin,
		property: 'margin',
		important: true
	});

	const {
		deskDimension: contactPaddingStyleDesk,
		tabDimension: contactPaddingStyleTab,
		mobDimension: contactPaddingStyleMob
	} = dimensionStyle({
		controlValue: contactPadding,
		property: 'padding',
		important: true
	});

	const {
		deskTypography: contactTypoStyleDesk,
		tabTypography: contactTypoStyleTab,
		mobTypography: contactTypoStyleMob
	} = typographyStyle({
		controlValue: contactTypo
	});

	const {
		rangeDeviceDesk: contactSocialIconWidthStyleDesk,
		rangeDeviceTab: contactSocialIconWidthStyleTab,
		rangeDeviceMob: contactSocialIconWidthStyleMob
	} = rangeDeviceStyle({
		controlValue: contactSocialIconWidth,
		property: 'width',
		important: true
	});

	const {
		rangeDeviceDesk: contactSocialIconHeightStyleDesk,
		rangeDeviceTab: contactSocialIconHeightStyleTab,
		rangeDeviceMob: contactSocialIconHeightStyleMob
	} = rangeDeviceStyle({
		controlValue: contactSocialIconWidth,
		property: 'height',
		important: true
	});


	const {
		rangeDeviceDesk: contactSocialIconSizeStyleDesk,
		rangeDeviceTab: contactSocialIconSizeStyleTab,
		rangeDeviceMob: contactSocialIconSizeStyleMob
	} = rangeDeviceStyle({
		controlValue: contactSocialIconSize,
		property: 'font-size',
	});

	const {
		rangeDeviceDesk: contactFieldHeightStyleDesk,
		rangeDeviceTab: contactFieldHeightStyleTab,
		rangeDeviceMob: contactFieldHeightStyleMob
	} = rangeDeviceStyle({
		controlValue: contactFieldHeight,
		property: 'height',
	});

	const {
		deskDimension: btnPaddingStyleDesk,
		tabDimension: btnPaddingStyleTab,
		mobDimension: btnPaddingStyleMob
	} = dimensionStyle({
		controlValue: btnPadding,
		property: 'padding',
		important: true
	});

	const {
		deskTypography: btnTypoStyleDesk,
		tabTypography: btnTypoStyleTab,
		mobTypography: btnTypoStyleMob
	} = typographyStyle({
		controlValue: btnTypo
	});
	const {
		deskDimension: btnBorderStyleDesk,
		tabDimension: btnBorderStyleTab,
		mobDimension: btnBorderStyleMob
	} = dimensionStyle({
		controlValue: btnBorder,
		property: 'border-width',
		important: true
	});

	const deskAllStyles = softMinifyCssStrings(`
	${!showStatus ? `.${blockId} .store-information .store-info .store-info-item.store-hour{
		display:none;
	}`: ''}
	${!showAddress ? `.${blockId} .store-information .store-info .store-info-item.store-address{
		display:none;
	}`: ''}
	${!showPhone ? `.${blockId} .store-information .store-info .store-info-item.store-phone{
		display:none;
	}`: ''}
	${!showSocialMedia ? `.${blockId} .store-information .store-info .store-social-media{
		display:none;
	}`: ''}
	${!showEmail ? `.${blockId} .store-information .store-info .store-info-item.store-email{
		display:none;
	}`: ''}
	
	.${blockId}.store-content-wrap{
		background-color:inherit;
	}
	.${blockId}.rtcl-block-frontend{
		margin-top:15px;
		margin-bottom:15px;
	}
	.${blockId} .store-information .store-info .store-info-item{
		${contactMarginStyleDesk}
		${contactPaddingStyleDesk}
		${contactTypoStyleDesk}
	}
	.${blockId} .store-information input.form-control{
		${contactFieldHeightStyleDesk}
	}
	.${blockId} .store-information .store-info .store-social-media .rtcl-icon{
		${contactSocialIconHeightStyleDesk}
		${contactSocialIconWidthStyleDesk}
		${contactSocialIconSizeStyleDesk}
	}
	.${blockId} .store-information .store-info #store-email-area button.sc-submit{
		${btnPaddingStyleDesk}
		${btnTypoStyleDesk}
		${btnBorderStyleDesk}
		${btnTextColor ? `color:${btnTextColor}!important;` : ''}
		${btnBgColor ? `background-color:${btnBgColor}!important;` : ''}
	}
	.${blockId} .store-information .store-info #store-email-area button.sc-submit:hover{
		${btnHoverTextColor ? `color:${btnHoverTextColor}!important;` : ''}
		${btnHoverBgColor ? `background-color:${btnHoverBgColor}!important;` : ''}
	}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
	.${blockId} .store-information .store-info .store-info-item{
		${contactMarginStyleTab}
		${contactPaddingStyleTab}
		${contactTypoStyleTab}
	}
	.${blockId} .store-information input.form-control{
		${contactFieldHeightStyleTab}
	}
	.${blockId} .store-information .store-info .store-social-media .rtcl-icon{
		${contactSocialIconHeightStyleTab}
		${contactSocialIconWidthStyleTab}
		${contactSocialIconSizeStyleTab}
	}
	.${blockId} .store-information .store-info #store-email-area button.sc-submit{
		${btnPaddingStyleTab}
		${btnTypoStyleTab}
		${btnBorderStyleTab}
	}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
	.${blockId} .store-information .store-info .store-info-item{
		${contactMarginStyleMob}
		${contactPaddingStyleMob}
		${contactTypoStyleMob}
	}
	.${blockId} .store-information input.form-control{
		${contactFieldHeightStyleMob}
	}
	.${blockId} .store-information .store-info .store-social-media .rtcl-icon{
		${contactSocialIconHeightStyleMob}
		${contactSocialIconWidthStyleMob}
		${contactSocialIconSizeStyleMob}
	}
	.${blockId} .store-information .store-info #store-email-area button.sc-submit{
		${btnPaddingStyleMob}
		${btnTypoStyleMob}
		${btnBorderStyleMob}
	}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and (max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and (max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;