import { addFilter } from '@wordpress/hooks';

//slider settings hook callback function
const sidebarSliderSettings = (defaultSettings, attributes) => {
	const {
		sliderOptions,
		slidesItem
	} = attributes;

	const sliderSettings = {
		...defaultSettings,
		slidesPerView: parseInt(slidesItem['lg']) ? parseInt(slidesItem['lg']) : 4,
		slidesPerGroup: parseInt(slidesItem['lg']) ? parseInt(slidesItem['lg']) : 4,
		spaceBetween: parseInt(sliderOptions["spaceBetween"]),
		speed: parseInt(sliderOptions["autoPlaySlideSpeed"]),
		loop: Boolean(sliderOptions["loop"]),
		autoHeight: Boolean(sliderOptions["autoHeight"]),

		// Responsive breakpoints
		breakpoints: {
			0: {
				slidesPerView: parseInt(slidesItem['sm']) ? parseInt(slidesItem['sm']) : 1,
				slidesPerGroup: parseInt(slidesItem['sm']) ? parseInt(slidesItem['sm']) : 1
			},
			576: {
				slidesPerView: parseInt(slidesItem['sm']) ? parseInt(slidesItem['sm']) : 2,
				slidesPerGroup: parseInt(slidesItem['sm']) ? parseInt(slidesItem['sm']) : 2
			},
			768: {
				slidesPerView: parseInt(slidesItem['md']) ? parseInt(slidesItem['md']) : 4,
				slidesPerGroup: parseInt(slidesItem['md']) ? parseInt(slidesItem['md']) : 4
			},
			1200: {
				slidesPerView: parseInt(slidesItem['lg']) ? parseInt(slidesItem['lg']) : 4,
				slidesPerGroup: parseInt(slidesItem['lg']) ? parseInt(slidesItem['lg']) : 4
			}
		}
	}

	if (sliderOptions.arrowNavigation) {
		sliderSettings.navigation = {
			nextEl: '.rtcl-slider-btn.button-left.rtcl-icon-angle-left',
			prevEl: '.rtcl-slider-btn.button-right.rtcl-icon-angle-right',
		};
	}

	if (sliderOptions.dotNavigation) {
		sliderSettings.pagination = {
			el: '.rtcl-slider-pagination',
			type: 'bullets',
			clickable: true
		};
	} else {
		sliderSettings.pagination = false;
	}

	if (sliderOptions.autoPlay) {
		sliderSettings.autoplay = {
			delay: parseInt(sliderOptions.autoPlayDelay),
			pauseOnMouseEnter: Boolean(sliderOptions.stopOnHover),
			disableOnInteraction: false
		};
	}

	return sliderSettings;
}

//slider settings hook
addFilter(
	'blocks.sliderSettings',
	'rtcl/related-listing',
	sidebarSliderSettings
);