import { __ } from '@wordpress/i18n';
import StyleGrid from './layout/grid/style-grid';
import StyleGrid2 from './layout/grid/style-grid2';
import StyleGrid3 from './layout/grid/style-grid3';
import StyleGrid4 from './layout/grid/style-grid4';
import StyleGrid5 from './layout/grid/style-grid5';

function RenderView(props) {
	const {
		style,
		postResults,
		sliderSettings
	} = props;
	return (
		<>
			{style == '1' && <StyleGrid {...props} data={postResults} />}
			{style == '2' && <StyleGrid2 {...props} data={postResults} />}
			{style == '3' && <StyleGrid3 {...props} data={postResults} />}
			{style == '4' && <StyleGrid4 {...props} data={postResults} />}
			{style == '5' && <StyleGrid5 {...props} data={postResults} />}
		</>
	)
}

export default RenderView; 