import { __ } from '@wordpress/i18n';
import { useEffect, useRef } from '@wordpress/element';
import { dispatch } from "@wordpress/data";

function RenderView(props) {
	const {
		clientId,
		postResults,
		enableSlider,
		showBadge,
		enableThumbSlider,
		showLightboxIcon,
		showArrow,
		showVideo,
		galleryImageSize
	} = props;

	var slider1;
	var slider2;

	const sliderSettings1 = {
		slidesPerView: 1,
		centeredSlides: true,
		loop: false,
		loopedSlides: 6,
		navigation: {
			nextEl: '.swiper-button-next',
			prevEl: '.swiper-button-prev',
		},
	}

	const sliderSettings2 = {
		slidesPerView: 'auto',
		spaceBetween: 5,
		centeredSlides: true,
		loop: false,
		slideToClickedSlide: true,
	}

	const swiperSliderInit1 = function (sliderE, sliderOptions) {
		let options = Object.assign({}, sliderOptions);
		if (sliderE?.swiper) {
			sliderE.swiper.destroy();
		}
		slider1 = new Swiper(sliderE, options);
	};
	const swiperSliderInit2 = function (sliderE, sliderOptions) {
		let options = Object.assign({}, sliderOptions);
		if (sliderE?.swiper) {
			sliderE.swiper.destroy();
		}
		slider2 = new Swiper(sliderE, options);
	};


	const swiperSliderReff1 = useRef(null);
	if (swiperSliderReff1.current) {
		swiperSliderReff1.current.addEventListener("click", function () {
			dispatch("core/block-editor").selectBlock(clientId);
			dispatch("core/edit-post").openGeneralSidebar("edit-post/block");
		});
	}
	const swiperSliderReff2 = useRef(null);
	if (swiperSliderReff2.current) {
		swiperSliderReff2.current.addEventListener("click", function () {
			dispatch("core/block-editor").selectBlock(clientId);
			dispatch("core/edit-post").openGeneralSidebar("edit-post/block");
		});
	}

	//slider initialize
	useEffect(() => {
		if (enableSlider) {
			swiperSliderInit1(swiperSliderReff1.current, sliderSettings1);
			swiperSliderInit2(swiperSliderReff2.current, sliderSettings2);
			slider1.controller.control = slider2;
			slider2.controller.control = slider1;
		}
	}, [enableSlider, showVideo, showArrow, enableThumbSlider, galleryImageSize]);

	const totalGalleryItem = postResults?.images.length + postResults?.video_url.length;

	const getVideoEmbedUrl = (url) => {
		let embedUrl = null;
		const youtubeRegex = /(?:youtu\.be\/|youtube\.com\/(?:embed\/|v\/|watch\?v=|watch\?.+&v=))([^&]{11})/;
		const youtubeMatch = url.match(youtubeRegex);
		if (youtubeMatch && youtubeMatch[1]) {
			const videoId = youtubeMatch[1];
			embedUrl = `https://www.youtube.com/embed/${videoId}`;
		}
		const vimeoRegex = /vimeo\.com\/(?:video\/|channels\/(?:\w+\/)?|groups\/(?:[^\/]*)\/videos\/|album\/(?:\d+)\/video\/|)(\d+)/;
		const vimeoMatch = url.match(vimeoRegex);
		if (vimeoMatch && vimeoMatch[1]) {
			const videoId = vimeoMatch[1];
			embedUrl = `https://player.vimeo.com/video/${videoId}`;
		}
		return embedUrl;
	}

	return (
		(Object.keys(postResults).length > 0 && (postResults?.images.length > 0 || postResults?.video_url.length > 0)) ? (

			<div id="rtcl-slider-wrapper" className="el-single-addon rtcl-slider-wrapper">


				<div ref={swiperSliderReff1} className={`rtcl-slider ${enableSlider ? '' : 'off'}`}>
					{showLightboxIcon && (
						<a href="#" className="rtcl-listing-gallery__trigger">
							<i className="rtcl-icon-search"></i>
						</a>
					)}

					{(showBadge && postResults?.is_sold) && (
						<span className="rtcl-sold-out">Sold Out</span>
					)}

					<div className="swiper-wrapper">

						{(postResults?.video_url.length > 0 && showVideo) ? postResults?.video_url.map((item, index) => (
							<div className="swiper-slide rtcl-slider-item rtcl-slider-video-item">
								<iframe
									className="rtcl-lightbox-iframe"
									src={getVideoEmbedUrl(item)}
									style={{
										margin: 0,
										padding: 0,
										backgroundColor: '#000'
									}}
									frameborder="0"
									webkitAllowFullScreen
									mozallowfullscreen
									allowFullScreen
								></iframe>
							</div>
						)) : ''}

						{postResults?.images.length > 0 ? postResults?.images.map((item, index) => (
							<div className="swiper-slide rtcl-slider-item" keys={index}>
								<img src={item.url} alt={item.title} />
							</div>
						)) : ''}

					</div>

					{showArrow && (
						<>
							<div className="swiper-button-next"></div>
							<div className="swiper-button-prev"></div>
						</>
					)}
				</div>

				{(enableThumbSlider && enableSlider && totalGalleryItem > 1) && (
					<div ref={swiperSliderReff2} className="rtcl-slider-nav">
						<div className="swiper-wrapper">

							{(postResults?.video_thumb && showVideo) && (
								<div className="swiper-slide rtcl-slider-thumb-item rtcl-slider-video-thumb">
									<img src={postResults?.video_thumb} className="rtcl-gallery-thumbnail" alt="image" />
								</div>
							)}

							{postResults?.images.length > 0 ? postResults?.images.map((item, index) => (
								<div className="swiper-slide rtcl-slider-thumb-item" keys={index}>
									<img src={item.url} alt={item.title} />
								</div>
							)) : ''}

						</div>
					</div>
				)}

			</div>
		) : (
			<h3>{__('Images or video does not add to the listing')}</h3>
		)
	)
}

export default RenderView; 