import { __ } from '@wordpress/i18n';
import { printHtmlStringMarkup } from '../../helpers'
function RenderView(props) {
	const {
		postResults,
		style
	} = props;

	return (
		(Object.keys(postResults).length > 0 && postResults.price) ? (
			<div className={`rtcl el-single-addon item-price ${style || ''}`} dangerouslySetInnerHTML={printHtmlStringMarkup(postResults.price)} />
		) : (<h3>{__('Price does not add to the listing')}</h3>)
	)
}

export default RenderView; 