import { __ } from '@wordpress/i18n';
import { printHtmlStringMarkup } from '../../helpers'
function RenderView(props) {
	const {
		postResults,
		style
	} = props;

	return (
		(Object.keys(postResults).length > 0 && postResults?.badge) ? (
			<div className='rtcl el-single-addon single-listing-meta-wrap' dangerouslySetInnerHTML={printHtmlStringMarkup(postResults.badge)} />
		) : (
			<h3>{__('Badge does not add to the listing', 'rtcl-elementor-builder')}</h3>
		)
	)
}

export default RenderView; 