import { useEffect } from '@wordpress/element';
import Inspector from "./inspector";
import { InnerBlocks, useBlockProps } from "@wordpress/block-editor";
import { select } from '@wordpress/data';
import styles from './styles';
import { uniqueBlockId, blockEditorWrapperClass } from '../../../helpers';

function Edit(props) {
	const { isSelected, attributes, setAttributes, clientId } = props;

	const {
		blockId,
	} = attributes;

	useEffect(() => {
		const BLOCK_PREFIX = 'rtcl-column-'
		uniqueBlockId(BLOCK_PREFIX, clientId, attributes, setAttributes)
	}, []);

	//generate all style
	const allStyles = styles({
		attributes,
		setAttributes
	})

	const { getBlockOrder } = select('core/block-editor');

	const editorWrapClass = blockEditorWrapperClass(useBlockProps, attributes);

	return [
		isSelected && (
			<Inspector
				attributes={attributes}
				setAttributes={setAttributes}
			/>
		),

		<div {...editorWrapClass}>
			<style>{`${allStyles}`}</style>
			<div className="rtcl-block-column">
				<InnerBlocks
					templateLock={false}
					renderAppender={(
						getBlockOrder(clientId).length > 0 ? undefined :
							() => {
								if (getBlockOrder(clientId).length > 0) {
									return null;
								} else {
									return <InnerBlocks.ButtonBlockAppender />
								}
							}
					)}
				/>
			</div>
		</div>

	]
}

export default Edit;