import { InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, ToggleControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import {
	Alignment,
	Typography,
	Dimension,
	Color,
	AdvancedControl
} from '../../components';

function Inspector({ attributes, setAttributes }) {
	const {
		resDevice,
		dropCap,
		descAlignment,
		descSpacing,
		descTypo,
		descColor,
		dropCapTypo,
		dropCapColor
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	return (
		<InspectorControls key={'controls'}>
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "styles",
							title: "Styles",
							className: "rtrb-tab-btn styles",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>
					{(tabs) => (
						<div className="rtrb-tab-content">
							{tabs.name === 'general' && (
								<>
									<PanelBody title={__('General', 'rtcl-elementor-builder')} initialOpen={true}>
										<ToggleControl
											label={__('Dropcap', 'rtcl-elementor-builder')}
											checked={dropCap}
											onChange={(dropCap) => setAttributes({ dropCap })}
										/>

										<Alignment
											label={__("Text Alignment", "rtcl-elementor-builder")}
											value={descAlignment}
											responsive={true}
											options={['left', 'center', 'right', 'justify']}
											onChange={val => setAttributes({ descAlignment: val })}
											resRequiredProps={resRequiredProps}
										/>

									</PanelBody>
								</>
							)}

							{tabs.name === 'styles' && (
								<>
									<PanelBody title={__("Description", "rtcl-elementor-builder")} initialOpen={true}>

										<Typography
											label={__('Typography', 'rtcl-elementor-builder')}
											value={descTypo}
											onChange={(val) => setAttributes({ descTypo: val })}
											resRequiredProps={resRequiredProps}
										/>

										<Color
											label={__("Color", "rtcl-elementor-builder")}
											color={descColor}
											onChange={(descColor) => setAttributes({ descColor })}
										/>

										<Dimension
											label={__("Spacing", "rtcl-elementor-builder")}
											type="margin"
											responsive={true}
											value={descSpacing}
											onChange={(descSpacing) => {
												setAttributes({ descSpacing })
											}}
											resRequiredProps={resRequiredProps}
										/>

									</PanelBody>

									{dropCap && (
										<PanelBody title={__('DropCap')}>
											<Typography
												label={__('Typography', 'rtcl-elementor-builder')}
												value={dropCapTypo}
												onChange={(val) => setAttributes({ dropCapTypo: val })}
												resRequiredProps={resRequiredProps}
											/>

											<Color
												label={__("Color", "rtcl-elementor-builder")}
												color={dropCapColor}
												onChange={(dropCapColor) => setAttributes({ dropCapColor })}
											/>
										</PanelBody>
									)}
								</>
							)}

							{tabs.name === 'advance' && (
								<>
									<AdvancedControl
										attributes={attributes}
										setAttributes={setAttributes}
									/>
								</>
							)}

						</div>
					)}
				</TabPanel>
			</div>
		</InspectorControls>
	)

}

export default Inspector;