import { __ } from '@wordpress/i18n';
import { printHtmlStringMarkup, RenderDynamicTag } from '../../helpers'
function RenderView(props) {
	const {
		pageHeaderInfo,
		titleTag,
		style
	} = props;

	return (
		Object.keys(pageHeaderInfo).length > 0 ? (
			<div className={`rtcl el-single-addon header-inner-wrapper ${style ? `header-${style}` : ''}`} >
				<header className="rtcl-listing-header">
					<RenderDynamicTag className="rtcl-listings-header-title page-title" tagName={titleTag}>{pageHeaderInfo.title}</RenderDynamicTag>
				</header>
				<div className="breadcrumb-section" dangerouslySetInnerHTML={printHtmlStringMarkup(pageHeaderInfo.breadcrumb)} />
			</div>
		) : (
			<h3>{__('Page header does not add to the listing', "rtcl-elementor-builder")}</h3>
		)
	)
}

export default RenderView; 