import { useEffect } from '@wordpress/element';
import {
	rangeDeviceStyle,
	typographyStyle,
	mainWrapperStyle,
	softMinifyCssStrings,
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		showLabel,
		labelText,
		showFacebook,
		showTwitter,
		showYoutube,
		showInstagram,
		showLinkedin,
		showPinterest,
		showReddit,
		labelTypo,
		iconColor,
		iconHoverColor,
		iconSize,
		iconSpacing,
		iconAlign
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});

	const {
		rangeDeviceDesk: iconAlignStyleDesk,
		rangeDeviceTab: iconAlignStyleTab,
		rangeDeviceMob: iconAlignStyleMob
	} = rangeDeviceStyle({
		controlValue: iconAlign,
		property: 'justify-content',
	});


	const {
		rangeDeviceDesk: iconSpacingStyleDesk,
		rangeDeviceTab: iconSpacingStyleTab,
		rangeDeviceMob: iconSpacingStyleMob
	} = rangeDeviceStyle({
		controlValue: iconSpacing,
		property: 'gap',
	});

	const {
		deskTypography: labelTypoStyleDesk,
		tabTypography: labelTypoStyleTab,
		mobTypography: labelTypoStyleMob
	} = typographyStyle({
		controlValue: labelTypo
	});


	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}.rtcl-block-frontend{
			margin-top: 15px;
			margin-bottom: 15px;
		}
		.${blockId} .rtcl-social-profile-wrap{
			${iconAlignStyleDesk}
			background-color: inherit;
			padding:0;
			border:none;
			box-shadow: none;
			background:inherit;
			border-radius:0;
		}

		.${blockId} .rtcl-social-profile-wrap .rtcl-social-profiles{
			${iconSpacingStyleDesk}
		}

		.${blockId} .rtcl-social-profile-wrap .rtcl-social-profiles a{
			${iconColor ? `color:${iconColor};` : ''}
			${iconSize ? `font-size:${iconSize}px;` : ''}
			transition:all .25s;
		}

		${!showFacebook ?
			`.${blockId} .rtcl-social-profiles a:nth-child(1){display:none;}`
			: ''}
		${!showTwitter ?
			`.${blockId} .rtcl-social-profiles a:nth-child(2){display:none;}`
			: ''}
		${!showYoutube ?
			`.${blockId} .rtcl-social-profiles a:nth-child(3){display:none;}`
			: ''}
	
		${!showInstagram ?
			`.${blockId} .rtcl-social-profiles a:nth-child(4){display:none;}`
			: ''}
			${!showLinkedin ?
			`.${blockId} .rtcl-social-profiles a:nth-child(5){display:none;}`
			: ''}
		${!showPinterest ?
			`.${blockId} .rtcl-social-profiles a:nth-child(6){display:none;}`
			: ''}
		
		${!showReddit ?
			`.${blockId} .rtcl-social-profiles a:nth-child(7){display:none;}`
			: ''}

		${!showLabel ?
			`.${blockId} .rtcl-social-profile-label{display:none;}`
			: ''}
		
		.${blockId} .rtcl-social-profile-wrap .rtcl-social-profiles a:hover{
			${iconHoverColor ? `color:${iconHoverColor};` : ''}
		}

		.${blockId} .rtcl-social-profile-label{
			${labelTypoStyleDesk}
		}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
	.${blockId} .single-listing-list-wrap{
			${iconAlignStyleTab}
		}

		.${blockId} .rtcl-social-profile-wrap .rtcl-social-profiles{
			${iconSpacingStyleTab}
		}

		.${blockId} .rtcl-social-profile-label{
			${labelTypoStyleTab}
		}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
	.${blockId} .single-listing-list-wrap{
			${iconAlignStyleMob}
		}

		.${blockId} .rtcl-social-profile-wrap .rtcl-social-profiles{
			${iconSpacingStyleMob}
		}

		.${blockId} .rtcl-social-profile-label{
			${labelTypoStyleMob}
		}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and(max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and(max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;