import { useEffect } from '@wordpress/element';
import {
	mainWrapperStyle,
	softMinifyCssStrings,
	rangeDeviceStyle
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		videoWidth,
		videoHeight
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});

	const {
		rangeDeviceDesk: videoWidthStyleDesk,
		rangeDeviceTab: videoWidthStyleTab,
		rangeDeviceMob: videoWidthStyleMob
	} = rangeDeviceStyle({
		controlValue: videoWidth,
		property: 'width',
		important: true
	});

	const {
		rangeDeviceDesk: videoHeightStyleDesk,
		rangeDeviceTab: videoHeightStyleTab,
		rangeDeviceMob: videoHeightStyleMob
	} = rangeDeviceStyle({
		controlValue: videoHeight,
		property: 'height',
		important: true
	});

	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}.rtcl-block-frontend{
			margin-top: 15px;
			margin-bottom: 15px;
		}
		.${blockId} .rtcl-lightbox-iframe {
			${videoWidthStyleDesk}
			${videoHeightStyleDesk}
		}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
		.${blockId} .rtcl-lightbox-iframe {
			${videoWidthStyleTab}
			${videoHeightStyleTab}
		}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
		.${blockId} .rtcl-lightbox-iframe {
			${videoWidthStyleMob}
			${videoHeightStyleMob}
		}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and(max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and(max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;