import { __ } from '@wordpress/i18n';
import { printHtmlStringMarkup } from '../../helpers'
function RenderView(props) {
	const {
		postResults
	} = props;

	return (
		(Object.keys(postResults).length > 0 && postResults?.business_hours) ? (
			<div className="rtcl el-single-addon business-hours" dangerouslySetInnerHTML={printHtmlStringMarkup(postResults?.business_hours)} />
		) : (
			<h3>{__('Business Hours does not add to the listing', 'rtcl-elementor-builder')}</h3>
		)
	)
}

export default RenderView; 