import { InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, ToggleControl, SelectControl, TextControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import {
	Typography,
	Color,
	AdvancedControl,
	Dimension,
	BorderDimension
} from '../../components';

function Inspector({ attributes, setAttributes }) {
	const {
		resDevice,
		showAuthor,
		showAuthorImg,
		showLocation,
		showContactNum,
		showContactForm,
		showOnlineStatus,
		showSellerWebsite,

		dataTypo,
		labelTypo,
		labelColor,
		dataColor,
		itemPadding,
		itemBorder,
		btnTypo,
		btnTextColor,
		btnHoverTextColor,
		btnBgColor,
		btnHoverBgColor,
		btnPadding
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	return (
		<InspectorControls key={'controls'}>
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "styles",
							title: "Styles",
							className: "rtrb-tab-btn styles",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>
					{(tabs) => (
						<div className="rtrb-tab-content">
							{tabs.name === 'general' && (
								<>
									<PanelBody title={__('General', 'rtcl-elementor-builder')} initialOpen={true}>
										<ToggleControl
											label={__('Show Author', "rtcl-elementor-builder")}
											checked={showAuthor}
											onChange={(showAuthor) => setAttributes({ showAuthor })}
										/>
										{showAuthor && (
											<ToggleControl
												label={__('Show Author Image', "rtcl-elementor-builder")}
												checked={showAuthorImg}
												onChange={(showAuthorImg) => setAttributes({ showAuthorImg })}
											/>
										)}

										<ToggleControl
											label={__('Show Location', "rtcl-elementor-builder")}
											checked={showLocation}
											onChange={(showLocation) => setAttributes({ showLocation })}
										/>
										<ToggleControl
											label={__('Show Contact Number', "rtcl-elementor-builder")}
											checked={showContactNum}
											onChange={(showContactNum) => setAttributes({ showContactNum })}
										/>
										<ToggleControl
											label={__('Show Contact Form', "rtcl-elementor-builder")}
											checked={showContactForm}
											onChange={(showContactForm) => setAttributes({ showContactForm })}
										/>
										<ToggleControl
											label={__('Show Online Status', "rtcl-elementor-builder")}
											checked={showOnlineStatus}
											onChange={(showOnlineStatus) => setAttributes({ showOnlineStatus })}
										/>
										<ToggleControl
											label={__('Show Seller Website', "rtcl-elementor-builder")}
											checked={showSellerWebsite}
											onChange={(showSellerWebsite) => setAttributes({ showSellerWebsite })}
										/>
									</PanelBody>
								</>
							)}

							{tabs.name === 'styles' && (
								<>
									<PanelBody title={__('Style')} initialOpen={true}>
										<Typography
											label={__('Label Typography', 'rtcl-elementor-builder')}
											value={labelTypo}
											onChange={(labelTypo) => setAttributes({ labelTypo })}
											resRequiredProps={resRequiredProps}
										/>
										<Typography
											label={__('Data Typography', 'rtcl-elementor-builder')}
											value={dataTypo}
											onChange={(dataTypo) => setAttributes({ dataTypo })}
											resRequiredProps={resRequiredProps}
										/>
										<Color
											label={__('Label Color', 'rtcl-elementor-builder')}
											color={labelColor}
											onChange={(labelColor) => setAttributes({ labelColor })}
										/>
										<Color
											label={__('Data Color', 'rtcl-elementor-builder')}
											color={dataColor}
											onChange={(dataColor) => setAttributes({ dataColor })}
										/>
										<Dimension
											label={__('Padding', 'rtcl-elementor-builder')}
											type="padding"
											responsive={true}
											value={itemPadding}
											onChange={(itemPadding) => {
												setAttributes({ itemPadding })
											}}
											resRequiredProps={resRequiredProps}
										/>
										<BorderDimension
											value={itemBorder}
											onChange={(value) => {
												setAttributes({ itemBorder: value })
											}}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>

									<PanelBody title={__('Button')} initialOpen={false}>
										<Typography
											label={__('Typography', 'rtcl-elementor-builder')}
											value={btnTypo}
											onChange={(btnTypo) => setAttributes({ btnTypo })}
											resRequiredProps={resRequiredProps}
										/>
										<Color
											label={__('Text Color', 'rtcl-elementor-builder')}
											color={btnTextColor}
											onChange={(btnTextColor) => setAttributes({ btnTextColor })}
										/>
										<Color
											label={__('Hover Text Color', 'rtcl-elementor-builder')}
											color={btnHoverTextColor}
											onChange={(btnHoverTextColor) => setAttributes({ btnHoverTextColor })}
										/>
										<Color
											label={__("Background", "rtcl-elementor-builder")}
											color={btnBgColor}
											onChange={(btnBgColor) => setAttributes({ btnBgColor })}
										/>
										<Color
											label={__("Hover Background", "rtcl-elementor-builder")}
											color={btnHoverBgColor}
											onChange={(btnHoverBgColor) => setAttributes({ btnHoverBgColor })}
										/>
										<Dimension
											label={__('Button Padding', 'rtcl-elementor-builder')}
											type="padding"
											responsive={true}
											value={btnPadding}
											onChange={(btnPadding) => {
												setAttributes({ btnPadding })
											}}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>

								</>
							)}

							{tabs.name === 'advance' && (
								<>
									<AdvancedControl
										attributes={attributes}
										setAttributes={setAttributes}
									/>
								</>
							)}
						</div>
					)}
				</TabPanel>
			</div>
		</InspectorControls>
	)

}

export default Inspector;