import { InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, ToggleControl, SelectControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import {
	Alignment,
	Typography,
	Dimension,
	Color,
	AdvancedControl,
	RangeDevice
} from '../../components';

function Inspector({ attributes, setAttributes }) {
	const {
		resDevice,
		showType,
		showDate,
		showUser,
		showCategory,
		showLocation,
		showViews,

		metaAlign,
		metaMargin,
		metaSpacing,
		metaTypo,
		metaIconColor,
		metaTextColor,
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	return (
		<InspectorControls key={'controls'}>
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "styles",
							title: "Styles",
							className: "rtrb-tab-btn styles",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>
					{(tabs) => (
						<div className="rtrb-tab-content">
							{tabs.name === 'general' && (
								<>
									<PanelBody title={__('General', 'rtcl-elementor-builder')} initialOpen={true}>
										<ToggleControl
											label={__('Show Type', "rtcl-elementor-builder")}
											checked={showType}
											onChange={(showType) => setAttributes({ showType })}
										/>

										<ToggleControl
											label={__('Show Date', "rtcl-elementor-builder")}
											checked={showDate}
											onChange={(showDate) => setAttributes({ showDate })}
										/>

										<ToggleControl
											label={__('Show User', "rtcl-elementor-builder")}
											checked={showUser}
											onChange={(showUser) => setAttributes({ showUser })}
										/>

										<ToggleControl
											label={__('Show Category', "rtcl-elementor-builder")}
											checked={showCategory}
											onChange={(showCategory) => setAttributes({ showCategory })}
										/>

										<ToggleControl
											label={__('Show Location', "rtcl-elementor-builder")}
											checked={showLocation}
											onChange={(showLocation) => setAttributes({ showLocation })}
										/>

										<ToggleControl
											label={__('Show Views', "rtcl-elementor-builder")}
											checked={showViews}
											onChange={(showViews) => setAttributes({ showViews })}
										/>

										<Alignment
											label={__("Text Alignment", "rtcl-elementor-builder")}
											value={metaAlign}
											responsive={true}
											options={['left', 'center', 'right']}
											onChange={val => setAttributes({ metaAlign: val })}
											resRequiredProps={resRequiredProps}
										/>

									</PanelBody>
								</>
							)}

							{tabs.name === 'styles' && (
								<>
									<PanelBody title={__('Meta')} initialOpen={true}>

										<Typography
											label={__('Typography', 'rtcl-elementor-builder')}
											value={metaTypo}
											onChange={(val) => setAttributes({ metaTypo: val })}
											resRequiredProps={resRequiredProps}
										/>

										<Color
											label={__('Text Color', "rtcl-elementor-builder")}
											color={metaTextColor}
											onChange={(metaTextColor) => setAttributes({ metaTextColor })}
										/>

										<Color
											label={__('Icon Color', "rtcl-elementor-builder")}
											color={metaIconColor}
											onChange={(metaIconColor) => setAttributes({ metaIconColor })}
										/>

										<RangeDevice
											label={__('Gap', "rtcl-elementor-builder")}
											responsive={true}
											value={metaSpacing}
											min={1}
											max={200}
											step={1}
											onChange={(val) => setAttributes({ metaSpacing: val })}
											resRequiredProps={resRequiredProps}
										/>

										<Dimension
											label={__("Margin", "rtcl-elementor-builder")}
											type="margin"
											responsive={true}
											value={metaMargin}
											onChange={(metaMargin) => {
												setAttributes({ metaMargin })
											}}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>

								</>
							)}

							{tabs.name === 'advance' && (
								<>
									<AdvancedControl
										attributes={attributes}
										setAttributes={setAttributes}
									/>
								</>
							)}

						</div>
					)}
				</TabPanel>
			</div>
		</InspectorControls>
	)

}

export default Inspector;