import { __ } from '@wordpress/i18n';
import { printHtmlStringMarkup } from '../../helpers';
import { useEffect } from '@wordpress/element';

function RenderView(props) {
	const {
		postResults
	} = props;

	useEffect(() => {
		var $ = jQuery;
		$(document).ready(function () {
			$('.rtcl-map.embed-responsive-item').each(function () {
				rtcl_render_map(this);
			});
		})
	}, []);


	return (
		(Object.keys(postResults).length > 0 && postResults?.map) ? (
			<div className="rtcl el-single-addon rtin-content-area" dangerouslySetInnerHTML={printHtmlStringMarkup(postResults?.map)} />
		) : (
			<h3>{__('Map does not add to the listing')}</h3>
		)
	)
}

export default RenderView; 