import { useEffect } from '@wordpress/element';
import {
	mainWrapperStyle,
	softMinifyCssStrings,
	dimensionStyle,
	typographyStyle,
	rangeDeviceStyle
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		mainBackground,
		mainMargin,
		mainPadding,

		sectionTitleTypo,
		sectionTitleColor,
		averageRatingTypo,
		averageRatingColor,
		averageRatingBorder,

		headerBtnTypo,
		headerBtnTextColor,
		headerBtnHoverTextColor,
		headerBtnBgColor,
		headerBtnHoverBgColor,
		headerBtnBorderColor,
		headerBtnPadding,

		commentListPadding,
		commentListMargin,
		commentListGap,
		authorTypo,
		commentListRatingGap,
		commentListTitleTypo,
		commentListTitleGap,

		formBgColor,
		formTitleTypo,
		formTitleGap,
		formLabelTypo,
		formFieldHeight,
		formTextareaHeight,
		formFieldBorderColor,

		formBtnTypo,
		formBtnTextColor,
		formBtnHoverTextColor,
		formBtnBgColor,
		formBtnHoverBgColor,
		formBtnPadding,
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});
	const {
		deskDimension: mainPaddingStyleDesk,
		tabDimension: mainPaddingStyleTab,
		mobDimension: mainPaddingStyleMob
	} = dimensionStyle({
		controlValue: mainPadding,
		property: 'padding',
		important: true
	});

	const {
		deskDimension: mainMarginStyleDesk,
		tabDimension: mainMarginStyleTab,
		mobDimension: mainMarginStyleMob
	} = dimensionStyle({
		controlValue: mainMargin,
		property: 'margin',
		important: true
	});
	const {
		deskTypography: sectionTitleTypoStyleDesk,
		tabTypography: sectionTitleTypoStyleTab,
		mobTypography: sectionTitleTypoStyleMob
	} = typographyStyle({
		controlValue: sectionTitleTypo
	});

	const {
		deskTypography: averageRatingTypoStyleDesk,
		tabTypography: averageRatingTypoStyleTab,
		mobTypography: averageRatingTypoStyleMob
	} = typographyStyle({
		controlValue: averageRatingTypo
	});


	const {
		deskDimension: averageRatingBorderStyleDesk,
		tabDimension: averageRatingBorderStyleTab,
		mobDimension: averageRatingBorderStyleMob
	} = dimensionStyle({
		controlValue: averageRatingBorder,
		property: 'border-width',
		important: true
	});

	const {
		deskTypography: headerBtnTypoStyleDesk,
		tabTypography: headerBtnTypoStyleTab,
		mobTypography: headerBtnTypoStyleMob
	} = typographyStyle({
		controlValue: headerBtnTypo
	});


	const {
		deskDimension: headerBtnPaddingStyleDesk,
		tabDimension: headerBtnPaddingStyleTab,
		mobDimension: headerBtnPaddingStyleMob
	} = dimensionStyle({
		controlValue: headerBtnPadding,
		property: 'padding',
		important: true
	});

	const {
		deskDimension: commentListPaddingStyleDesk,
		tabDimension: commentListPaddingStyleTab,
		mobDimension: commentListPaddingStyleMob
	} = dimensionStyle({
		controlValue: commentListPadding,
		property: 'padding',
		important: true
	});
	const {
		deskDimension: commentListMarginStyleDesk,
		tabDimension: commentListMarginStyleTab,
		mobDimension: commentListMarginStyleMob
	} = dimensionStyle({
		controlValue: commentListMargin,
		property: 'margin',
		important: true
	});
	const {
		deskTypography: authorTypoStyleDesk,
		tabTypography: authorTypoStyleTab,
		mobTypography: authorTypoStyleMob
	} = typographyStyle({
		controlValue: authorTypo
	});

	const {
		rangeDeviceDesk: commentListGapStyleDesk,
		rangeDeviceTab: commentListGapStyleTab,
		rangeDeviceMob: commentListGapStyleMob
	} = rangeDeviceStyle({
		controlValue: commentListGap,
		property: 'gap',
	});

	const {
		rangeDeviceDesk: commentListRatingGapStyleDesk,
		rangeDeviceTab: commentListRatingGapStyleTab,
		rangeDeviceMob: commentListRatingGapStyleMob
	} = rangeDeviceStyle({
		controlValue: commentListRatingGap,
		property: 'margin-top',
	});

	const {
		rangeDeviceDesk: commentListTitleGapStyleDesk,
		rangeDeviceTab: commentListTitleGapStyleTab,
		rangeDeviceMob: commentListTitleGapStyleMob
	} = rangeDeviceStyle({
		controlValue: commentListTitleGap,
		property: 'margin-bottom',
	});

	const {
		deskTypography: commentListTitleTypoStyleDesk,
		tabTypography: commentListTitleTypoStyleTab,
		mobTypography: commentListTitleTypoStyleMob
	} = typographyStyle({
		controlValue: commentListTitleTypo
	});

	const {
		deskTypography: formTitleTypoStyleDesk,
		tabTypography: formTitleTypoStyleTab,
		mobTypography: formTitleTypoStyleMob
	} = typographyStyle({
		controlValue: formTitleTypo
	});

	const {
		rangeDeviceDesk: formTitleGapStyleDesk,
		rangeDeviceTab: formTitleGapStyleTab,
		rangeDeviceMob: formTitleGapStyleMob
	} = rangeDeviceStyle({
		controlValue: formTitleGap,
		property: 'margin-bottom',
	});

	const {
		deskTypography: formLabelTypoStyleDesk,
		tabTypography: formLabelTypoStyleTab,
		mobTypography: formLabelTypoStyleMob
	} = typographyStyle({
		controlValue: formLabelTypo
	});

	const {
		rangeDeviceDesk: formFieldHeightStyleDesk,
		rangeDeviceTab: formFieldHeightStyleTab,
		rangeDeviceMob: formFieldHeightStyleMob
	} = rangeDeviceStyle({
		controlValue: formFieldHeight,
		property: 'height',
	});

	const {
		rangeDeviceDesk: formTextareaHeightStyleDesk,
		rangeDeviceTab: formTextareaHeightStyleTab,
		rangeDeviceMob: formTextareaHeightStyleMob
	} = rangeDeviceStyle({
		controlValue: formTextareaHeight,
		property: 'height',
	});

	const {
		deskTypography: formBtnTypoStyleDesk,
		tabTypography: formBtnTypoStyleTab,
		mobTypography: formBtnTypoStyleMob
	} = typographyStyle({
		controlValue: formBtnTypo
	});

	const {
		deskDimension: formBtnPaddingStyleDesk,
		tabDimension: formBtnPaddingStyleTab,
		mobDimension: formBtnPaddingStyleMob
	} = dimensionStyle({
		controlValue: formBtnPadding,
		property: 'padding',
		important: true
	});

	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}.rtcl-block-frontend{
			margin-top: 15px;
			margin-bottom: 15px;
		}
		.${blockId} .rtcl-Reviews.rtcl{
			${mainBackground ? `background-color:${mainBackground} !important;` : ''}
			${mainMarginStyleDesk}
			${mainPaddingStyleDesk}
		}
		.${blockId} .rtcl-single-listing-section-title{
			${sectionTitleColor ? `color:${sectionTitleColor};` : ''}
			${sectionTitleTypoStyleDesk}
		}
		.${blockId} .rtcl-Reviews.rtcl #comments .rtcl-reviews-meta .listing-meta .listing-meta-rating{
			${averageRatingColor ? `color:${averageRatingColor};` : ''}
			${averageRatingBorderStyleDesk}
			${averageRatingTypoStyleDesk}
		}
		.${blockId} .rtcl-Reviews.rtcl #comments .rtcl-reviews-meta .rtcl-reviews-meta-action a{
			${headerBtnTextColor ? `color:${headerBtnTextColor};` : ''}
			${headerBtnBgColor ? `background-color:${headerBtnBgColor};` : ''}
			${headerBtnBorderColor ? `border-color:${headerBtnBorderColor};` : ''}
			${headerBtnPaddingStyleDesk}
			${headerBtnTypoStyleDesk}
		}
		.${blockId} .rtcl-Reviews.rtcl #comments .rtcl-reviews-meta .rtcl-reviews-meta-action a:hover{
			${headerBtnHoverTextColor ? `color:${headerBtnHoverTextColor};` : ''}
			${headerBtnHoverBgColor ? `background-color:${headerBtnHoverBgColor};` : ''}
		}

		.${blockId} .rtcl-Reviews.rtcl #comments ol.comment-list li{
			${commentListPaddingStyleDesk}
			${commentListMarginStyleDesk}
		}
		.${blockId} .rtcl-Reviews.rtcl .each-comment.media{
			${commentListGapStyleDesk}
		}
		.${blockId} .rtcl-Reviews.rtcl .star-rating{
			${commentListRatingGapStyleDesk}
		}
		.${blockId} .rtcl-Reviews.rtcl .comment-title{
			${commentListTitleTypoStyleDesk}
			${commentListTitleGapStyleDesk}
		}
		.${blockId} #respond form input, 
		.${blockId} #respond form textarea{
			${formBgColor ? `background-color:${formBgColor};` : ''}
			${formFieldBorderColor ? `border-color:${formFieldBorderColor};` : ''}
		}
		.${blockId} #respond form input{
			${formFieldHeightStyleDesk}
		}
		.${blockId} #respond form textarea{
			${formTextareaHeightStyleDesk}
		}
		.${blockId} .rtcl-Reviews.rtcl #respond .comment-reply-title{
			${formTitleTypoStyleDesk}
			${formTitleGapStyleDesk}
		}
		.${blockId} .rtcl-Reviews.rtcl #respond .comment-form label{
			${formLabelTypoStyleDesk}
		}
		.${blockId} #review-form #respond .form-submit input{
			${formBtnTextColor ? `color:${formBtnTextColor}!important;` : ''}
			${formBtnBgColor ? `background-color:${formBtnBgColor}!important;` : ''}
			${formBtnPaddingStyleDesk}
			${formBtnTypoStyleDesk}
		}
		.${blockId} #review-form #respond .form-submit input:hover{
			${formBtnHoverTextColor ? `color:${formBtnHoverTextColor};` : ''}
			${formBtnHoverBgColor ? `background-color:${formBtnHoverBgColor};` : ''}
		}

		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
	.${blockId} .rtcl-Reviews.rtcl{
		${mainMarginStyleTab}
		${mainPaddingStyleTab}
	}
	.${blockId} .rtcl-single-listing-section-title{
		${sectionTitleTypoStyleTab}
	}
	.${blockId} .rtcl-Reviews.rtcl #comments .rtcl-reviews-meta .listing-meta .listing-meta-rating{
		${averageRatingBorderStyleTab}
		${averageRatingTypoStyleTab}
	}
	.${blockId} .rtcl-Reviews.rtcl #comments .rtcl-reviews-meta .rtcl-reviews-meta-action a{
		${headerBtnPaddingStyleTab}
		${headerBtnTypoStyleTab}
	}
	.${blockId} .rtcl-Reviews.rtcl #comments ol.comment-list li{
		${commentListPaddingStyleTab}
		${commentListMarginStyleTab}
	}
	.${blockId} .rtcl-Reviews.rtcl .each-comment.media{
		${commentListGapStyleTab}
	}
	.${blockId} .rtcl-Reviews.rtcl .star-rating{
		${commentListRatingGapStyleTab}
	}
	.${blockId} .rtcl-Reviews.rtcl .comment-title{
		${commentListTitleTypoStyleTab}
		${commentListTitleGapStyleTab}
	}

	.${blockId} #respond form input{
		${formFieldHeightStyleTab}
	}
	.${blockId} #respond form textarea{
		${formTextareaHeightStyleTab}
	}
	.${blockId} .rtcl-Reviews.rtcl #respond .comment-reply-title{
		${formTitleTypoStyleTab}
		${formTitleGapStyleTab}
	}
	.${blockId} .rtcl-Reviews.rtcl #respond .comment-form label{
		${formLabelTypoStyleTab}
	}
	.${blockId} #review-form #respond .form-submit input{
		${formBtnPaddingStyleTab}
		${formBtnTypoStyleTab}
	}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
	.${blockId} .rtcl-Reviews.rtcl{
		${mainMarginStyleMob}
		${mainPaddingStyleMob}
	}
	.${blockId} .rtcl-single-listing-section-title{
		${sectionTitleTypoStyleMob}
	}
	.${blockId} .rtcl-Reviews.rtcl #comments .rtcl-reviews-meta .listing-meta .listing-meta-rating{
		${averageRatingBorderStyleMob}
		${averageRatingTypoStyleMob}
	}
	.${blockId} .rtcl-Reviews.rtcl #comments .rtcl-reviews-meta .rtcl-reviews-meta-action a{
		${headerBtnPaddingStyleMob}
		${headerBtnTypoStyleMob}
	}
	.${blockId} .rtcl-Reviews.rtcl #comments ol.comment-list li{
		${commentListPaddingStyleMob}
		${commentListMarginStyleMob}
	}
	.${blockId} .rtcl-Reviews.rtcl .each-comment.media{
		${commentListGapStyleMob}
	}
	.${blockId} .rtcl-Reviews.rtcl .star-rating{
		${commentListRatingGapStyleMob}
	}
	.${blockId} .rtcl-Reviews.rtcl .comment-title{
		${commentListTitleTypoStyleMob}
		${commentListTitleGapStyleMob}
	}
	.${blockId} #respond form input{
		${formFieldHeightStyleMob}
	}
	.${blockId} #respond form textarea{
		${formTextareaHeightStyleMob}
	}
	.${blockId} .rtcl-Reviews.rtcl #respond .comment-reply-title{
		${formTitleTypoStyleMob}
		${formTitleGapStyleMob}
	}
	.${blockId} .rtcl-Reviews.rtcl #respond .comment-form label{
		${formLabelTypoStyleMob}
	}
	.${blockId} #review-form #respond .form-submit input{
		${formBtnPaddingStyleMob}
		${formBtnTypoStyleMob}
	}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and(max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and(max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;