import { __ } from '@wordpress/i18n';
import { Spinner } from "@wordpress/components";
import Inspector from './inspector';
import { useBlockProps } from '@wordpress/block-editor';
import RenderView from './render-view';
import { useEffect, useState } from '@wordpress/element';
import styles from './styles';
import { uniqueBlockId, blockEditorWrapperClass } from '../../helpers';
import axios from 'axios';
import Qs from 'qs';
import { Icons } from '../../components';
import { applyFilters } from '@wordpress/hooks';
import './editor.scss';
import classnames from 'classnames';

function Edit({ isSelected, attributes, setAttributes, clientId }) {
	const {
		blockId,
		preview,
		sliderOptions
	} = attributes;

	//swiper slider settings
	let sliderSettings = {};
	sliderSettings = applyFilters('blocks.sliderSettings', sliderSettings, attributes);

	const [lastPostResult, setLastPostResult] = useState([]);
	const [dataSuccess, setDataSuccess] = useState(true);

	const [queryEffect, setQueryEffect] = useState(false);
	const postQueryChange = () => {
		setQueryEffect(!queryEffect);
	}

	const apiFetchData = async () => {
		let ajaxData = {
			action: 'rtcl_block_related_listing_ajax',
			rtcl_block_nonce: rtclBlockParams.rtcl_block_nonce,
			listingId: rtclBlockParams.lastPostId,
			attributes
		}
		try {
			const result = await axios.post(rtclBlockParams.ajaxurl, Qs.stringify(ajaxData))
				.then((response) => response.data);
			if (result.success) {
				setLastPostResult([...result.data.posts]);
				setDataSuccess(result.success);
			} else {
				setLastPostResult([]);
				setDataSuccess(result.success);
			}
		} catch (error) {
			console.error(error);
		}
	};

	useEffect(() => {
		const BLOCK_PREFIX = 'rtcl-related-listing-';
		uniqueBlockId(BLOCK_PREFIX, clientId, attributes, setAttributes);
		apiFetchData();
	}, [queryEffect]);

	const wrapClass = classnames([
		'rtcl rtcl-listings-sc-wrapper rtcl-elementor-widget rtcl-el-slider-wrapper rtcl-listings-slider',
		sliderOptions['dotNavigation'] && sliderOptions['dotStyle'] ? `rtcl-slider-pagination-style-${sliderOptions['dotStyle']}` : '',
		sliderOptions['arrowNavigation'] && sliderOptions['arrowPosition'] ? `rtcl-slider-btn-style-${sliderOptions['arrowPosition']}` : ''
	])

	const editorWrapClass = blockEditorWrapperClass(useBlockProps, attributes, wrapClass);

	//generate styles
	const allStyle = styles({
		attributes,
		setAttributes
	})

	const hasPosts = Array.isArray(lastPostResult) && lastPostResult.length;
	if (!hasPosts) {
		return [
			isSelected && (
				<Inspector
					attributes={attributes}
					setAttributes={setAttributes}
					postQueryChange={postQueryChange}
				/>
			),
			dataSuccess ? (<div className="rtrb-spinner"><Spinner /></div>) : (
				<p>{__('No listing found.', 'rtcl-elementor-builder')}</p>
			)
		];
	}



	//set preview
	if (preview) {
		return Icons.title_preview;
	}

	return [
		isSelected && (
			<Inspector
				attributes={attributes}
				setAttributes={setAttributes}
				postQueryChange={postQueryChange}
			/>
		),

		<style>{`${allStyle}`}</style>,

		<div {...editorWrapClass}>
			<RenderView {...attributes} setAttributes={setAttributes} postResults={lastPostResult} clientId={clientId} sliderSettings={sliderSettings} />
		</div>
	]
}

export default Edit;