import { useEffect } from '@wordpress/element';
import {
	mainWrapperStyle,
	softMinifyCssStrings,
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		arrowColor,
		arrowBgColor,
		zoomIconColor,
		zoomIconBgColor
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});

	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}.rtcl-block-frontend{
			margin-top: 15px;
			margin-bottom: 15px;
		}
		.${blockId} .el-single-addon .swiper-button-next,
		.${blockId} .el-single-addon .swiper-button-prev{
			${arrowColor ? `color:${arrowColor};` : ''}
			${arrowBgColor ? `background-color:${arrowBgColor} !important;` : ''}
		}
		.${blockId} .rtcl-slider-wrapper .rtcl-slider .rtcl-listing-gallery__trigger{
			${zoomIconColor ? `color:${zoomIconColor};` : ''}
			${zoomIconBgColor ? `background-color:${zoomIconBgColor} !important;` : ''}
		}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and(max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and(max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;