import { __ } from '@wordpress/i18n';
import { Spinner } from "@wordpress/components";
import Inspector from './inspector';
import { useBlockProps } from '@wordpress/block-editor';
import RenderView from './render-view';
import { useEffect, useState } from '@wordpress/element';
import styles from './styles';
import { uniqueBlockId, blockEditorWrapperClass } from '../../helpers';
import axios from 'axios';
import Qs from 'qs';
import { Icons, Pagination } from '../../components';
import './editor.scss';

function Edit({ isSelected, attributes, setAttributes, clientId }) {
	const {
		blockId,
		preview,
		page,
		pagination,
		listingImageSize
	} = attributes;

	const [listingsEffect, setListingsEffect] = useState(false);
	const listingsSideEffectChange = () => {
		setListingsEffect(!listingsEffect);
	}

	const [postResult, setPostResult] = useState([]);
	const [dataSuccess, setDataSuccess] = useState(true);
	const [pageTotal, setPageTotal] = useState(0);

	const apiFetchData = async () => {
		let ajaxData = {
			action: 'rtcl_store_listing_ajax',
			rtcl_block_nonce: rtclBlockParams.rtcl_block_nonce,
			attributes
		}
		try {
			const result = await axios.post(rtclBlockParams.ajaxurl, Qs.stringify(ajaxData))
				.then((response) => response.data);
			if (result.success) {
				setPostResult([...result.data.posts]);
				setPageTotal(result.data.total_page);
				setDataSuccess(result.success);
			} else {
				setPostResult([]);
				setPageTotal(0);
				setDataSuccess(result.success);
			}
		} catch (error) {
			console.error(error);
		}
	};

	useEffect(() => {
		const BLOCK_PREFIX = 'rtcl-storelist-';
		uniqueBlockId(BLOCK_PREFIX, clientId, attributes, setAttributes);
		apiFetchData();
	}, [listingImageSize, listingsEffect]);

	const editorWrapClass = blockEditorWrapperClass(useBlockProps, attributes, 'rtcl rtcl-listings-sc-wrapper rtcl-elementor-widget rtcl-block-archive-listing');

	//generate styles
	const allStyle = styles({
		attributes,
		setAttributes
	})

	const hasPosts = Array.isArray(postResult) && postResult.length;
	if (!hasPosts) {
		return [
			isSelected && (
				<Inspector
					attributes={attributes}
					setAttributes={setAttributes}
				/>
			),
			dataSuccess ? (<div className="rtrb-spinner"><Spinner /></div>) : (
				<p>{__('No Store found.', 'rtcl-elementor-builder')}</p>
			)
		];
	}

	//set preview
	if (preview) {
		return Icons.title_preview;
	}

	return [
		isSelected && (
			<Inspector
				attributes={attributes}
				setAttributes={setAttributes}
				listingsSideEffectChange={listingsSideEffectChange}
			/>
		),

		<style>{`${allStyle}`}</style>,

		<div {...editorWrapClass}>
			<div className="rtcl-listings-wrapper">
				<RenderView {...attributes} setAttributes={setAttributes} postResults={postResult} />
				{(pagination && pageTotal > 1) && (
					<nav className="rtcl-pagination">
						<Pagination
							total={pageTotal}
							current={page}
							prevText=""
							nextText=""
							onClickPage={(page) => setAttributes({ page })}
						/>
					</nav>
				)}
			</div>
		</div>
	]
}

export default Edit;