import { InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, ToggleControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import {
	Typography,
	Color,
	AdvancedControl,
	RangeDevice,
	BorderDimension
} from '../../components';

function Inspector({ attributes, setAttributes }) {
	const {
		resDevice,
		showStoreLogo,
		showStoreName,
		showStoreCategory,
		showStoreRating,

		bannerHeight,
		bannerWidth,
		logoWrapHeight,
		logoWrapWidth,

		//style
		nameTypo,
		nameColor,
		catTypo,
		catColor,
		catIconColor,
		reviewTypo,
		reviewColor,
		logoWrapBgColor,
		logoBorder,
		logoImageWidth,
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	return (
		<InspectorControls key={'controls'}>
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "styles",
							title: "Styles",
							className: "rtrb-tab-btn styles",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>
					{(tabs) => (
						<div className="rtrb-tab-content">
							{tabs.name === 'general' && (
								<>
									<PanelBody title={__('General', 'rtcl-elementor-builder')} initialOpen={true}>
										<RangeDevice
											label={__('Banner Height(px)', "rtcl-elementor-builder")}
											responsive={true}
											value={bannerHeight}
											min={150}
											max={600}
											step={1}
											onChange={(val) => setAttributes({ bannerHeight: val })}
											resRequiredProps={resRequiredProps}
										/>
										<RangeDevice
											label={__('Banner Width(%)', "rtcl-elementor-builder")}
											responsive={true}
											value={bannerWidth}
											min={0}
											max={100}
											step={1}
											onChange={(val) => setAttributes({ bannerWidth: val })}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>

									<PanelBody title={__('Logo', 'rtcl-elementor-builder')} initialOpen={false}>
										<RangeDevice
											label={__('Logo Wrapper Height(px)', "rtcl-elementor-builder")}
											responsive={true}
											value={logoWrapHeight}
											min={150}
											max={600}
											step={1}
											onChange={(val) => setAttributes({ logoWrapHeight: val })}
											resRequiredProps={resRequiredProps}
										/>
										<RangeDevice
											label={__('Logo Wrapper Width(px)', "rtcl-elementor-builder")}
											responsive={true}
											value={logoWrapWidth}
											min={150}
											max={600}
											step={1}
											onChange={(val) => setAttributes({ logoWrapWidth: val })}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>

									<PanelBody title={__('Content Visibility', 'rtcl-elementor-builder')} initialOpen={false}>
										<ToggleControl
											label={__("Show Store Logo", "rtcl-elementor-builder")}
											onChange={(showStoreLogo) => setAttributes({ showStoreLogo })}
											checked={showStoreLogo}
										/>
										<ToggleControl
											label={__("Show Store Name", "rtcl-elementor-builder")}
											onChange={(showStoreName) => setAttributes({ showStoreName })}
											checked={showStoreName}
										/>
										<ToggleControl
											label={__("Show Category", "rtcl-elementor-builder")}
											onChange={(showStoreCategory) => setAttributes({ showStoreCategory })}
											checked={showStoreCategory}
										/>
										<ToggleControl
											label={__("Show Rating", "rtcl-elementor-builder")}
											onChange={(showStoreRating) => setAttributes({ showStoreRating })}
											checked={showStoreRating}
										/>
									</PanelBody>
								</>
							)}

							{tabs.name === 'styles' && (
								<>
									<PanelBody title={__('Styles')} initialOpen={true}>
										<Typography
											label={__('Name Typography', 'rtcl-elementor-builder')}
											value={nameTypo}
											onChange={(val) => setAttributes({ nameTypo: val })}
											resRequiredProps={resRequiredProps}
										/>
										<Color
											label={__("Name Color", "rtcl-elementor-builder")}
											color={nameColor}
											onChange={(nameColor) => setAttributes({ nameColor })}
										/>
										<Typography
											label={__('Category Typography', 'rtcl-elementor-builder')}
											value={catTypo}
											onChange={(val) => setAttributes({ catTypo: val })}
											resRequiredProps={resRequiredProps}
										/>
										<Color
											label={__("Category Color", "rtcl-elementor-builder")}
											color={catColor}
											onChange={(catColor) => setAttributes({ catColor })}
										/>
										<Color
											label={__("Category Icon Color", "rtcl-elementor-builder")}
											color={catIconColor}
											onChange={(catIconColor) => setAttributes({ catIconColor })}
										/>
										<Typography
											label={__('Review Typography', 'rtcl-elementor-builder')}
											value={reviewTypo}
											onChange={(val) => setAttributes({ reviewTypo: val })}
											resRequiredProps={resRequiredProps}
										/>
										<Color
											label={__("Review Count Color", "rtcl-elementor-builder")}
											color={reviewColor}
											onChange={(reviewColor) => setAttributes({ reviewColor })}
										/>
									</PanelBody>

									<PanelBody title={__('Logo', 'rtcl-elementor-builder')} initialOpen={false}>
										<Color
											label={__("Wrapper Background", "rtcl-elementor-builder")}
											color={logoWrapBgColor}
											onChange={(logoWrapBgColor) => setAttributes({ logoWrapBgColor })}
										/>
										<BorderDimension
											value={logoBorder}
											onChange={(value) => {
												setAttributes({ logoBorder: value })
											}}
											resRequiredProps={resRequiredProps}
										/>
										<RangeDevice
											label={__('Logo Image Width (Px)', "rtcl-elementor-builder")}
											responsive={true}
											value={logoImageWidth}
											min={150}
											max={600}
											step={1}
											onChange={(val) => setAttributes({ logoImageWidth: val })}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>
								</>
							)}

							{tabs.name === 'advance' && (
								<>
									<AdvancedControl
										attributes={attributes}
										setAttributes={setAttributes}
									/>
								</>
							)}

						</div>
					)}
				</TabPanel>
			</div>
		</InspectorControls>
	)

}

export default Inspector;