import { useEffect } from '@wordpress/element';
import {
	rangeDeviceStyle,
	mainWrapperStyle,
	softMinifyCssStrings
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		containerWidth,
		innerContainerWidth,
		alignItem,
		justifyContent,
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes,
	})

	const {
		rangeDeviceDesk: innerContainerWidthStyleDesk,
		rangeDeviceTab: innerContainerWidthStyleTab,
		rangeDeviceMob: innerContainerWidthStyleMob
	} = rangeDeviceStyle({
		controlValue: innerContainerWidth,
		property: 'max-width',
		important: true
	})

	const {
		rangeDeviceDesk: justifyContentStyleDesk,
		rangeDeviceTab: justifyContentStyleTab,
		rangeDeviceMob: justifyContentStyleMob
	} = rangeDeviceStyle({
		controlValue: justifyContent,
		property: 'justify-content'
	})

	const {
		rangeDeviceDesk: alignItemStyleDesk,
		rangeDeviceTab: alignItemStyleTab,
		rangeDeviceMob: alignItemStyleMob
	} = rangeDeviceStyle({
		controlValue: alignItem,
		property: 'align-items'
	})

	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}{
			${containerWidth === 'boxed' ? `
				${innerContainerWidthStyleDesk}
			`: ''}
			${containerWidth === 'fullwidth' ? `
				max-width:100%;
		`: ''}
		}

		.${blockId}.rtcl-block-editor > 
		.block-editor-inner-blocks > 
		.block-editor-block-list__layout,
		.${blockId}.rtcl-block-frontend{
			${alignItemStyleDesk}
			${justifyContentStyleDesk}
		}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
		.${blockId}{
			${containerWidth == 'boxed' ? `
				${innerContainerWidthStyleTab}
			`: ''}
		}
		.${blockId}.rtcl-block-editor > 
		.block-editor-inner-blocks > 
		.block-editor-block-list__layout,
		.${blockId}.rtcl-block-frontend{
			${alignItemStyleTab}
			${justifyContentStyleTab}
		}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
		.${blockId}{
			${innerContainerWidthStyleMob}
		}
		.${blockId}.rtcl-block-editor > 
		.block-editor-inner-blocks > 
		.block-editor-block-list__layout,
		.${blockId}.rtcl-block-frontend{
			${alignItemStyleMob}
			${justifyContentStyleMob}
		}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and (max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and (max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;