import { InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import {
	AdvancedControl,
	RangeDevice
} from '../../components';

function Inspector({ attributes, setAttributes }) {
	const {
		resDevice,
		videoWidth,
		videoHeight
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	return (
		<InspectorControls key={'controls'}>
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>
					{(tabs) => (
						<div className="rtrb-tab-content">
							{tabs.name === 'general' && (
								<>
									<PanelBody title={__('General', 'rtcl-elementor-builder')} initialOpen={true}>
										<RangeDevice
											label={__('Width', "rtcl-elementor-builder")}
											responsive={true}
											value={videoWidth}
											units={['px', '%']}
											min={1}
											max={videoWidth?.unit == '%' ? 100 : 2000}
											step={1}
											onChange={(val) => setAttributes({ videoWidth: val })}
											resRequiredProps={resRequiredProps}
										/>
										<RangeDevice
											label={__('Height', "rtcl-elementor-builder")}
											responsive={true}
											value={videoHeight}
											units={['px', '%']}
											min={1}
											max={videoHeight?.unit == '%' ? 100 : 2000}
											step={1}
											onChange={(val) => setAttributes({ videoHeight: val })}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>
								</>
							)}

							{tabs.name === 'advance' && (
								<>
									<AdvancedControl
										attributes={attributes}
										setAttributes={setAttributes}
									/>
								</>
							)}
						</div>
					)}
				</TabPanel>
			</div>
		</InspectorControls>
	)

}

export default Inspector;