import { InnerBlocks, useBlockProps } from '@wordpress/block-editor';
import { blockFrontendWrapperClass } from '../../../helpers';

export default function save(props) {
	const {
		blockId,
		mainWrapShowHide,
	} = props.attributes;

	const saveBlockProps = blockFrontendWrapperClass(useBlockProps, props.attributes)

	return (
		<div {...saveBlockProps}>
			<div className="rtcl-block-column">
				<InnerBlocks.Content />
			</div>
		</div>
	);
}

