import { RenderDynamicTag } from '../../helpers';
import { __ } from '@wordpress/i18n';
function RenderView(props) {
	const {
		storeResults,
		titleTag
	} = props;

	return (
		(Object.keys(storeResults).length > 0 && storeResults?.description) ? (
			<div class="store-details store-description-content">
				<RenderDynamicTag
					tagName={titleTag}>
					{storeResults?.description}
				</RenderDynamicTag>
			</div>
		) : (
			<h3>{__('Description does not add to the Store', "rtcl-elementor-builder")}</h3>
		)
	)
}

export default RenderView; 