import { useEffect } from '@wordpress/element';
import {
	rangeDeviceStyle,
	typographyStyle,
	mainWrapperStyle,
	softMinifyCssStrings,
	dimensionStyle
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		showPUnit,
		showPType,
		priceAlignment,
		priceSpacing,
		priceTypo,
		priceColor,
		priceLabelTypo,
		priceLabelColor
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});

	const {
		rangeDeviceDesk: priceAlignStyleDesk,
		rangeDeviceTab: priceAlignStyleTab,
		rangeDeviceMob: priceAlignStyleMob
	} = rangeDeviceStyle({
		controlValue: priceAlignment,
		property: 'justify-content',
	});

	const {
		deskDimension: priceSpacingStyleDesk,
		tabDimension: priceSpacingStyleTab,
		mobDimension: priceSpacingStyleMob
	} = dimensionStyle({
		controlValue: priceSpacing,
		property: 'margin',
		important: true
	});

	const {
		deskTypography: priceTypoStyleDesk,
		tabTypography: priceTypoStyleTab,
		mobTypography: priceTypoStyleMob
	} = typographyStyle({
		controlValue: priceTypo
	});

	const {
		deskTypography: priceLabelTypoStyleDesk,
		tabTypography: priceLabelTypoStyleTab,
		mobTypography: priceLabelTypoStyleMob
	} = typographyStyle({
		controlValue: priceLabelTypo
	});


	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}.rtcl-block-frontend{
			margin-top: 15px;
			margin-bottom: 15px;
		}
		${!showPUnit ? `
		.${blockId} .rtcl-price-unit-label{
			display:none !important;
		}`: ''}

		${!showPType ? `
		.${blockId} .rtcl-price-type-label{
			display:none !important;
		}`: ''}

		.${blockId} .item-price.style-1 .rtcl-price,
		.${blockId} .item-price.style-2{
			${priceAlignStyleDesk}
		}

		.${blockId} .rtcl-price{
			${priceColor ? `color:${priceColor};` : ''}
			${priceSpacingStyleDesk}
			${priceTypoStyleDesk}
		}
		.${blockId} .rtcl-price .rtcl-price-meta{
			${priceLabelColor ? `color:${priceLabelColor};` : ''}
			${priceLabelTypoStyleDesk}
		}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
		.${blockId} .item-price.style-1 .rtcl-price,
		.${blockId} .item-price.style-2{
			${priceAlignStyleTab}
		}
		.${blockId} .rtcl-price{
			${priceSpacingStyleTab}
			${priceTypoStyleTab}
		}
		.${blockId} .rtcl-price .rtcl-price-meta{
			${priceLabelTypoStyleTab}
		}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
		.${blockId} .item-price.style-1 .rtcl-price,
		.${blockId} .item-price.style-2{
			${priceAlignStyleMob}
		}
		.${blockId} .rtcl-price{
			${priceSpacingStyleMob}
			${priceTypoStyleMob}
		}
		.${blockId} .rtcl-price .rtcl-price-meta{
			${priceLabelTypoStyleMob}
		}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and (max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and (max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;