import { RenderDynamicTag } from '../../helpers';
import { __ } from '@wordpress/i18n';
function RenderView(props) {
	const {
		storeResults,
		titleTag
	} = props;

	return (
		(Object.keys(storeResults).length > 0 && storeResults?.name) ? (
			<div class="rtcl store-content-wrap">
				<div class="store-name">
					<RenderDynamicTag
						tagName={titleTag}>
						{storeResults?.name}
					</RenderDynamicTag>
				</div>
			</div>
		) : (<h3>{__('Store name does not insert', "rtcl-elementor-builder")}</h3>)
	)
}

export default RenderView; 