import { RenderDynamicTag } from '../../helpers';
import { __ } from '@wordpress/i18n';
function RenderView(props) {
	const {
		storeResults,
		titleTag
	} = props;

	return (
		(Object.keys(storeResults).length > 0 && storeResults?.slogan) ? (
			<div class="store-details">
				<RenderDynamicTag
					tagName={titleTag}
					className='is-slogan'>
					{storeResults?.slogan}
				</RenderDynamicTag>
			</div>
		) : (
			<h3>{__('Slogan does not add to the store', "rtcl-elementor-builder")}</h3>
		)
	)
}

export default RenderView; 