import { InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, ToggleControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import {
	AdvancedControl,
	RangeDevice,
	Typography,
	Color,
	Dimension
} from '../../components';

function Inspector({ attributes, setAttributes }) {
	const {
		resDevice,
		mapWidth,
		mapHeight,
		showLabel,
		titleTypo,
		titleColor,
		titleMargin
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	return (
		<InspectorControls key={'controls'}>
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "styles",
							title: "Styles",
							className: "rtrb-tab-btn styles",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>
					{(tabs) => (
						<div className="rtrb-tab-content">
							{tabs.name === 'general' && (
								<>
									<PanelBody title={__('General', 'rtcl-elementor-builder')} initialOpen={true}>
										<ToggleControl
											label={__('Show Label')}
											onChange={(showLabel) => setAttributes({ showLabel })}
											checked={showLabel}
										/>
										<RangeDevice
											label={__('Width')}
											responsive={true}
											value={mapWidth}
											units={['px', '%']}
											min={1}
											max={mapWidth?.unit == '%' ? 100 : 2000}
											step={1}
											onChange={(val) => setAttributes({ mapWidth: val })}
											resRequiredProps={resRequiredProps}
										/>
										<RangeDevice
											label={__('Height')}
											responsive={true}
											value={mapHeight}
											units={['px', '%']}
											min={1}
											max={mapHeight?.unit == '%' ? 100 : 1000}
											step={1}
											onChange={(val) => setAttributes({ mapHeight: val })}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>
								</>
							)}

							{tabs.name === 'styles' && (
								<PanelBody title={__('Location Title', "rtcl-elementor-builder")} initialOpen={true}>
									<Typography
										label={__('Typography', 'rtcl-elementor-builder')}
										value={titleTypo}
										onChange={(val) => setAttributes({ titleTypo: val })}
										resRequiredProps={resRequiredProps}
									/>
									<Color
										label={__('Text Color', "rtcl-elementor-builder")}
										color={titleColor}
										onChange={(titleColor) => setAttributes({ titleColor })}
									/>
									<Dimension
										label={__("Margin", "rtcl-elementor-builder")}
										type="margin"
										responsive={true}
										value={titleMargin}
										onChange={(titleMargin) => {
											setAttributes({ titleMargin })
										}}
										resRequiredProps={resRequiredProps}
									/>
								</PanelBody>

							)}
							{tabs.name === 'advance' && (
								<>
									<AdvancedControl
										attributes={attributes}
										setAttributes={setAttributes}
									/>
								</>
							)
							}
						</div>
					)}
				</TabPanel>
			</div>
		</InspectorControls>
	)

}

export default Inspector;