import { __ } from '@wordpress/i18n';
import { printHtmlStringMarkup } from '../../helpers'
function RenderView(props) {
	const {
		postResults
	} = props;

	return (
		(Object.keys(postResults).length > 0 && postResults?.social_profiles) ? (
			<div className="rtcl el-single-addon social-profile" dangerouslySetInnerHTML={printHtmlStringMarkup(postResults?.social_profiles)} />
		) : (
			<h3>{__('Social profiles does not add to the listing', 'rtcl-elementor-builder')}</h3>
		)
	)
}

export default RenderView; 