import { __ } from '@wordpress/i18n';
import { printHtmlStringMarkup } from '../../helpers';

function RenderView(props) {
	const {
		postResults,
		style,
		labelValueNewLine
	} = props;
	return (
		(Object.keys(postResults).length > 0 && postResults?.customField) ? (
			<div className={`rtcl el-single-addon custom-field-content-area ${style} ${labelValueNewLine ? 'label-new-line' : ''}`} dangerouslySetInnerHTML={printHtmlStringMarkup(postResults.customField)} />
		) : (
			<h3>{__('Please Select Custom Feild', "rtcl-elementor-builder")}</h3>
		)
	)
}

export default RenderView; 