//wordpress dependencies
const { __ } = wp.i18n;
const { InspectorControls } = wp.blockEditor;
import {
	BaseControl,
	Button,
	ButtonGroup,
	PanelBody,
	SelectControl,
	TabPanel
} from "@wordpress/components";

import {
	RangeDevice,
	AdvancedControl
} from '../../components';

import {
	WRAPPER_TAG
} from "../../globals/Constants";

function Inspector(props) {
	const { attributes, setAttributes } = props;

	const {
		resDevice,
		contentWidth,
		innerContentWidth,
		wrapperTag,
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	return (
		<InspectorControls key="controls">
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>

					{(tab) => (
						<div className="rtrb-tab-content">

							{tab.name === "general" && (
								<>
									<PanelBody>

										<BaseControl label={__("Content Width", "rtcl-elementor-builder")} className="rtrb-control-field rtrb-cf-heading-warp">
											<ButtonGroup className="rtrb-btn-group">
												{[{ label: 'Full Width', value: 'fullWidth' }, { label: 'Custom', value: 'custom' }].map((item, key) => (
													<Button
														key={key}
														variant={contentWidth === item.value ? 'primary' : 'secondary'}
														onClick={() => setAttributes({ contentWidth: item.value })}
													>
														{item.label}
													</Button>
												))}
											</ButtonGroup>
										</BaseControl>

										{'custom' === contentWidth && (
											<RangeDevice
												label={__('Max Width')}
												responsive={true}
												value={innerContentWidth}
												//units={['px', '%',]}
												min={1}
												max={2560}
												step={1}
												onChange={(val) => setAttributes({ innerContentWidth: val })}
												resRequiredProps={resRequiredProps}
											/>
										)}

										<SelectControl
											label={__('HTML Tag', 'rtcl-elementor-builder')}
											value={wrapperTag}
											options={WRAPPER_TAG}
											onChange={(wrapperTag) => setAttributes({
												wrapperTag
											})
											}
										/>
									</PanelBody>
								</>
							)}

							{tab.name === "advance" && (
								<>
									<AdvancedControl
										attributes={attributes}
										setAttributes={setAttributes}
										type='row'
									/>
								</>
							)}

						</div>
					)}
				</TabPanel>
			</div >
		</InspectorControls >
	)
}

export default Inspector;