import { useEffect } from '@wordpress/element';
import {
	typographyStyle,
	mainWrapperStyle,
	softMinifyCssStrings,
	dimensionStyle
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		showAuthor,
		showAuthorImg,
		showLocation,
		showContactNum,
		showContactForm,
		showOnlineStatus,
		showSellerWebsite,
		dataTypo,
		labelTypo,
		labelColor,
		dataColor,
		itemPadding,
		itemBorder,
		btnTypo,
		btnTextColor,
		btnHoverTextColor,
		btnBgColor,
		btnHoverBgColor,
		btnPadding
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});

	const {
		deskTypography: labelTypoStyleDesk,
		tabTypography: labelTypoStyleTab,
		mobTypography: labelTypoStyleMob
	} = typographyStyle({
		controlValue: labelTypo
	});

	const {
		deskTypography: dataTypoStyleDesk,
		tabTypography: dataTypoStyleTab,
		mobTypography: dataTypoStyleMob
	} = typographyStyle({
		controlValue: dataTypo
	});

	const {
		deskDimension: itemBorderStyleDesk,
		tabDimension: itemBorderStyleTab,
		mobDimension: itemBorderStyleMob
	} = dimensionStyle({
		controlValue: itemBorder,
		property: 'border-width',
		important: true
	});
	const {
		deskDimension: itemPaddingStyleDesk,
		tabDimension: itemPaddingStyleTab,
		mobDimension: itemPaddingStyleMob
	} = dimensionStyle({
		controlValue: itemPadding,
		property: 'padding',
		important: true
	});

	const {
		deskTypography: btnTypoStyleDesk,
		tabTypography: btnTypoStyleTab,
		mobTypography: btnTypoStyleMob
	} = typographyStyle({
		controlValue: btnTypo
	});

	const {
		deskDimension: btnPaddingStyleDesk,
		tabDimension: btnPaddingStyleTab,
		mobDimension: btnPaddingStyleMob
	} = dimensionStyle({
		controlValue: btnPadding,
		property: 'padding',
		important: true
	});


	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}.rtcl-block-frontend{
			margin-top: 15px;
			margin-bottom: 15px;
		}
		.${blockId} .list-group-item:not(.rtcl-website) {
			${itemBorderStyleDesk}
		}
		.${blockId} .list-group-item{
			${itemPaddingStyleDesk}
		}
		.${blockId} .list-group-item .media-body>span,
		.${blockId} .list-group-item>span,
		.${blockId} .list-group-item .media-body>a>span,
		.${blockId} .list-group-item .rtcl-icon{
			${labelTypoStyleDesk}
			${labelColor ? `color:${labelColor};` : ''}
		}

		.${blockId} .list-group-item.rtcl-user-status > span{
			position: relative;
			padding-left: 25px;
			font-size: 13px;
			line-height: 1;
		}

		.${blockId} .list-group-item.rtcl-user-status > span::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 16px;
			height: 16px;
			border-radius: 50%;
	  }
		.${blockId} .list-group-item.rtcl-user-status.offline > span::before {
			background: #ff0000;
	  }
		.${blockId} .list-group-item.rtcl-user-status.online > span::before {
			background: #14ad3f;
	  }
		

		.${blockId} .list-group-item .media-body div{
			${dataTypoStyleDesk}
			${dataColor ? `color:${dataColor};` : ''}
		}
		
		${!showAuthor ?
			`.${blockId} .listing-author{display:none !important;}`
			: ''}
		${!showAuthorImg ?
			`.${blockId} .author-logo-wrapper{display:none;}`
			: ''}
		${!showLocation ?
			`.${blockId} .list-group .list-group-item:nth-child(2){display:none;}`
			: ''}
			${!showContactNum ?
			`.${blockId} .list-group .list-group-item.reveal-phone{display:none;}`
			: ''}
		${!showContactForm ?
			`.${blockId} .list-group .rtcl-do-email.list-group-item{display:none;}`
			: ''}
			${!showOnlineStatus ?
			`.${blockId} .list-group .list-group-item.rtcl-user-status{display:none;}`
			: ''}
		${!showSellerWebsite ?
			`.${blockId} .list-group .rtcl-website.list-group-item{display:none;}`
			: ''}
		.${blockId} .list-group-item .btn-primary{
			${btnTypoStyleDesk}
			${btnPaddingStyleDesk}
			${btnTextColor ? `color:${btnTextColor};` : ''}
			${btnBgColor ? `background-color:${btnBgColor} !important;` : ''}
		}
		.${blockId} .list-group-item .btn-primary:hover{
			${btnHoverTextColor ? `color:${btnHoverTextColor};` : ''}
			${btnHoverBgColor ? `background-color:${btnHoverBgColor} !important;` : ''}
		}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
		.${blockId} .list-group-item:not(.rtcl-website) {
			${itemBorderStyleTab}
		}
		.${blockId} .list-group-item{
			${itemPaddingStyleTab}
		}
		.${blockId} .list-group-item .media-body span,
		.${blockId} .list-group-item .rtcl-icon{
			${labelTypoStyleTab}
		}
		.${blockId} .list-group-item .media-body div{
			${dataTypoStyleTab}
		}
		.${blockId} .list-group-item .btn-primary{
			${btnTypoStyleTab}
			${btnPaddingStyleTab}
		}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
		.${blockId} .list-group-item:not(.rtcl-website) {
			${itemBorderStyleMob}
		}
		.${blockId} .list-group-item{
			${itemPaddingStyleMob}
		}
		.${blockId} .list-group-item .media-body span,
		.${blockId} .list-group-item .rtcl-icon{
			${labelTypoStyleMob}
		}
		.${blockId} .list-group-item .media-body div{
			${dataTypoStyleMob}
		}
		.${blockId} .list-group-item .btn-primary{
			${btnTypoStyleMob}
			${btnPaddingStyleMob}
		}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and(max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and(max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;