function RenderView(props) {
	const {
		postResults,
		showTitle,
	} = props;

	return (
		Array.isArray(postResults?.feature_lists) && postResults?.feature_lists.length > 0 ? (
			<div className="rtcl-single-block el-single-addon classima-listing-single">

				<div className="classima-single-details">

					<div className="rtin-specs">
						{showTitle && (
							<h3 className="rtin-specs-title">Features:</h3>
						)}

						<ul className="rtin-spec-items">
							{postResults?.feature_lists.map((list) => (
								<li>{list}</li>
							))}
						</ul>

					</div>

				</div>
			</div>
		) : (
			<p>{postResults?.feature_lists}</p>
		)
	)
}

export default RenderView; 