import { __ } from '@wordpress/i18n';
import { InspectorControls } from '@wordpress/block-editor';
import {
	PanelBody,
	TabPanel,
} from "@wordpress/components";

import {
	RangeDevice,
	AdvancedControl
} from '../../../components';


function Inspector(props) {
	const { attributes, setAttributes } = props;

	const {
		resDevice,
		colWidth,
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	return (
		<InspectorControls key="controls">
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>

					{(tab) => (

						<div className="rtrb-tab-content">
							{tab.name === "general" && (
								<>
									<PanelBody title={__('Column Settings', 'rtcl-elementor-builder')} initialOpen={true}>
										<RangeDevice
											label={__('Column Width')}
											responsive={true}
											value={colWidth}
											units={['%']}
											min={.001}
											max={100}
											step={.001}
											onChange={(val) => setAttributes({ colWidth: val })}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>
								</>
							)}
							{tab.name === "advance" && (
								<AdvancedControl
									attributes={attributes}
									setAttributes={setAttributes}
								/>
							)}
						</div>
					)}
				</TabPanel>

			</div >
		</InspectorControls >
	)
}

export default Inspector;