import { InnerBlocks, useBlockProps } from '@wordpress/block-editor';
import { blockFrontendWrapperClass } from '../../helpers';

export default function save(props) {
	const {
		gutterX,
		gutterY,
	} = props.attributes;

	let inlineStyle = {
		'--d-gap-x': gutterX['lg'] + gutterX['unit'],
		'--t-gap-x': gutterX['md'] + gutterX['unit'],
		'--m-gap-x': gutterX['sm'] + gutterX['unit'],

		'--d-gap-y': gutterY['lg'] + gutterY['unit'],
		'--t-gap-y': gutterY['md'] + gutterY['unit'],
		'--m-gap-y': gutterY['sm'] + gutterY['unit'],
	}
	const saveBlockProps = blockFrontendWrapperClass(useBlockProps, props.attributes)

	return (
		<div {...saveBlockProps} style={inlineStyle}>
			<InnerBlocks.Content />
		</div>
	);

}

