import { useEffect } from '@wordpress/element';
import {
	rangeDeviceStyle,
	typographyStyle,
	mainWrapperStyle,
	softMinifyCssStrings,
	dimensionStyle
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		titleTag,
		titleAlignment,
		titleSpacing,
		titleTypo,
		titleColor
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});

	const {
		rangeDeviceDesk: titleAlignStyleDesk,
		rangeDeviceTab: titleAlignStyleTab,
		rangeDeviceMob: titleAlignStyleMob
	} = rangeDeviceStyle({
		controlValue: titleAlignment,
		property: 'text-align',
	});

	const {
		deskDimension: titleSpacingStyleDesk,
		tabDimension: titleSpacingStyleTab,
		mobDimension: titleSpacingStyleMob
	} = dimensionStyle({
		controlValue: titleSpacing,
		property: 'margin',
		important: true
	});

	const {
		deskTypography: titleTypoStyleDesk,
		tabTypography: titleTypoStyleTab,
		mobTypography: titleTypoStyleMob
	} = typographyStyle({
		controlValue: titleTypo
	});


	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}.rtcl-block-frontend{
			margin-top: 15px;
			margin-bottom: 15px;
		}
		.${blockId}{
			${titleAlignStyleDesk}
		}
		.${blockId} ${titleTag}{
			margin:0;
			${titleColor ? `color:${titleColor} !important;` : ''}
			${titleSpacingStyleDesk}
			${titleTypoStyleDesk}
		}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
	  .${blockId}{
			${titleAlignStyleTab}
		}
		.${blockId} ${titleTag}{
			${titleSpacingStyleTab}
			${titleTypoStyleTab}
		}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
		.${blockId}{
			${titleAlignStyleMob}
		}
		.${blockId} ${titleTag}{
			${titleSpacingStyleMob}
			${titleTypoStyleMob}
		}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and (max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and (max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;