import { __ } from '@wordpress/i18n';
import { printHtmlStringMarkup } from '../../helpers'
function RenderView(props) {
	const {
		postResults
	} = props;

	return (
		(Object.keys(postResults).length > 0 && postResults?.seller_info) ? (
			<div className="rtcl-listing-user-info">
				<div className="list-group" dangerouslySetInnerHTML={printHtmlStringMarkup(postResults?.seller_info)} />
			</div>
		) : (
			<h3>{__('Seller information does not add to the listing', "rtcl-elementor-builder")}</h3>
		)
	)
}

export default RenderView; 