import { useEffect } from '@wordpress/element';
import {
	rangeDeviceStyle,
	typographyStyle,
	mainWrapperStyle,
	softMinifyCssStrings,
	dimensionStyle
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		showPageTitle,
		showBreadcrumb,
		titleAlign,
		titleSpacing,
		titleTypo,
		titleColor,
		breadcrumbSpacing,
		breadcrumbAlign,
		breadcrumbTypo,
		breadcrumbColor
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});

	const {
		rangeDeviceDesk: titleAlignStyleDesk,
		rangeDeviceTab: titleAlignStyleTab,
		rangeDeviceMob: titleAlignStyleMob
	} = rangeDeviceStyle({
		controlValue: titleAlign,
		property: 'text-align',
	});

	const {
		deskDimension: titleSpacingStyleDesk,
		tabDimension: titleSpacingStyleTab,
		mobDimension: titleSpacingStyleMob
	} = dimensionStyle({
		controlValue: titleSpacing,
		property: 'margin',
		important: true
	});

	const {
		deskTypography: titleTypoStyleDesk,
		tabTypography: titleTypoStyleTab,
		mobTypography: titleTypoStyleMob
	} = typographyStyle({
		controlValue: titleTypo
	});

	const {
		deskDimension: breadcrumbSpacingStyleDesk,
		tabDimension: breadcrumbSpacingStyleTab,
		mobDimension: breadcrumbSpacingStyleMob
	} = dimensionStyle({
		controlValue: breadcrumbSpacing,
		property: 'margin',
		important: true
	});

	const {
		deskTypography: breadcrumbTypoStyleDesk,
		tabTypography: breadcrumbTypoStyleTab,
		mobTypography: breadcrumbTypoStyleMob
	} = typographyStyle({
		controlValue: breadcrumbTypo
	});

	const {
		rangeDeviceDesk: breadcrumbAlignStyleDesk,
		rangeDeviceTab: breadcrumbAlignStyleTab,
		rangeDeviceMob: breadcrumbAlignStyleMob
	} = rangeDeviceStyle({
		controlValue: breadcrumbAlign,
		property: 'text-align',
	});

	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}.rtcl-block-frontend{
			margin-top: 15px;
			margin-bottom: 15px;
		}
		${!showPageTitle ? `
		.${blockId} .rtcl-listing-header{
			display:none !important;
		}`: ''}

		${!showBreadcrumb ? `
		.${blockId} .breadcrumb-section{
			display:none !important;
		}`: ''}

		.${blockId} .header-style-2 {
			flex-direction: column-reverse;
		}
	
		.${blockId} .header-style-3 {
			flex-direction: initial;
			justify-content: space-between;
			align-items: center;
			flex-wrap: wrap;
			-moz-column-gap: 50px;
			column-gap: 50px;
		}
		.${blockId}{
			padding:40px 0 60px;
		}
		.${blockId} .rtcl-listing-header{
			${titleAlignStyleDesk}
			${titleSpacingStyleDesk}
			margin:0;
			margin-bottom:10px;
		}
		.${blockId} .rtcl-listings-header-title{
			font-size:32px;
			word-wrap: break-word;
			font-weight:700;
			${titleColor ? `color:${titleColor};` : ''}
			${titleTypoStyleDesk}
			margin:0;
		}
		.${blockId} .rtcl-breadcrumb{
			font-size:16px;
			font-weight: 600;
			${breadcrumbAlignStyleDesk}
			${breadcrumbColor ? `color:${breadcrumbColor};` : ''}
			${breadcrumbTypoStyleDesk}
			${breadcrumbSpacingStyleDesk}
		}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
		.${blockId} .rtcl-listing-header{
			${titleAlignStyleTab}
			${titleSpacingStyleTab}
		}
		.${blockId} .rtcl-listings-header-title{
			${titleTypoStyleTab}
		}
		.${blockId} .rtcl-breadcrumb{
			${breadcrumbTypoStyleTab}
			${breadcrumbSpacingStyleTab}
		}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
		.${blockId} .rtcl-listing-header{
			${titleAlignStyleMob}
			${titleSpacingStyleMob}
		}
		.${blockId} .rtcl-listings-header-title{
			${titleTypoStyleMob}
		}
		.${blockId} .rtcl-breadcrumb{
			${breadcrumbTypoStyleMob}
			${breadcrumbSpacingStyleMob}
		}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and (max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and (max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;