import { InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, ToggleControl, SelectControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import {
	Typography,
	Dimension,
	Color,
	AdvancedControl,
	RangeDevice,
	Range
} from '../../components';

function Inspector({ attributes, setAttributes }) {
	const {
		resDevice,
		showTitle,
		listGap,
		listColumn,
		listMargin,
		listTypo,
		listIconColor,
		listIconBgColor,
		listIconSize,
		listIconDimention,
		listTextColor,
		titleMargin,
		titleTypo,
		titleColor
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	return (
		<InspectorControls key={'controls'}>
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "styles",
							title: "Styles",
							className: "rtrb-tab-btn styles",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>
					{(tabs) => (
						<div className="rtrb-tab-content">
							{tabs.name === 'general' && (
								<>
									<PanelBody title={__('General', 'rtcl-elementor-builder')} initialOpen={true}>

										<ToggleControl
											label={__("Show Title", "rtcl-elementor-builder")}
											checked={showTitle}
											onChange={(showTitle) => setAttributes({ showTitle })}
										/>

										<RangeDevice
											label={__('Gap', "rtcl-elementor-builder")}
											responsive={true}
											value={listGap}
											min={1}
											max={200}
											step={1}
											onChange={(val) => setAttributes({ listGap: val })}
											resRequiredProps={resRequiredProps}
										/>

										<RangeDevice
											label={__('List Column', "rtcl-elementor-builder")}
											responsive={true}
											value={listColumn}
											min={1}
											max={10}
											step={1}
											onChange={(val) => setAttributes({ listColumn: val })}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>
								</>
							)}

							{tabs.name === 'styles' && (
								<>
									<PanelBody title={__('Feature Title')} initialOpen={true}>
										<Typography
											label={__('Typography', 'rtcl-elementor-builder')}
											value={titleTypo}
											onChange={(val) => setAttributes({ titleTypo: val })}
											resRequiredProps={resRequiredProps}
										/>
										<Color
											label={__('Text Color', "rtcl-elementor-builder")}
											color={titleColor}
											onChange={(titleColor) => setAttributes({ titleColor })}
										/>
										<Dimension
											label={__("Margin", "rtcl-elementor-builder")}
											type="margin"
											responsive={true}
											value={titleMargin}
											onChange={(titleMargin) => {
												setAttributes({ titleMargin })
											}}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>

									<PanelBody title={__('Feature List', "rtcl-elementor-builder")} initialOpen={true}>
										<Typography
											label={__('Typography', 'rtcl-elementor-builder')}
											value={listTypo}
											onChange={(val) => setAttributes({ listTypo: val })}
											resRequiredProps={resRequiredProps}
										/>

										<Color
											label={__('Text Color', "rtcl-elementor-builder")}
											color={listTextColor}
											onChange={(listTextColor) => setAttributes({ listTextColor })}
										/>

										<Color
											label={__('Icon Color', "rtcl-elementor-builder")}
											color={listIconColor}
											onChange={(listIconColor) => setAttributes({ listIconColor })}
										/>

										<Color
											label={__('Icon Background', "rtcl-elementor-builder")}
											color={listIconBgColor}
											onChange={(listIconBgColor) => setAttributes({ listIconBgColor })}
										/>

										<Range
											label={__("Icon Size", "rtcl-elementor-builder")}
											reset={true}
											value={listIconSize}
											onChange={(listIconSize) => setAttributes({ listIconSize })}
											min={1}
											max={40}
											step={1}
										/>
										<Range
											label={__('Icon Dimention', "rtcl-elementor-builder")}
											reset={true}
											value={listIconDimention}
											onChange={(listIconDimention) => setAttributes({ listIconDimention })}
											min={1}
											max={50}
											step={1}
										/>
										<Dimension
											label={__("Margin", "rtcl-elementor-builder")}
											type="margin"
											responsive={true}
											value={listMargin}
											onChange={(listMargin) => {
												setAttributes({ listMargin })
											}}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>
								</>
							)}

							{tabs.name === 'advance' && (
								<>
									<AdvancedControl
										attributes={attributes}
										setAttributes={setAttributes}
									/>
								</>
							)}

						</div>
					)}
				</TabPanel>
			</div>
		</InspectorControls>
	)

}

export default Inspector;