import { InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, ToggleControl, TextControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import {
	Alignment,
	Typography,
	Color,
	AdvancedControl,
	RangeDevice,
	Range
} from '../../components';

function Inspector({ attributes, setAttributes }) {
	const {
		resDevice,
		showLabel,
		labelText,
		showFacebook,
		showTwitter,
		showYoutube,
		showInstagram,
		showLinkedin,
		showPinterest,
		showReddit,
		labelTypo,
		iconColor,
		iconHoverColor,
		iconSize,
		iconSpacing,
		iconAlign
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	return (
		<InspectorControls key={'controls'}>
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "styles",
							title: "Styles",
							className: "rtrb-tab-btn styles",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>
					{(tabs) => (
						<div className="rtrb-tab-content">
							{tabs.name === 'general' && (
								<>
									<PanelBody title={__('General', 'rtcl-elementor-builder')} initialOpen={true}>
										<ToggleControl
											label={__('Show Label', 'rtcl-elementor-builder')}
											checked={showLabel}
											onChange={(showLabel) => setAttributes({ showLabel })}
										/>
										{/* {showLabel && (
											<TextControl
												label={__('Label Text')}
												value={labelText}
												onChange={(labelText) => setAttributes({ labelText })}
											/>
										)} */}
										<ToggleControl
											label={__('Show Facebook', 'rtcl-elementor-builder')}
											checked={showFacebook}
											onChange={(showFacebook) => setAttributes({ showFacebook })}
										/>
										<ToggleControl
											label={__('Show Twitter', 'rtcl-elementor-builder')}
											checked={showTwitter}
											onChange={(showTwitter) => setAttributes({ showTwitter })}
										/>
										<ToggleControl
											label={__('Show Youtube')}
											checked={showYoutube}
											onChange={(showYoutube) => setAttributes({ showYoutube })}
										/>
										<ToggleControl
											label={__('Show Instagram', 'rtcl-elementor-builder')}
											checked={showInstagram}
											onChange={(showInstagram) => setAttributes({ showInstagram })}
										/>
										<ToggleControl
											label={__('Show LinkedIn', 'rtcl-elementor-builder')}
											checked={showLinkedin}
											onChange={(showLinkedin) => setAttributes({ showLinkedin })}
										/>
										<ToggleControl
											label={__('Show Pinterest', 'rtcl-elementor-builder')}
											checked={showPinterest}
											onChange={(showPinterest) => setAttributes({ showPinterest })}
										/>
										<ToggleControl
											label={__('Show Reddit', 'rtcl-elementor-builder')}
											checked={showReddit}
											onChange={(showReddit) => setAttributes({ showReddit })}
										/>
									</PanelBody>
								</>
							)}

							{tabs.name === 'styles' && (
								<>
									<PanelBody title={__('Style')} initialOpen={true}>
										<Typography
											label={__('Label Typography', 'rtcl-elementor-builder')}
											value={labelTypo}
											onChange={(val) => setAttributes({ labelTypo: val })}
											resRequiredProps={resRequiredProps}
										/>

										<Color
											label={__('Icon Color')}
											color={iconColor}
											onChange={(iconColor) => setAttributes({ iconColor })}
										/>

										<Color
											label={__('Icon Hover Color')}
											color={iconHoverColor}
											onChange={(iconHoverColor) => setAttributes({ iconHoverColor })}
										/>

										<Range
											label={__("Icon Size", "rtcl-elementor-builder")}
											reset={true}
											value={iconSize}
											onChange={(iconSize) => setAttributes({ iconSize })}
											min={1}
											max={40}
											step={1}
										/>

										<RangeDevice
											label={__('Icon Speacing')}
											responsive={true}
											value={iconSpacing}
											min={1}
											max={200}
											step={1}
											onChange={(val) => setAttributes({ iconSpacing: val })}
											resRequiredProps={resRequiredProps}
										/>

										<Alignment
											label={__("Alignment", "rtcl-elementor-builder")}
											value={iconAlign}
											responsive={true}
											options={['left', 'center', 'right']}
											onChange={val => setAttributes({ iconAlign: val })}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>
								</>
							)}

							{tabs.name === 'advance' && (
								<>
									<AdvancedControl
										attributes={attributes}
										setAttributes={setAttributes}
									/>
								</>
							)}
						</div>
					)}
				</TabPanel>
			</div>
		</InspectorControls>
	)

}

export default Inspector;