import { useEffect } from '@wordpress/element';
import {
	rangeDeviceStyle,
	typographyStyle,
	mainWrapperStyle,
	softMinifyCssStrings,
	dimensionStyle
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		showHeading,
		showHeadingIndicator,
		boxBackground,
		boxPadding,
		boxBorder,
		boxRadius,
		titleSpacing,
		titleTypo,
		titleColor,
		titleIndicatorColor,
		formLabelTypo,
		formLabelColor,
		formFieldBgColor,
		formFieldHeight,
		formFieldBorder,
		formFieldRadius,
		formBtnTypo,
		formBtnTextColor,
		formBtnHoverTextColor,
		formBtnBgColor,
		formBtnHoverBgColor,
		formBtnPadding,
		formBtnBorder,
		formBtnRadius,
		infoTypo,
		infoColor,
		infoSpacing
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});

	const {
		deskDimension: boxPaddingDesk,
		tabDimension: boxPaddingTab,
		mobDimension: boxPaddingMob
	} = dimensionStyle({
		controlValue: boxPadding,
		property: 'padding',
		important: true
	});

	const {
		deskDimension: boxBorderDesk,
		tabDimension: boxBorderTab,
		mobDimension: boxBorderMob
	} = dimensionStyle({
		controlValue: boxBorder,
		property: 'border-width',
		important: true
	});

	const {
		deskDimension: boxRadiusDesk,
		tabDimension: boxRadiusTab,
		mobDimension: boxRadiusMob
	} = dimensionStyle({
		controlValue: boxRadius,
		property: 'border-radius',
		important: true
	});

	const {
		deskDimension: titleSpacingStyleDesk,
		tabDimension: titleSpacingStyleTab,
		mobDimension: titleSpacingStyleMob
	} = dimensionStyle({
		controlValue: titleSpacing,
		property: 'margin',
		important: true
	});

	const {
		deskTypography: titleTypoStyleDesk,
		tabTypography: titleTypoStyleTab,
		mobTypography: titleTypoStyleMob
	} = typographyStyle({
		controlValue: titleTypo
	});

	const {
		deskTypography: formLabelTypoDesk,
		tabTypography: formLabelTypoTab,
		mobTypography: formLabelTypoMob
	} = typographyStyle({
		controlValue: formLabelTypo
	});

	const {
		rangeDeviceDesk: formFieldHeightDesk,
		rangeDeviceTab: formFieldHeightTab,
		rangeDeviceMob: formFieldHeightMob
	} = rangeDeviceStyle({
		controlValue: formFieldHeight,
		property: 'height',
	});
	const {
		deskDimension: formFieldBorderDesk,
		tabDimension: formFieldBorderTab,
		mobDimension: formFieldBorderMob
	} = dimensionStyle({
		controlValue: formFieldBorder,
		property: 'border-width',
		important: true
	});
	const {
		deskDimension: formFieldRadiusDesk,
		tabDimension: formFieldRadiusTab,
		mobDimension: formFieldRadiusMob
	} = dimensionStyle({
		controlValue: formFieldRadius,
		property: 'border-radius',
		important: true
	});

	const {
		deskTypography: formBtnTypoDesk,
		tabTypography: formBtnTypoTab,
		mobTypography: formBtnTypoMob
	} = typographyStyle({
		controlValue: formBtnTypo
	});

	const {
		deskDimension: formBtnPaddingDesk,
		tabDimension: formBtnPaddingTab,
		mobDimension: formBtnPaddingMob
	} = dimensionStyle({
		controlValue: formBtnPadding,
		property: 'padding',
		important: true
	});
	const {
		deskDimension: formBtnBorderDesk,
		tabDimension: formBtnBorderTab,
		mobDimension: formBtnBorderMob
	} = dimensionStyle({
		controlValue: formBtnBorder,
		property: 'border-width',
		important: true
	});
	const {
		deskDimension: formBtnRadiusDesk,
		tabDimension: formBtnRadiusTab,
		mobDimension: formBtnRadiusMob
	} = dimensionStyle({
		controlValue: formBtnRadius,
		property: 'border-radius',
		important: true
	});

	const {
		deskTypography: infoTypoDesk,
		tabTypography: infoTypoTab,
		mobTypography: infoTypoMob
	} = typographyStyle({
		controlValue: infoTypo
	});
	const {
		deskDimension: infoSpacingDesk,
		tabDimension: infoSpacingTab,
		mobDimension: infoSpacingMob
	} = dimensionStyle({
		controlValue: infoSpacing,
		property: 'margin',
		important: true
	});

	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}{
			margin-top:15px;
			margin-bottom:15px;
		}

		.${blockId} .rtcl-listing-booking-wrap{
			padding:20px;
			color: #646464;
			${boxBackground ? `background-color:${boxBackground};` : ''}
			${boxPaddingDesk}
			${boxBorderDesk}
			${boxRadiusDesk}
		}
		.${blockId} .rtcl-listing-side-title{
			margin-bottom:15px;
			${!showHeading ? `display:none;` : ''}
		}
		.${blockId} .rtcl-listing-side-title h3{
			padding-bottom:15px;
			font-size:26px;
			${titleColor ? `color:${titleColor} !important;` : ''}
			${titleSpacingStyleDesk}
			${titleTypoStyleDesk}
		}
		.${blockId} .rtcl-listing-side-title h3::after{
			${!showHeadingIndicator ? `content:unset;` : ''}
			${titleIndicatorColor ? `background-color:${titleIndicatorColor};` : ''}
		}
	
		.${blockId} .rtcl-listing-booking-wrap>form{
			padding:0;
		}
		.${blockId} .rtcl-listing-booking-wrap .form-group label,
		.${blockId} .rtcl-listing-booking-wrap .available-slots{
			${formLabelTypoDesk}
			${formLabelColor ? `color:${formLabelColor};` : ''}
		}
		.${blockId} .rtcl-listing-booking-wrap .form-group .form-control{
			${formFieldBgColor ? `background-color:${formFieldBgColor} !important;` : ''}
			${formFieldHeightDesk}
			${formFieldBorderDesk}
			${formFieldRadiusDesk}
		}
		.${blockId} .rtcl-listing-booking-wrap .btn.btn-primary{
			${formBtnTypoDesk}
			${formBtnPaddingDesk}
			${formBtnBgColor ? `background-color:${formBtnBgColor};` : ''}
			${formBtnTextColor ? `color:${formBtnTextColor};` : ''}
			${formBtnBorderDesk}
			${formBtnRadiusDesk}
		}
		.${blockId} .rtcl-listing-booking-wrap .btn.btn-primary:hover{
			${formBtnHoverBgColor ? `background-color:${formBtnHoverBgColor};` : ''}
			${formBtnHoverTextColor ? `color:${formBtnHoverTextColor};` : ''}
		}
		.${blockId} .rtcl-listing-booking-wrap .rtcl-booking-info{
			${infoTypoDesk}
			${infoSpacingDesk}
			${infoColor ? `color:${infoColor};` : ''}
		}

		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
	.${blockId} .rtcl-listing-booking-wrap{
		${boxPaddingTab}
		${boxBorderTab}
		${boxRadiusTab}
	}

	.${blockId} .rtcl-listing-side-title h3{
		${titleSpacingStyleTab}
		${titleTypoStyleTab}
	}
	
	.${blockId} .rtcl-listing-booking-wrap .form-group label,
	.${blockId} .rtcl-listing-booking-wrap .available-slots{
		${formLabelTypoTab}
	}
	.${blockId} .rtcl-listing-booking-wrap .form-group .form-control{
		${formFieldHeightTab}
		${formFieldBorderTab}
		${formFieldRadiusTab}
	}
	.${blockId} .rtcl-listing-booking-wrap .btn.btn-primary{
		${formBtnTypoTab}
		${formBtnPaddingTab}
		${formBtnBorderTab}
		${formBtnRadiusTab}
	}
	
	.${blockId} .rtcl-listing-booking-wrap .rtcl-booking-info{
		${infoTypoTab}
		${infoSpacingTab}
	}

		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
	.${blockId} .rtcl-listing-booking-wrap{
		${boxPaddingMob}
		${boxBorderMob}
		${boxRadiusMob}
	}

	.${blockId} .rtcl-listing-side-title h3{
		${titleSpacingStyleMob}
		${titleTypoStyleMob}
	}
	
	.${blockId} .rtcl-listing-booking-wrap .form-group label,
	.${blockId} .rtcl-listing-booking-wrap .available-slots{
		${formLabelTypoMob}
	}
	.${blockId} .rtcl-listing-booking-wrap .form-group .form-control{
		${formFieldHeightMob}
		${formFieldBorderMob}
		${formFieldRadiusMob}
	}
	.${blockId} .rtcl-listing-booking-wrap .btn.btn-primary{
		${formBtnTypoMob}
		${formBtnPaddingMob}
		${formBtnBorderMob}
		${formBtnRadiusMob}
	}
	
	.${blockId} .rtcl-listing-booking-wrap .rtcl-booking-info{
		${infoTypoMob}
		${infoSpacingMob}
	}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and (max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and (max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;