import { useEffect } from '@wordpress/element';
import {
	rangeDeviceStyle,
	mainWrapperStyle,
	softMinifyCssStrings
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		contentWidth,
		innerContentWidth,
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes,
	})

	const {
		rangeDeviceDesk: innerContentWidthStyleDesk,
		rangeDeviceTab: innerContentWidthStyleTab,
		rangeDeviceMob: innerContentWidthStyleMob
	} = rangeDeviceStyle({
		controlValue: innerContentWidth,
		property: 'width'
	})


	const deskAllStyles = softMinifyCssStrings(`
		${contentWidth === 'custom' ? `
			.${blockId}{
				${innerContentWidthStyleDesk}
			}`: ''}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
	${contentWidth === 'custom' ? `
		.${blockId}{
			${innerContentWidthStyleTab}
		}`: ''}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
	${contentWidth === 'custom' ? `
	.${blockId}{
		${innerContentWidthStyleMob}
	}`: ''}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and (max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and (max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;