import { InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, ToggleControl, SelectControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import {
	Alignment,
	Typography,
	Dimension,
	Color,
	AdvancedControl,
	RangeDevice
} from '../../components';

function Inspector({ attributes, setAttributes }) {
	const {
		resDevice,
		showNew,
		showFeature,
		showPopular,
		showTop,
		showBumpUp,
		badgeAlign,

		badgePadding,
		badgeSpacing,

		badgeTypo,
		badgeBgColor,
		badgeTextColor,

		topBgColor,
		topTextColor,
		featureBgColor,
		featureTextColor,
		newBgColor,
		newTextColor,
		popularBgColor,
		popularTextColor,
		bumpUpBgColor,
		bumpUpTextColor
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	return (
		<InspectorControls key={'controls'}>
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "styles",
							title: "Styles",
							className: "rtrb-tab-btn styles",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>
					{(tabs) => (
						<div className="rtrb-tab-content">
							{tabs.name === 'general' && (
								<>
									<PanelBody title={__('General', 'rtcl-elementor-builder')} initialOpen={true}>
										<ToggleControl
											label={__('Show New', 'rtcl-elementor-builder')}
											checked={showNew}
											onChange={(showNew) => setAttributes({ showNew })}
										/>

										<ToggleControl
											label={__('Show Top', 'rtcl-elementor-builder')}
											checked={showTop}
											onChange={(showTop) => setAttributes({ showTop })}
										/>

										<ToggleControl
											label={__('Show Popular', 'rtcl-elementor-builder')}
											checked={showPopular}
											onChange={(showPopular) => setAttributes({ showPopular })}
										/>

										<ToggleControl
											label={__('Show Feature', 'rtcl-elementor-builder')}
											checked={showFeature}
											onChange={(showFeature) => setAttributes({ showFeature })}
										/>

										<ToggleControl
											label={__('Show BumpUp', 'rtcl-elementor-builder')}
											checked={showBumpUp}
											onChange={(showBumpUp) => setAttributes({ showBumpUp })}
										/>

										<Alignment
											label={__("Text Alignment", 'rtcl-elementor-builder')}
											value={badgeAlign}
											responsive={true}
											options={['left', 'center', 'right']}
											onChange={val => setAttributes({ badgeAlign: val })}
											resRequiredProps={resRequiredProps}
										/>

									</PanelBody>
								</>
							)}

							{tabs.name === 'styles' && (
								<>

									<PanelBody title={__('Badge')} initialOpen={true}>
										<Dimension
											label={__('Padding', 'rtcl-elementor-builder')}
											type="padding"
											responsive={true}
											value={badgePadding}
											onChange={(badgePadding) => {
												setAttributes({ badgePadding })
											}}
											resRequiredProps={resRequiredProps}
										/>

										<RangeDevice
											label={__("Spacing", "rtcl-elementor-builder")}
											responsive={true}
											value={badgeSpacing}
											min={1}
											max={200}
											step={1}
											onChange={(val) => setAttributes({ badgeSpacing: val })}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>

									<PanelBody title={__('Default Badge', 'rtcl-elementor-builder')} initialOpen={true}>

										<Typography
											label={__('Badge Default Typography', 'rtcl-elementor-builder')}
											value={badgeTypo}
											onChange={(val) => setAttributes({ badgeTypo: val })}
											resRequiredProps={resRequiredProps}
										/>
										<Color
											label={__('Badge Default Background', 'rtcl-elementor-builder')}
											color={badgeBgColor}
											onChange={(badgeBgColor) => setAttributes({ badgeBgColor })}
										/>

										<Color
											label={__('Badge Default Text Color', 'rtcl-elementor-builder')}
											color={badgeTextColor}
											onChange={(badgeTextColor) => setAttributes({ badgeTextColor })}
										/>


									</PanelBody>

									{showTop && (<PanelBody title={__('Top', 'rtcl-elementor-builder')}>
										<Color
											label={__('Top Background Color', 'rtcl-elementor-builder')}
											color={topBgColor}
											onChange={(topBgColor) => setAttributes({ topBgColor })}
										/>
										<Color
											label={__('Top Text Color', 'rtcl-elementor-builder')}
											color={topTextColor}
											onChange={(topTextColor) => setAttributes({ topTextColor })}
										/>
									</PanelBody>)}

									{showFeature && (<PanelBody title={__('Featured', 'rtcl-elementor-builder')}>
										<Color
											label={__('Featured Background Color', 'rtcl-elementor-builder')}
											color={featureBgColor}
											onChange={(featureBgColor) => setAttributes({ featureBgColor })}
										/>
										<Color
											label={__('Featured Text Color', 'rtcl-elementor-builder')}
											color={featureTextColor}
											onChange={(featureTextColor) => setAttributes({ featureTextColor })}
										/>
									</PanelBody>)}

									{showNew && (<PanelBody title={__('New', 'rtcl-elementor-builder')}>
										<Color
											label={__('New Background Color', 'rtcl-elementor-builder')}
											color={newBgColor}
											onChange={(newBgColor) => setAttributes({ newBgColor })}
										/>
										<Color
											label={__('New Text Color', 'rtcl-elementor-builder')}
											color={newTextColor}
											onChange={(newTextColor) => setAttributes({ newTextColor })}
										/>
									</PanelBody>)}

									{showPopular && (<PanelBody title={__('Popular', 'rtcl-elementor-builder')}>
										<Color
											label={__('Popular Background Color', 'rtcl-elementor-builder')}
											color={popularBgColor}
											onChange={(popularBgColor) => setAttributes({ popularBgColor })}
										/>
										<Color
											label={__('Popular Text Color', 'rtcl-elementor-builder')}
											color={popularTextColor}
											onChange={(popularTextColor) => setAttributes({ popularTextColor })}
										/>
									</PanelBody>)}

									{showBumpUp && (<PanelBody title={__('BumpUp', 'rtcl-elementor-builder')}>
										<Color
											label={__('BumpUp Background Color', 'rtcl-elementor-builder')}
											color={bumpUpBgColor}
											onChange={(bumpUpBgColor) => setAttributes({ bumpUpBgColor })}
										/>
										<Color
											label={__('BumpUp Text Color', 'rtcl-elementor-builder')}
											color={bumpUpTextColor}
											onChange={(bumpUpTextColor) => setAttributes({ bumpUpTextColor })}
										/>
									</PanelBody>)}

								</>
							)}

							{tabs.name === 'advance' && (
								<>
									<AdvancedControl
										attributes={attributes}
										setAttributes={setAttributes}
									/>
								</>
							)}

						</div>
					)}
				</TabPanel>
			</div>
		</InspectorControls>
	)

}

export default Inspector;