import { useEffect } from '@wordpress/element';
import {
	rangeDeviceStyle,
	typographyStyle,
	mainWrapperStyle,
	softMinifyCssStrings,
	dimensionStyle
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		listAlign,
		listGap,
		listColumn,
		listTypo,
		listIconBgColor,
		listIconColor,
		listIconSize,
		listIconDimention,
		listMargin,
		listTextColor,
		titleMargin,
		titleTypo,
		titleColor
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});

	const {
		rangeDeviceDesk: listAlignStyleDesk,
		rangeDeviceTab: listAlignStyleTab,
		rangeDeviceMob: listAlignStyleMob
	} = rangeDeviceStyle({
		controlValue: listAlign,
		property: 'justify-content',
	});

	const {
		deskDimension: listMarginStyleDesk,
		tabDimension: listMarginStyleTab,
		mobDimension: listMarginStyleMob
	} = dimensionStyle({
		controlValue: listMargin,
		property: 'margin',
		important: true
	});

	const {
		rangeDeviceDesk: listGapStyleDesk,
		rangeDeviceTab: listGapStyleTab,
		rangeDeviceMob: listGapStyleMob
	} = rangeDeviceStyle({
		controlValue: listGap,
		property: 'gap',
	});

	const {
		deskTypography: listTypoStyleDesk,
		tabTypography: listTypoStyleTab,
		mobTypography: listTypoStyleMob
	} = typographyStyle({
		controlValue: listTypo
	});

	const {
		deskDimension: titleMarginStyleDesk,
		tabDimension: titleMarginStyleTab,
		mobDimension: titleMarginStyleMob
	} = dimensionStyle({
		controlValue: titleMargin,
		property: 'margin',
		important: true
	});

	const {
		deskTypography: titleTypoStyleDesk,
		tabTypography: titleTypoStyleTab,
		mobTypography: titleTypoStyleMob
	} = typographyStyle({
		controlValue: titleTypo
	});


	const deskAllStyles = softMinifyCssStrings(`
	.${blockId}.rtcl-block-frontend{
		margin-top: 15px;
		margin-bottom: 15px;
	}
		.${blockId} .single-listing-list-wrap{
			${listAlignStyleDesk}
		}
		.${blockId} .rtin-specs-title{
			margin:0;
			margin-bottom:10px;
			font-size:22px;
			${titleMarginStyleDesk}
			${titleTypoStyleDesk}
			${titleColor ? `color:${titleColor};` : ''}
		}

		.${blockId} .rtcl-single-block.el-single-addon.classima-listing-single 
		.classima-single-details .rtin-specs .rtin-spec-items{
			${listGapStyleDesk}
			${listMarginStyleDesk}
			${listColumn?.lg && `display: grid;grid-template-columns:repeat(${listColumn?.lg}, 1fr);`}
		}
		.${blockId} .rtcl-single-block.classima-listing-single 
		.classima-single-details .rtin-specs .rtin-spec-items li:before{
			display: flex;
			align-items: center;
			justify-content: center;
			${listIconColor ? `color:${listIconColor};` : ''}
			${listIconBgColor ? `background-color:${listIconBgColor};` : ''}
			${listIconSize ? `font-size:${listIconSize}px;` : ''}
			${listIconDimention ? `width:${listIconDimention}px;` : ''}
			${listIconDimention ? `height:${listIconDimention}px;` : ''}
		}

		.${blockId} .rtcl-single-block.classima-listing-single 
		.classima-single-details .rtin-specs .rtin-spec-items li{
			${listTextColor ? `color:${listTextColor};` : ''}
			${listTypoStyleDesk}
		}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
		.${blockId} .single-listing-list-wrap{
			${listAlignStyleTab}
		}
		.${blockId} .rtin-specs-title{
			${titleMarginStyleTab}
			${titleTypoStyleTab}
		}
		.${blockId} .rtcl-single-block.el-single-addon.classima-listing-single 
		.classima-single-details .rtin-specs .rtin-spec-items{
			${listGapStyleTab}
			${listMarginStyleTab}
			${listColumn?.md && `display: grid;grid-template-columns:repeat(${listColumn?.md}, 1fr);`}
		}

		.${blockId} .rtcl-single-block.classima-listing-single 
		.classima-single-details .rtin-specs .rtin-spec-items li{
			${listTypoStyleTab}
		}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
		.${blockId} .single-listing-list-wrap{
			${listAlignStyleMob}
		}
		.${blockId} .rtin-specs-title{
			${titleMarginStyleMob}
			${titleTypoStyleMob}
		}
		.${blockId} .rtcl-single-block.el-single-addon.classima-listing-single 
		.classima-single-details .rtin-specs .rtin-spec-items{
			${listGapStyleMob}
			${listMarginStyleMob}
			${listColumn?.sm && `display: grid;grid-template-columns:repeat(${listColumn?.sm}, 1fr);`}
		}

		.${blockId} .rtcl-single-block.classima-listing-single 
		.classima-single-details .rtin-specs .rtin-spec-items li{
			${listTypoStyleMob}
		}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and (max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and (max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;