import { useEffect } from '@wordpress/element';
import {
	mainWrapperStyle,
	softMinifyCssStrings,
	rangeDeviceStyle,
	typographyStyle,
	dimensionStyle
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		mapWidth,
		mapHeight,
		showLabel,
		titleTypo,
		titleColor,
		titleMargin
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});

	const {
		rangeDeviceDesk: mapWidthStyleDesk,
		rangeDeviceTab: mapWidthStyleTab,
		rangeDeviceMob: mapWidthStyleMob
	} = rangeDeviceStyle({
		controlValue: mapWidth,
		property: 'width',
		important: true
	});

	const {
		rangeDeviceDesk: mapHeightStyleDesk,
		rangeDeviceTab: mapHeightStyleTab,
		rangeDeviceMob: mapHeightStyleMob
	} = rangeDeviceStyle({
		controlValue: mapHeight,
		property: 'height',
		important: true
	});

	const {
		deskDimension: titleMarginStyleDesk,
		tabDimension: titleMarginStyleTab,
		mobDimension: titleMarginStyleMob
	} = dimensionStyle({
		controlValue: titleMargin,
		property: 'margin',
		important: true
	});

	const {
		deskTypography: titleTypoStyleDesk,
		tabTypography: titleTypoStyleTab,
		mobTypography: titleTypoStyleMob
	} = typographyStyle({
		controlValue: titleTypo
	});

	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}.rtcl-block-frontend{
			margin-top: 15px;
			margin-bottom: 15px;
		}
		.${blockId} .embed-responsive {
			${mapWidthStyleDesk}
			${mapHeightStyleDesk}
		}
		.${blockId} .main-title{
			margin:0;
			margin-bottom:10px;
			font-size:22px;
			${titleMarginStyleDesk}
			${titleTypoStyleDesk}
			${titleColor ? `color:${titleColor};` : ''}
		}
		.${blockId} .content-block-gap{
			display:none;
		}
		${!showLabel ?
			`.${blockId} .main-title-block{display:none;}`
			: ''}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
		.${blockId} .embed-responsive {
			${mapWidthStyleTab}
			${mapHeightStyleTab}
		}
		.${blockId} .main-title{
			${titleMarginStyleTab}
			${titleTypoStyleTab}
		}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
		.${blockId} .embed-responsive {
			${mapWidthStyleMob}
			${mapHeightStyleMob}
		}
		.${blockId} .main-title{
			${titleMarginStyleMob}
			${titleTypoStyleMob}
		}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and(max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and(max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;