import { useEffect } from '@wordpress/element';
import {
	rangeDeviceStyle,
	typographyStyle,
	mainWrapperStyle,
	softMinifyCssStrings,
	dimensionStyle
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,

		bannerHeight,
		bannerWidth,
		logoWrapHeight,
		logoWrapWidth,

		nameTypo,
		nameColor,
		catTypo,
		catColor,
		catIconColor,
		reviewTypo,
		reviewColor,
		logoBorder,
		logoImageWidth,
		logoWrapBgColor
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});

	const {
		rangeDeviceDesk: bannerHeightDesk,
		rangeDeviceTab: bannerHeightTab,
		rangeDeviceMob: bannerHeightMob
	} = rangeDeviceStyle({
		controlValue: bannerHeight,
		property: 'height',
	});

	const {
		rangeDeviceDesk: bannerWidthDesk,
		rangeDeviceTab: bannerWidthTab,
		rangeDeviceMob: bannerWidthMob
	} = rangeDeviceStyle({
		controlValue: bannerWidth,
		property: 'width',
	});

	const {
		rangeDeviceDesk: logoWrapHeightDesk,
		rangeDeviceTab: logoWrapHeightTab,
		rangeDeviceMob: logoWrapHeightMob
	} = rangeDeviceStyle({
		controlValue: logoWrapHeight,
		property: 'height',
	});

	const {
		rangeDeviceDesk: logoWrapWidthDesk,
		rangeDeviceTab: logoWrapWidthTab,
		rangeDeviceMob: logoWrapWidthMob
	} = rangeDeviceStyle({
		controlValue: logoWrapWidth,
		property: 'width',
	});

	const {
		deskTypography: nameTypoDesk,
		tabTypography: nameTypoTab,
		mobTypography: nameTypoMob
	} = typographyStyle({
		controlValue: nameTypo
	});
	const {
		deskTypography: catTypoDesk,
		tabTypography: catTypoTab,
		mobTypography: catTypoMob
	} = typographyStyle({
		controlValue: catTypo
	});
	const {
		deskTypography: reviewTypoDesk,
		tabTypography: reviewTypoTab,
		mobTypography: reviewTypoMob
	} = typographyStyle({
		controlValue: reviewTypo
	});
	const {
		deskDimension: logoBorderDesk,
		tabDimension: logoBorderTab,
		mobDimension: logoBorderMob
	} = dimensionStyle({
		controlValue: logoBorder,
		property: 'border-width',
		important: true
	});
	const {
		rangeDeviceDesk: logoImageWidthDesk,
		rangeDeviceTab: logoImageWidthTab,
		rangeDeviceMob: logoImageWidthMob
	} = rangeDeviceStyle({
		controlValue: logoImageWidth,
		property: 'width',
	});

	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}{
			margin-top:15px;
			margin-bottom:15px;
		}
		.${blockId} .store-banner .banner{
			${bannerHeightDesk}
		}
		.${blockId} .store-banner .banner img{
			${bannerWidthDesk}
		}
		.${blockId} .rtcl.store-content-wrap .store-banner .store-name-logo .store-logo{
			${logoWrapWidthDesk}
			${logoWrapHeightDesk}
			${logoWrapBgColor ? `background:${logoWrapBgColor}; !important` : ''}
		}
		.${blockId} .store-banner .store-name-logo img{
			${logoBorderDesk}
			${logoImageWidthDesk}
		}
		.${blockId} .rtcl.store-content-wrap .store-banner .store-name-logo .store-name h2{
			${nameTypoDesk}
			${nameColor ? `color:${nameColor};` : ''}
		}
		.${blockId} .rtcl.store-content-wrap .store-banner .store-name-logo .store-info .rtcl-store-cat{
			${catTypoDesk}
			${catColor ? `color:${catColor};` : ''}
		}
		.${blockId} .rtcl.store-content-wrap .store-banner .store-name-logo .store-info .rtcl-store-cat .rtcl-icon{
			${catIconColor ? `color:${catIconColor};` : ''}
		}
		.${blockId} .rtcl.store-content-wrap .store-banner .store-name-logo .store-info .reviews-rating{
			${reviewTypoDesk}
		}
		.${blockId} .rtcl.store-content-wrap .store-banner .store-name-logo .store-info .reviews-rating-count{
			${reviewColor ? `color:${reviewColor};` : ''}
		}
	
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
	.${blockId} .store-banner .banner{
		${bannerHeightTab}
	}
	.${blockId} .store-banner .banner img{
		${bannerWidthTab}
	}
	.${blockId} .rtcl.store-content-wrap .store-banner .store-name-logo .store-logo{
		${logoWrapWidthTab}
		${logoWrapHeightTab}
	}
	.${blockId} .store-banner .store-name-logo img{
		${logoBorderTab}
		${logoImageWidthTab}
	}
	.${blockId} .rtcl.store-content-wrap .store-banner .store-name-logo .store-name h2{
		${nameTypoTab}
	}
	.${blockId} .rtcl.store-content-wrap .store-banner .store-name-logo .store-info .rtcl-store-cat{
		${catTypoTab}
	}
	.${blockId} .rtcl.store-content-wrap .store-banner .store-name-logo .store-info .reviews-rating{
		${reviewTypoTab}
	}
		
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
	.${blockId} .store-banner .banner{
		${bannerHeightMob}
	}
	.${blockId} .store-banner .banner img{
		${bannerWidthMob}
	}
	.${blockId} .rtcl.store-content-wrap .store-banner .store-name-logo .store-logo{
		${logoWrapWidthMob}
		${logoWrapHeightMob}
	}
	.${blockId} .store-banner .store-name-logo img{
		${logoBorderMob}
		${logoImageWidthMob}
	}
	.${blockId} .rtcl.store-content-wrap .store-banner .store-name-logo .store-name h2{
		${nameTypoMob}
	}
	.${blockId} .rtcl.store-content-wrap .store-banner .store-name-logo .store-info .rtcl-store-cat{
		${catTypoMob}
	}
	.${blockId} .rtcl.store-content-wrap .store-banner .store-name-logo .store-info .reviews-rating{
		${reviewTypoMob}
	}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and (max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and (max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;