import { useEffect } from '@wordpress/element';
import {
	rangeDeviceStyle,
	typographyStyle,
	mainWrapperStyle,
	softMinifyCssStrings,
	dimensionStyle
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		openingTypo,
		dayMargin,
		dayPadding,
		openingTextColor,
		todayTextColor,
		offdayTextColor,
		openingBorder
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});

	const {
		deskTypography: openingTypoStyleDesk,
		tabTypography: openingTypoStyleTab,
		mobTypography: openingTypoStyleMob
	} = typographyStyle({
		controlValue: openingTypo
	});

	const {
		deskDimension: dayMarginStyleDesk,
		tabDimension: dayMarginStyleTab,
		mobDimension: dayMarginStyleMob
	} = dimensionStyle({
		controlValue: dayMargin,
		property: 'margin',
		important: true
	});

	const {
		deskDimension: dayPaddingStyleDesk,
		tabDimension: dayPaddingStyleTab,
		mobDimension: dayPaddingStyleMob
	} = dimensionStyle({
		controlValue: dayPadding,
		property: 'padding',
		important: true
	});

	const {
		deskDimension: openingBorderStyleDesk,
		tabDimension: openingBorderStyleTab,
		mobDimension: openingBorderStyleMob
	} = dimensionStyle({
		controlValue: openingBorder,
		property: 'border-width',
		important: true
	});

	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}.rtcl-block-frontend{
			margin-top: 15px;
			margin-bottom: 15px;
		}
		.${blockId} .store-hours-list .store-hour{
			${openingTypoStyleDesk}
			${openingBorderStyleDesk}
			${dayMarginStyleDesk}
			${openingTextColor ? `color:${openingTextColor};` : ''}
		}
		.${blockId} .store-hours-list .store-hour .hour-day, 
		.${blockId} .store-hours-list .store-hour .oh-hours-wrap{
			${dayPaddingStyleDesk}
		}
		.${blockId} .store-hours-list .store-hour.current-store-hour{
			${todayTextColor ? `color:${todayTextColor};` : ''}
		}
		.${blockId} .store-hours-list .store-hour .off-day{
			${offdayTextColor ? `color:${offdayTextColor};` : ''}
		}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
	.${blockId} .store-hours-list .store-hour{
		${openingTypoStyleTab}
		${openingBorderStyleTab}
		${dayMarginStyleTab}
	}
	.${blockId} .store-hours-list .store-hour .hour-day, 
	.${blockId} .store-hours-list .store-hour .oh-hours-wrap{
		${dayPaddingStyleTab}
	}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
	.${blockId} .store-hours-list .store-hour{
		${openingTypoStyleMob}
		${openingBorderStyleMob}
		${dayMarginStyleMob}
	}
	.${blockId} .store-hours-list .store-hour .hour-day, 
	.${blockId} .store-hours-list .store-hour .oh-hours-wrap{
		${dayPaddingStyleMob}
	}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and (max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and (max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;