import { __ } from '@wordpress/i18n';
import { InspectorControls } from '@wordpress/block-editor';
import {
	BaseControl,
	Button,
	ButtonGroup,
	PanelBody,
	TabPanel,
	Icon
} from "@wordpress/components";

import {
	Range,
	RangeDevice,
	IconControl,
	RenderIcons,
	AdvancedControl
} from '../../components';

function Inspector(props) {
	const { attributes, setAttributes } = props;

	const {
		resDevice,
		gutterX,
		columns,
		containerWidth,
		innerContainerWidth,
		alignItem,
		justifyContent,
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	const alignItemsOptions = [
		{
			value: 'flex-start',
			label: __('Flex Start', 'rtcl-elementor-builder'),
			icon: <Icon icon={RenderIcons(`flex-ai-row-start`)} />,
		},
		{
			value: 'center',
			label: __('Center', 'rtcl-elementor-builder'),
			icon: <Icon icon={RenderIcons(`flex-ai-row-center`)} />,
		},
		{
			value: 'flex-end',
			label: __('Flex End', 'rtcl-elementor-builder'),
			icon: <Icon icon={RenderIcons(`flex-ai-row-end`)} />,
		},
		{
			value: 'stretch',
			label: __('Stretch', 'rtcl-elementor-builder'),
			icon: <Icon icon={RenderIcons(`flex-ai-row-strech`)} />,
		},
	];
	const justifyContentOptions = [
		{
			value: 'flex-start',
			label: __('Flex Start', 'rtcl-elementor-builder'),
			icon: <Icon icon={RenderIcons(`flex-jc-row-start`)} />,
		},
		{
			value: 'center',
			label: __('Center', 'rtcl-elementor-builder'),
			icon: <Icon icon={RenderIcons(`flex-jc-row-center`)} />,
		},
		{
			value: 'flex-end',
			label: __('Flex End', 'rtcl-elementor-builder'),
			icon: <Icon icon={RenderIcons(`flex-jc-row-end`)} />,
		},
		{
			value: 'space-between',
			label: __('Space Between', 'rtcl-elementor-builder'),
			icon: <Icon icon={RenderIcons(`flex-jc-row-space-between`)} />,
		},
		{
			value: 'space-around',
			label: __('Space Around', 'rtcl-elementor-builder'),
			icon: <Icon icon={RenderIcons(`flex-jc-row-space-around`)} />,
		},
		{
			value: 'space-evenly',
			label: __('Space Evenly', 'rtcl-elementor-builder'),
			icon: <Icon icon={RenderIcons(`flex-jc-row-space-evenly`)} />,
		},
	];



	return (
		<InspectorControls key="controls">
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>

					{(tab) => (

						<div className="rtrb-tab-content">
							{tab.name === "general" && (
								<>
									<PanelBody title={__('Row Settings', 'rtcl-elementor-builder')} initialOpen={true}>
										<BaseControl label={__("Container Width", "rtcl-elementor-builder")} className="rtrb-control-field rtrb-cf-heading-warp">
											<ButtonGroup className="rtrb-btn-group">
												{[{ label: 'Full Width', value: 'fullwidth' }, { label: 'Boxed', value: 'boxed' }].map((item, key) => (
													<Button
														key={key}
														variant={containerWidth === item.value ? 'primary' : 'secondary'}
														onClick={() => setAttributes({ containerWidth: item.value })}
													>
														{item.label}
													</Button>
												))}
											</ButtonGroup>
										</BaseControl>

										{containerWidth === 'boxed' && (
											<RangeDevice
												label={__('Max Width (px)')}
												responsive={true}
												value={innerContainerWidth}
												defultValue={{ lg: 1200, md: 984, sm: 564, unit: 'px' }}
												min={0}
												max={1600}
												step={1}
												onChange={(val) => setAttributes({ innerContainerWidth: val })}
												resRequiredProps={resRequiredProps}
											/>
										)}

										<Range
											label={__('Column')}
											reset={true}
											value={columns}
											min={0}
											max={6}
											step={1}
											onChange={(val) => setAttributes({ columns: val })}
										/>

										<RangeDevice
											label={__('Columns Gap')}
											responsive={true}
											value={gutterX}
											min={0}
											max={100}
											step={1}
											onChange={(val) => setAttributes({ gutterX: val })}
											resRequiredProps={resRequiredProps}
										/>

										<IconControl
											label={__("Align Items", "rtcl-elementor-builder")}
											value={alignItem}
											responsive={true}
											options={alignItemsOptions}
											onChange={val => setAttributes({ alignItem: val })}
											resRequiredProps={resRequiredProps}
										/>

										<IconControl
											label={__("Justify Content", "rtcl-elementor-builder")}
											value={justifyContent}
											responsive={true}
											options={justifyContentOptions}
											onChange={val => setAttributes({ justifyContent: val })}
											resRequiredProps={resRequiredProps}
										/>

									</PanelBody>
								</>
							)}
							{tab.name === "advance" && (
								<AdvancedControl
									attributes={attributes}
									setAttributes={setAttributes}
									type='row'
								/>
							)}
						</div>
					)}
				</TabPanel>

			</div >
		</InspectorControls >
	)
}

export default Inspector;