const { __ } = wp.i18n;
import {
	PanelBody, SelectControl, ToggleControl
} from "@wordpress/components";

import {
	ARROW_POSITION, AUTOPLAY_DELAY, DOT_STYLE_OPTIONS
} from "../globals/Constants";

import RangeDevice from "../components/RangeDevice";
import Range from "../components/Range";

function SliderSetting(props) {

	const { attributes, setAttributes, initialOpen = true, dot = true, resRequiredProps } = props;

	const {
		sliderOptions,
		slidesItem,
	} = attributes;

	return (
		<>
			<PanelBody title={__('Slider Options', 'rtcl-elementor-builder')} initialOpen={initialOpen}>
				<ToggleControl
					label={__("Auto Heighit", 'rtcl-elementor-builder')}
					checked={sliderOptions["autoHeight"]}
					className="rtrb-control-field"
					onChange={(autoHeight) => {
						setAttributes({ sliderOptions: { ...sliderOptions, autoHeight } })
					}}
				/>

				<ToggleControl
					label={__("Loop", 'rtcl-elementor-builder')}
					className="rtrb-control-field"
					checked={sliderOptions["loop"]}
					onChange={(loop) => {
						setAttributes({ sliderOptions: { ...sliderOptions, loop } })
					}}
				/>

				<ToggleControl
					label={__("Autoplay", 'rtcl-elementor-builder')}
					className="rtrb-control-field"
					checked={sliderOptions["autoPlay"]}
					onChange={(autoPlay) => {
						setAttributes({ sliderOptions: { ...sliderOptions, autoPlay } })
					}}
				/>


				{sliderOptions["autoPlay"] == true && <>
					<ToggleControl
						label={__("Stop On Hover", 'rtcl-elementor-builder')}
						className="rtrb-control-field"
						checked={sliderOptions["stopOnHover"]}
						onChange={(stopOnHover) => {
							setAttributes({ sliderOptions: { ...sliderOptions, stopOnHover } })
						}}
					/>

					<SelectControl
						label={__('Autoplay Delay', 'rtcl-elementor-builder')}
						options={AUTOPLAY_DELAY}
						value={sliderOptions["autoPlayDelay"]}
						onChange={(autoPlayDelay) => {
							setAttributes({ sliderOptions: { ...sliderOptions, autoPlayDelay } })
						}}
					/>
				</>}

				<Range
					label={__('Slide Transition Speed', 'rtcl-elementor-builder')}
					value={sliderOptions["autoPlaySlideSpeed"]}
					onChange={(autoPlaySlideSpeed) => setAttributes({ sliderOptions: { ...sliderOptions, autoPlaySlideSpeed } })}
					min={1000}
					max={5000}
				/>

				<Range
					label={__('Space Between', 'rtcl-elementor-builder')}
					value={sliderOptions["spaceBetween"]}
					onChange={(spaceBetween) => setAttributes({ sliderOptions: { ...sliderOptions, spaceBetween } })}
					min={0}
					max={100}
					reset={true}
				/>

				<RangeDevice
					label={__('Slides To Item', 'rtcl-elementor-builder')}
					responsive={true}
					value={slidesItem}
					min={1}
					max={8}
					step={1}
					onChange={(val) => setAttributes({ slidesItem: val })}
					resRequiredProps={resRequiredProps}
				/>

				<ToggleControl
					label={__("Arrow Navigation", 'rtcl-elementor-builder')}
					className="rtrb-control-field"
					checked={sliderOptions["arrowNavigation"]}
					onChange={(arrowNavigation) => {
						setAttributes({ sliderOptions: { ...sliderOptions, arrowNavigation } })
					}}
				/>

				{(sliderOptions["arrowNavigation"]) &&
					<>
						<SelectControl
							label={__('Arrow Position', 'rtcl-elementor-builder')}
							options={ARROW_POSITION}
							value={sliderOptions["arrowPosition"] || '1'}
							onChange={(arrowPosition) => {
								setAttributes({ sliderOptions: { ...sliderOptions, arrowPosition } })
							}}
						/>
					</>
				}

				{dot && (
					<>
						<ToggleControl
							label={__("Dot Navigation", 'rtcl-elementor-builder')}
							className="rtrb-control-field"
							checked={sliderOptions["dotNavigation"]}
							onChange={(dotNavigation) => {
								setAttributes({ sliderOptions: { ...sliderOptions, dotNavigation } })
							}}
						/>

						{sliderOptions["dotNavigation"] && <SelectControl
							label={__('Dot style', 'rtcl-elementor-builder')}
							options={DOT_STYLE_OPTIONS}
							value={sliderOptions["dotStyle"]}
							onChange={(dotStyle) => {
								setAttributes({ sliderOptions: { ...sliderOptions, dotStyle } })
							}}
						/>}
					</>
				)}

				<ToggleControl
					label={__("Slider Loader", 'rtcl-elementor-builder')}
					className="rtrb-control-field"
					checked={sliderOptions["sliderLoader"]}
					onChange={(sliderLoader) => setAttributes({
						sliderOptions: { ...sliderOptions, sliderLoader }
					})}
				/>
			</PanelBody>
		</>
	)
}

export default SliderSetting;