import { __ } from '@wordpress/i18n';
import { useEffect, useMemo } from '@wordpress/element';
import Inspector from "./inspector";
import { withSelect, useDispatch } from '@wordpress/data';
import { compose } from '@wordpress/compose';
import {
	InnerBlocks,
	__experimentalBlockVariationPicker as BlockVariationPicker,
	useBlockProps
} from '@wordpress/block-editor';
import { createBlock } from '@wordpress/blocks';
import { Icons } from "../../components";
import styles from './styles';
import { uniqueBlockId, blockEditorWrapperClass } from '../../helpers';

function Edit(props) {
	const { isSelected, attributes, setAttributes, clientId, className } = props;

	const {
		blockId,
		columns,
		gutterX,
		gutterY,
	} = attributes;

	let inlineStyle = {
		'--d-gap-x': gutterX['lg'] + gutterX['unit'],
		'--t-gap-x': gutterX['md'] + gutterX['unit'],
		'--m-gap-x': gutterX['sm'] + gutterX['unit'],
		'--d-gap-y': gutterY['lg'] + gutterY['unit'],
		'--t-gap-y': gutterY['md'] + gutterY['unit'],
		'--m-gap-y': gutterY['sm'] + gutterY['unit'],
	}

	useEffect(() => {
		const BLOCK_PREFIX = 'rtcl-row-';
		uniqueBlockId(BLOCK_PREFIX, clientId, attributes, setAttributes);
	}, []);

	//generate all style
	const allStyles = styles({
		attributes,
		setAttributes
	})

	const editorWrapClass = blockEditorWrapperClass(useBlockProps, attributes);

	//variationPicker svg set width & height
	const variationPickerAllSvg = document.querySelectorAll('.rtrb-container-variation-picker svg');
	if (variationPickerAllSvg.length) {
		variationPickerAllSvg.forEach((svg) => {
			svg.setAttribute('width', '100%');
			svg.setAttribute('height', '100%');
		})
	}

	const getTemplate = useMemo(() => {
		const childColumns = [];
		for (let i = 0; i < columns; i++) {
			childColumns.push(['rtcl/column', { id: i + 1 }]);
		}
		return childColumns;
	}, [columns]);

	const blockVariationPickerOnSelect = (
		nextVariation = props.defaultVariation
	) => {
		if (nextVariation.attributes) {
			props.setAttributes(nextVariation.attributes);
		}

		if (nextVariation.innerBlocks) {
			props.replaceInnerBlocks(
				props.clientId,
				createBlocksFromInnerBlocksTemplate(nextVariation.innerBlocks)
			);
		}
	};

	const createBlocksFromInnerBlocksTemplate = (innerBlocksTemplate) => {
		return innerBlocksTemplate.map(
			([name, attributes, innerBlocks = []]) =>
				createBlock(
					name,
					attributes,
					createBlocksFromInnerBlocksTemplate(innerBlocks)
				)
		);
	};

	const { variations, hasInnerBlocks } = props;
	if (!hasInnerBlocks) {
		return (
			props.attributes.preview ? Icons.row_preview :
				<>
					<div className='rtrb-container-variation-picker'>
						<BlockVariationPicker
							icon={''}
							label={__('Select Column Layout', 'rtcl-elementor-builder')}
							variations={variations}
							instructions={''}
							onSelect={(nextVariation) =>
								blockVariationPickerOnSelect(nextVariation)
							}
						/>
					</div>
				</>
		);
	}


	return [
		isSelected && (
			<Inspector
				attributes={attributes}
				setAttributes={setAttributes}
			/>
		),

		<style>{`${allStyles}`}</style>,

		<div {...editorWrapClass} style={inlineStyle}>
			<InnerBlocks
				template={getTemplate}
				templateLock="all"
				allowedBlocks={['rtcl/column']}
			/>
		</div>
	]
}

const applyWithSelect = withSelect((select, props) => {
	const {
		attributes: {
			columns,
		},
	} = props;
	const { getBlocks } = select('core/block-editor');
	const {
		getBlockType,
		getBlockVariations,
		getDefaultBlockVariation,
	} = select('core/blocks');

	const innerBlocks = getBlocks(props.clientId);
	const { replaceInnerBlocks } = useDispatch('core/block-editor');

	return {
		innerBlocks,
		hasInnerBlocks: select('core/block-editor').getBlocks(props.clientId).length > 0,
		blockType: getBlockType(props.name),
		defaultVariation: typeof getDefaultBlockVariation === 'undefined' ? null : getDefaultBlockVariation(props.name),
		variations: typeof getBlockVariations === 'undefined' ? null : getBlockVariations(props.name),
		replaceInnerBlocks,
	};
});

export default compose(applyWithSelect)(Edit);
//export default Edit;