import { useBlockProps, InnerBlocks } from '@wordpress/block-editor';
import { blockFrontendWrapperClass } from '../../helpers';

export default function Save(props) {
	const {
		blockId,
		wrapperTag,
	} = props.attributes;

	const saveBlockProps = blockFrontendWrapperClass(useBlockProps, props.attributes, 'rtcl-block-wrapper')

	const WrapperTag = wrapperTag;

	return (
		<WrapperTag {...saveBlockProps}>
			<div className="rtcl-wrapper-inner">
				<InnerBlocks.Content />
			</div>
		</WrapperTag>
	);
}
