import { useEffect } from '@wordpress/element';
import {
	rangeDeviceStyle,
	typographyStyle,
	mainWrapperStyle,
	softMinifyCssStrings,
	dimensionStyle,
	boxshadowStyle
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		itemBoxBGColor,
		itemBoxMargin,
		itemBoxPadding,
		itemBoxBorder,
		itemBoxBoxShadow,
		itemTitleTypo,
		itemTitleBgColor,
		itemTitleColor,
		itemTitlePadding,
		filterBtnTypo,
		filterBtnBgColor,
		filterBtnColor,
		filterBtnPadding,
		filterBtnMargin,
		filterBtnBorder,
		filterBtnRadius
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});
	const {
		deskDimension: itemBoxMarginStyleDesk,
		tabDimension: itemBoxMarginStyleTab,
		mobDimension: itemBoxMarginStyleMob
	} = dimensionStyle({
		controlValue: itemBoxMargin,
		property: 'margin',
		important: true
	});

	const {
		deskDimension: itemBoxPaddingStyleDesk,
		tabDimension: itemBoxPaddingStyleTab,
		mobDimension: itemBoxPaddingStyleMob
	} = dimensionStyle({
		controlValue: itemBoxPadding,
		property: 'padding',
		important: true
	});

	const {
		deskDimension: itemBoxBorderStyleDesk,
		tabDimension: itemBoxBorderStyleTab,
		mobDimension: itemBoxBorderStyleMob
	} = dimensionStyle({
		controlValue: itemBoxBorder,
		property: 'border-width',
		important: true
	});

	const {
		boxshadowStyles: itemBoxShadowStyle,
	} = boxshadowStyle({
		controlValue: itemBoxBoxShadow,
	});

	const {
		deskTypography: itemTitleTypoStyleDesk,
		tabTypography: itemTitleTypoStyleTab,
		mobTypography: itemTitleTypoStyleMob
	} = typographyStyle({
		controlValue: itemTitleTypo
	});

	const {
		deskDimension: itemTitlePaddingStyleDesk,
		tabDimension: itemTitlePaddingStyleTab,
		mobDimension: itemTitlePaddingStyleMob
	} = dimensionStyle({
		controlValue: itemTitlePadding,
		property: 'padding',
		important: true
	});

	const {
		deskTypography: filterBtnTypoStyleDesk,
		tabTypography: filterBtnTypoStyleTab,
		mobTypography: filterBtnTypoStyleMob
	} = typographyStyle({
		controlValue: filterBtnTypo
	});

	const {
		deskDimension: filterBtnPaddingStyleDesk,
		tabDimension: filterBtnPaddingStyleTab,
		mobDimension: filterBtnPaddingStyleMob
	} = dimensionStyle({
		controlValue: filterBtnPadding,
		property: 'padding',
		important: true
	});

	const {
		deskDimension: filterBtnMarginStyleDesk,
		tabDimension: filterBtnMarginStyleTab,
		mobDimension: filterBtnMarginStyleMob
	} = dimensionStyle({
		controlValue: filterBtnMargin,
		property: 'margin',
		important: true
	});
	const {
		deskDimension: filterBtnBorderStyleDesk,
		tabDimension: filterBtnBorderStyleTab,
		mobDimension: filterBtnBorderStyleMob
	} = dimensionStyle({
		controlValue: filterBtnBorder,
		property: 'border-width',
		important: true
	});

	const {
		deskDimension: filterBtnRadiusStyleDesk,
		tabDimension: filterBtnRadiusStyleTab,
		mobDimension: filterBtnRadiusStyleMob
	} = dimensionStyle({
		controlValue: filterBtnRadius,
		property: 'border-width',
		important: true
	});

	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}.rtcl-block-frontend{
			margin-top: 15px;
			margin-bottom: 15px;
		}
		.${blockId}.elementor-widget .widget.rtcl-widget-filter-class .panel-block .rtcl-filter-form .ui-accordion-item{
			${itemBoxBGColor ? `background-color:${itemBoxBGColor};` : ''}
			${itemBoxMarginStyleDesk}
			${itemBoxBorderStyleDesk}
			${itemBoxShadowStyle}
		}
		.${blockId}.elementor-widget .widget.rtcl-widget-filter-class .panel-block .rtcl-filter-form .ui-accordion-item .ui-accordion-content{	
			${itemBoxPaddingStyleDesk}
		}
		.${blockId}.elementor-widget .rtcl-widget-filter-class .panel-block .rtcl-filter-form .ui-accordion-item:not(:first-child) .ui-accordion-title{
			margin-top:0;
		}
		.${blockId}.elementor-widget .widget.rtcl-widget-filter-class .panel-block .rtcl-filter-form .ui-accordion-item .ui-accordion-title{
      ${itemTitleTypoStyleDesk}
			${itemTitlePaddingStyleDesk}
			${itemTitleBgColor ? `background-color:${itemTitleBgColor};` : ''}
			${itemTitleColor ? `color:${itemTitleColor};` : ''}
		}
		.${blockId}.elementor-widget .widget.rtcl-widget-filter-class .panel-block .rtcl-filter-form .ui-buttons .btn{
			${filterBtnBgColor ? `background-color:${filterBtnBgColor};` : ''}
			${filterBtnColor ? `color:${filterBtnColor};` : ''}
			${filterBtnTypoStyleDesk}
			${filterBtnBorderStyleDesk}
			${filterBtnRadiusStyleDesk}
			${filterBtnMarginStyleDesk}
			${filterBtnPaddingStyleDesk}
		}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
	.${blockId}.elementor-widget .widget.rtcl-widget-filter-class .panel-block .rtcl-filter-form .ui-accordion-item{
		${itemBoxMarginStyleTab}
		${itemBoxBorderStyleTab}
	}
	.${blockId}.elementor-widget .widget.rtcl-widget-filter-class .panel-block .rtcl-filter-form .ui-accordion-item .ui-accordion-content{	
		${itemBoxPaddingStyleTab}
	}

	.${blockId}.elementor-widget .widget.rtcl-widget-filter-class .panel-block .rtcl-filter-form .ui-accordion-item .ui-accordion-title{
		${itemTitleTypoStyleTab}
		${itemTitlePaddingStyleTab}
	}
	.${blockId}.elementor-widget .widget.rtcl-widget-filter-class .panel-block .rtcl-filter-form .ui-buttons .btn{
		${filterBtnTypoStyleTab}
		${filterBtnBorderStyleTab}
		${filterBtnRadiusStyleTab}
		${filterBtnMarginStyleTab}
		${filterBtnPaddingStyleTab}
	}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
	.${blockId}.elementor-widget .widget.rtcl-widget-filter-class .panel-block .rtcl-filter-form .ui-accordion-item{
		${itemBoxMarginStyleMob}
		${itemBoxBorderStyleMob}
	}
	.${blockId}.elementor-widget .widget.rtcl-widget-filter-class .panel-block .rtcl-filter-form .ui-accordion-item .ui-accordion-content{	
		${itemBoxPaddingStyleMob}
	}

	.${blockId}.elementor-widget .widget.rtcl-widget-filter-class .panel-block .rtcl-filter-form .ui-accordion-item .ui-accordion-title{
		${itemTitleTypoStyleMob}
		${itemTitlePaddingStyleMob}
	}
	.${blockId}.elementor-widget .widget.rtcl-widget-filter-class .panel-block .rtcl-filter-form .ui-buttons .btn{
		${filterBtnTypoStyleMob}
		${filterBtnBorderStyleMob}
		${filterBtnRadiusStyleMob}
		${filterBtnMarginStyleMob}
		${filterBtnPaddingStyleMob}
	}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and (max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and (max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;