import { InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, ToggleControl, BaseControl, SelectControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import axios from 'axios';
import Qs from 'qs';
import { useState, useEffect } from '@wordpress/element';
import Select from 'react-select';
import {
	AdvancedControl,
	Color,
	Dimension,
	Typography,
	BorderDimension,
	RangeDevice
} from '../../components';


function Inspector({ attributes, setAttributes }) {
	const {
		resDevice,
		style,
		customFields,
		labelValueNewLine,
		labelTypo,
		dataTypo,
		labelColor,
		dataColor,
		customPadding,
		showTitle,
		titleTypo,
		titleColor,
		titleMargin,
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	const [customFieldList, setCustomFieldList] = useState([]);

	useEffect(() => {
		let ajaxData = {
			action: 'rtcl_custom_fields_list',
			rtcl_block_nonce: rtclBlockParams.rtcl_block_nonce,
		}
		axios.post(rtclBlockParams.ajaxurl, Qs.stringify(ajaxData))
			.then((response) => {
				if (response.data.data) {
					const listArr = [];
					const listObj = response.data.data;
					let i = 0;
					for (let [value, label] of Object.entries(listObj)) {
						listArr[i] = Object.assign({}, { label, value });
						i++;
					};
					setCustomFieldList([...listArr]);
				} else {
					setCustomFieldList([]);
				}
			})
			.catch((error) => console.log(error));
	}, []);

	const changeLyout = (selected) => {
		setAttributes({ style: selected });

		switch (selected) {
			case 'style-1':
				setAttributes({
					labelValueNewLine: true,
				});
				break;

			case 'style-2':
				setAttributes({
					labelValueNewLine: false,
				});
				break;

			default:
				return false;
		}
	}

	return (
		<InspectorControls key={'controls'}>
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "styles",
							title: "Styles",
							className: "rtrb-tab-btn styles",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>
					{(tabs) => (
						<div className="rtrb-tab-content">
							{tabs.name === 'general' && (
								<>
									<PanelBody title={__('General', 'rtcl-elementor-builder')} initialOpen={true}>
										<ToggleControl
											label={__("Show Title", "rtcl-elementor-builder")}
											onChange={(showTitle) => setAttributes({ showTitle })}
											checked={showTitle}
										/>

										<SelectControl
											label={__("Style", "rtcl-elementor-builder")}
											value={style}
											options={[
												{ label: 'Style 1', value: 'style-1' },
												{ label: 'Style 2', value: 'style-2' }
											]}
											onChange={(style) => changeLyout(style)}
										/>

										<ToggleControl
											label={__('Label And Value In New Line?', "rtcl-elementor-builder")}
											onChange={(labelValueNewLine) => setAttributes({ labelValueNewLine })}
											checked={labelValueNewLine}
										/>

										<BaseControl label={__('Custom Field Groups', "rtcl-elementor-builder")} >
											<Select
												value={customFields || null}
												onChange={(customFields) => setAttributes({ customFields })}
												options={customFieldList}
												isMulti={true}
											/>
										</BaseControl>

									</PanelBody>
								</>
							)}
							{tabs.name === 'styles' && (
								<>
									<PanelBody title={__("Title", "rtcl-elementor-builder")} initialOpen={true}>
										<Typography
											label={__('Typography', 'rtcl-elementor-builder')}
											value={titleTypo}
											onChange={(titleTypo) => setAttributes({ titleTypo })}
											resRequiredProps={resRequiredProps}
										/>
										<Color
											label={__("Color", "rtcl-elementor-builder")}
											color={titleColor}
											onChange={(titleColor) => setAttributes({ titleColor })}
										/>
										<Dimension
											label={__("Margin", "rtcl-elementor-builder")}
											type="margin"
											responsive={true}
											value={titleMargin}
											onChange={(titleMargin) => {
												setAttributes({ titleMargin })
											}}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>
									<PanelBody title={__('Fields', "rtcl-elementor-builder")} initialOpen={true}>
										<Typography
											label={__('Label Typography', 'rtcl-elementor-builder')}
											value={labelTypo}
											onChange={(labelTypo) => setAttributes({ labelTypo })}
											resRequiredProps={resRequiredProps}
										/>
										<Typography
											label={__('Data Typography', 'rtcl-elementor-builder')}
											value={dataTypo}
											onChange={(dataTypo) => setAttributes({ dataTypo })}
											resRequiredProps={resRequiredProps}
										/>
										<Color
											label={__('Label Color', 'rtcl-elementor-builder')}
											color={labelColor}
											onChange={(labelColor) => setAttributes({ labelColor })}
										/>
										<Color
											label={__('Data Color', 'rtcl-elementor-builder')}
											color={dataColor}
											onChange={(dataColor) => setAttributes({ dataColor })}
										/>
										<Dimension
											label={__('Padding', 'rtcl-elementor-builder')}
											type="padding"
											responsive={true}
											value={customPadding}
											onChange={(customPadding) => {
												setAttributes({ customPadding })
											}}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>
								</>
							)}
							{tabs.name === 'advance' && (
								<>
									<AdvancedControl
										attributes={attributes}
										setAttributes={setAttributes}
									/>
								</>
							)}
						</div>
					)}
				</TabPanel>
			</div>
		</InspectorControls>
	)

}

export default Inspector;