import { useState } from '@wordpress/element';
import Icons from './Icons';
import './scss/layout-control.scss';
import classnames from "classnames";
export default function LayoutControl(props) {
	const {
		label,
		value,
		options,
		columns = 3,
		onChange
	} = props;

	const [isClicked, setIsClicked] = useState(false);

	const handleProClick = () => {
		setIsClicked(true);
		setTimeout(() => {
			setIsClicked(false)
		}, 1800);
	}

	return (
		<div className="rtrb-control-field rtrb-cf-styles-wrap">

			{label && (
				<div className="rtrb-cf-head">
					<span className='rtrb-label'>{label}</span>
				</div>
			)}

			<div className={`rtrb-style-list rtrb-style-columns-${columns}`}>
				{options.map((data, index) => (
					<div
						role="button"
						key={index}
						aria-label={data?.label || ''}
						onClick={() => {
							if (!rtclBlockParams.rtclPro && data?.isPro) {
								handleProClick();
							} else {
								onChange(data.value)
							}
						}}
						className={classnames({
							'rtrb-active': value === data.value,
							'is-pro': !rtclBlockParams.rtclPro && data.isPro,
							'rtrb-pro': !rtclBlockParams.rtclPro && data.isPro && isClicked,
						})}
					>
						{data.icon && <span className="rtrb-layout rtrb-style-icon">{data.icon}</span>}
						{data.svg && <span
							className="rtrb-layout rtrb-style-svg">{Icons[data.svg] ? Icons[data.svg] : data.svg}</span>}
						{data.img && <span className="rtrb-layout rtrb-style-img">{data.img}</span>}
						{data.label && <span className="rtrb-label">{data.label}</span>}
					</div>
				))}

			</div>
		</div>
	)
}