import { InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, ToggleControl, ButtonGroup, Button, SelectControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import axios from 'axios';
import Qs from 'qs';
import { useState, useEffect } from '@wordpress/element';
import {
	AdvancedControl,
	Color,
} from '../../components';

function Inspector({ attributes, setAttributes }) {
	const {
		resDevice,
		showLightboxIcon,
		enableZoom,
		enableSlider,
		showBadge,
		showVideo,
		showArrow,
		enableThumbSlider,
		galleryImageSize,

		arrowColor,
		arrowBgColor,
		zoomIconColor,
		zoomIconBgColor

	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	const [imageSize, setImageSize] = useState([]);

	useEffect(() => {
		let data = {
			action: 'rtcl_gb_get_all_image_size_ajax',
			rtcl_nonce: rtcl_block_script.rtcl_nonce,
		}

		axios.post(rtcl_block_script.ajaxurl, Qs.stringify(data))
			.then((response) => {
				if (response.data.data) {
					let imageSize = [];
					let ai = 0;
					for (const [value, label] of Object.entries(response.data.data)) {
						imageSize[ai] = { value, label };
						ai++;
					}
					setImageSize([...imageSize])
				} else {
					setImageSize([])
				}
			})
			.catch((error) => console.log(error));
	}, []);

	return (
		<InspectorControls key={'controls'}>
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "styles",
							title: "Styles",
							className: "rtrb-tab-btn styles",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>
					{(tabs) => (
						<div className="rtrb-tab-content">
							{tabs.name === 'general' && (
								<>
									<PanelBody title={__('General', 'rtcl-elementor-builder')} initialOpen={true}>
										<ToggleControl
											label={__('Enable Slider', "rtcl-elementor-builder")}
											onChange={(enableSlider) => setAttributes({ enableSlider })}
											checked={enableSlider}
										/>
										{enableSlider && (
											<>
												<ToggleControl
													label={__('Show Thumbnail', "rtcl-elementor-builder")}
													onChange={(enableThumbSlider) => setAttributes({ enableThumbSlider })}
													checked={enableThumbSlider}
												/>
												<ToggleControl
													label={__('Show Arrow', "rtcl-elementor-builder")}
													onChange={(showArrow) => setAttributes({ showArrow })}
													checked={showArrow}
												/>
											</>
										)}
										<ToggleControl
											label={__('Show Video', "rtcl-elementor-builder")}
											onChange={(showVideo) => setAttributes({ showVideo })}
											checked={showVideo}
										/>
										<ToggleControl
											label={__('Show Lightbox Icon', "rtcl-elementor-builder")}
											onChange={(showLightboxIcon) => setAttributes({ showLightboxIcon })}
											checked={showLightboxIcon}
										/>
										{/* <ToggleControl
											label={__('Enable Image Zoom')}
											onChange={(enableZoom) => setAttributes({ enableZoom })}
											checked={enableZoom}
										/> */}
										<ToggleControl
											label={__('Show Badge', "rtcl-elementor-builder")}
											onChange={(showBadge) => setAttributes({ showBadge })}
											checked={showBadge}
										/>

										<SelectControl
											label={__("Image Size", "rtcl-elementor-builder")}
											options={imageSize}
											value={galleryImageSize}
											onChange={(value) => {
												setAttributes({ galleryImageSize: value })
											}}
											help={__('Imge Size will work on frontend only', "rtcl-elementor-builder")}
										/>

									</PanelBody>
								</>
							)}
							{tabs.name === 'styles' && (
								<>
									<PanelBody title={__('Carousel Arrow', "rtcl-elementor-builder")} initialOpen={true}>
										<Color
											label={__('Icon Color', "rtcl-elementor-builder")}
											color={arrowColor}
											onChange={(arrowColor) => setAttributes({ arrowColor })}
										/>
										<Color
											label={__('Icon Background', "rtcl-elementor-builder")}
											color={arrowBgColor}
											onChange={(arrowBgColor) => setAttributes({ arrowBgColor })}
										/>
									</PanelBody>

									<PanelBody title={__('Zoom Icon', "rtcl-elementor-builder")} initialOpen={true}>
										<Color
											label={__('Icon Color', "rtcl-elementor-builder")}
											color={zoomIconColor}
											onChange={(zoomIconColor) => setAttributes({ zoomIconColor })}
										/>
										<Color
											label={__('Icon Background', "rtcl-elementor-builder")}
											color={zoomIconBgColor}
											onChange={(zoomIconBgColor) => setAttributes({ zoomIconBgColor })}
										/>
									</PanelBody>
								</>
							)}
							{tabs.name === 'advance' && (
								<>
									<AdvancedControl
										attributes={attributes}
										setAttributes={setAttributes}
									/>
								</>
							)}
						</div>
					)}
				</TabPanel>
			</div>
		</InspectorControls>
	)

}

export default Inspector;