import { __ } from '@wordpress/i18n';
function RenderView(props) {
	const {
		postResults,
		dropCap
	} = props;

	return (
		(Object.keys(postResults).length > 0 && postResults.description) ? (
			<div className={`rtcl rtcl-listing-description el-single-addon ${dropCap ? 'enabled-drop-cap' : ''}`} >
				<p>{postResults.description}</p>
			</div>
		) : (
			<h3>{__('Description does not add to the listing', 'rtcl-elementor-builder')}</h3>
		)
	)
}

export default RenderView; 