import { useEffect } from '@wordpress/element';
import {
	rangeDeviceStyle,
	typographyStyle,
	mainWrapperStyle,
	softMinifyCssStrings,
	dimensionStyle
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		showNew,
		showFeature,
		showPopular,
		showTop,
		showBumpUp,
		badgeAlign,

		badgePadding,
		badgeSpacing,

		badgeTypo,
		badgeBgColor,
		badgeTextColor,

		topBgColor,
		topTextColor,
		featureBgColor,
		featureTextColor,
		newBgColor,
		newTextColor,
		popularBgColor,
		popularTextColor,
		bumpUpBgColor,
		bumpUpTextColor
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});

	const {
		rangeDeviceDesk: badgeAlignStyleDesk,
		rangeDeviceTab: badgeAlignStyleTab,
		rangeDeviceMob: badgeAlignStyleMob
	} = rangeDeviceStyle({
		controlValue: badgeAlign,
		property: 'justify-content',
	});

	const {
		deskDimension: badgePaddingStyleDesk,
		tabDimension: badgePaddingStyleTab,
		mobDimension: badgePaddingStyleMob
	} = dimensionStyle({
		controlValue: badgePadding,
		property: 'padding',
		important: true
	});

	const {
		rangeDeviceDesk: badgeSpacingStyleDesk,
		rangeDeviceTab: badgeSpacingStyleTab,
		rangeDeviceMob: badgeSpacingStyleMob
	} = rangeDeviceStyle({
		controlValue: badgeSpacing,
		property: 'gap',
	});

	const {
		deskTypography: badgeTypoStyleDesk,
		tabTypography: badgeTypoStyleTab,
		mobTypography: badgeTypoStyleMob
	} = typographyStyle({
		controlValue: badgeTypo
	});

	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}.rtcl-block-frontend{
			margin-top: 15px;
			margin-bottom: 15px;
		}
		${!showNew ? `
		.${blockId} .rtcl-badge-new{
			display:none !important;
		}`: ''}

		${showNew ? `
		.${blockId} .rtcl-badge-new{
			${newTextColor ? `color:${newTextColor} !important;` : ''}
			${newBgColor ? `background-color:${newBgColor} !important;` : ''}
		}`: ''}

		${!showFeature ? `
		.${blockId} .rtcl-badge-featured{
			display:none !important;
		}`: ''}

		${showFeature ? `
		.${blockId} .rtcl-badge-featured{
			${featureTextColor ? `color:${featureTextColor} !important;` : ''}
			${featureBgColor ? `background-color:${featureBgColor} !important;` : ''}
		}`: ''}

		${!showTop ? `
		.${blockId} .rtcl-badge-_top{
			display:none !important;
		}`: ''}

		${showTop ? `
		.${blockId} .rtcl-badge-_top{
			${topTextColor ? `color:${topTextColor} !important;` : ''}
			${topBgColor ? `background-color:${topBgColor} !important;` : ''}
		}`: ''}

		${!showBumpUp ? `
		.${blockId} .rtcl-badge-_bump_up{
			display:none !important;
		}`: ''}

		${showBumpUp ? `
		.${blockId} .rtcl-badge-_bump_up{
			${bumpUpTextColor ? `color:${bumpUpTextColor} !important;` : ''}
			${bumpUpBgColor ? `background-color:${bumpUpBgColor} !important;` : ''}
		}`: ''}

		${!showPopular ? `
		.${blockId} .rtcl-badge-popular{
			display:none !important;
		}`: ''}

		${showPopular ? `
		.${blockId} .rtcl-badge-popular{
			${popularTextColor ? `color:${popularTextColor} !important;` : ''}
			${popularBgColor ? `background-color:${popularBgColor} !important;` : ''}
		}`: ''}

		.${blockId} .single-listing-meta-wrap{
			${badgeAlignStyleDesk}
		}

		.${blockId} .rtcl-listing-badge-wrap{
			display:flex;
			${badgeSpacingStyleDesk}
		}

		.${blockId} .rtcl-listing-badge-wrap span.badge{
			${badgeTextColor ? `color:${badgeTextColor};` : ''}
			${badgeBgColor ? `background-color:${badgeBgColor} !important;` : ''}
			${badgeTypoStyleDesk}
			${badgePaddingStyleDesk}
		}
		
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
		.${blockId} .single-listing-meta-wrap{
			${badgeAlignStyleTab}
		}
		.${blockId} .rtcl-listing-badge-wrap{
			${badgeSpacingStyleTab}
		}
		.${blockId} .rtcl-listing-badge-wrap span.badge{
			${badgeTypoStyleTab}
			${badgePaddingStyleTab}
		}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
		.${blockId} .single-listing-meta-wrap{
			${badgeAlignStyleMob}
		}
		.${blockId} .rtcl-listing-badge-wrap{
			${badgeSpacingStyleMob}
		}
		.${blockId} .rtcl-listing-badge-wrap span.badge{
			${badgeTypoStyleMob}
			${badgePaddingStyleMob}
		}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and (max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and (max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;