import { __ } from '@wordpress/i18n';
import { printHtmlStringMarkup } from '../../helpers';

function RenderView(props) {
	const {
		postResults,
		inlineStyle
	} = props;

	return (
		(Object.keys(postResults).length > 0 && postResults?.actions) ? (
			<div className={`rtcl el-single-addon rtin-content-area ${inlineStyle ? 'inline-style' : ''}`} dangerouslySetInnerHTML={printHtmlStringMarkup(postResults?.actions)} />
		) : (
			<h3>{__('Listing actions does not add to the listing', "rtcl-elementor-builder")}</h3>
		)
	)
}

export default RenderView; 