import { InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, ToggleControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import {
	Typography,
	Dimension,
	Color,
	AdvancedControl,
	BorderDimension,
	RangeDevice
} from '../../components';

function Inspector({ attributes, setAttributes }) {
	const {
		resDevice,
		showHeading,
		showHeadingIndicator,

		boxBackground,
		boxPadding,
		boxBorder,
		boxRadius,

		titleSpacing,
		titleTypo,
		titleColor,
		titleIndicatorColor,

		formLabelTypo,
		formLabelColor,
		formFieldBgColor,
		formFieldHeight,
		formFieldBorder,
		formFieldRadius,

		formBtnTypo,
		formBtnTextColor,
		formBtnHoverTextColor,
		formBtnBgColor,
		formBtnHoverBgColor,
		formBtnPadding,
		formBtnBorder,
		formBtnRadius,

		infoTypo,
		infoColor,
		infoSpacing
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	return (
		<InspectorControls key={'controls'}>
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "styles",
							title: "Styles",
							className: "rtrb-tab-btn styles",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>
					{(tabs) => (
						<div className="rtrb-tab-content">
							{tabs.name === 'general' && (
								<>
									<PanelBody title={__('General', 'rtcl-elementor-builder')} initialOpen={true}>

										<ToggleControl
											label={__('Show Heading', "rtcl-elementor-builder")}
											checked={showHeading}
											onChange={(showHeading) => setAttributes({ showHeading })}
										/>
										{showHeading && (
											<ToggleControl
												label={__('Show Heading Indicator', "rtcl-elementor-builder")}
												checked={showHeadingIndicator}
												onChange={(showHeadingIndicator) => setAttributes({ showHeadingIndicator })}
											/>
										)}

									</PanelBody>
								</>
							)}

							{tabs.name === 'styles' && (
								<>
									<PanelBody title={__('Form Box', "rtcl-elementor-builder")} initialOpen={true}>
										<Color
											label={__("Background", "rtcl-elementor-builder")}
											color={boxBackground}
											onChange={(boxBackground) => setAttributes({ boxBackground })}
										/>
										<Dimension
											label={__('Padding', 'rtcl-elementor-builder')}
											type="padding"
											responsive={true}
											value={boxPadding}
											onChange={(boxPadding) => {
												setAttributes({ boxPadding })
											}}
											resRequiredProps={resRequiredProps}
										/>
										<BorderDimension
											value={boxBorder}
											onChange={(value) => {
												setAttributes({ boxBorder: value })
											}}
											resRequiredProps={resRequiredProps}
										/>
										<Dimension
											label={__("Border Radius", "rtcl-elementor-builder")}
											type="borderRadius"
											responsive={true}
											value={boxRadius}
											onChange={(value) => {
												setAttributes({ boxRadius: value })
											}}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>

									{showHeading && (
										<PanelBody title={__('Heading', 'rtcl-elementor-builder')} initialOpen={false}>
											<Typography
												label={__('Typography', 'rtcl-elementor-builder')}
												value={titleTypo}
												onChange={(val) => setAttributes({ titleTypo: val })}
												resRequiredProps={resRequiredProps}
											/>
											<Color
												label={__("Color", "rtcl-elementor-builder")}
												color={titleColor}
												onChange={(titleColor) => setAttributes({ titleColor })}
											/>
											<Dimension
												label={__("Spacing", "rtcl-elementor-builder")}
												type="margin"
												responsive={true}
												value={titleSpacing}
												onChange={(titleSpacing) => {
													setAttributes({ titleSpacing })
												}}
												resRequiredProps={resRequiredProps}
											/>
											{showHeadingIndicator && (
												<Color
													label={__("Indicator Color", "rtcl-elementor-builder")}
													color={titleIndicatorColor}
													onChange={(titleIndicatorColor) => setAttributes({ titleIndicatorColor })}
												/>
											)}
										</PanelBody>
									)}

									<PanelBody title={__('Form Field', 'rtcl-elementor-builder')} initialOpen={false}>

										<Typography
											label={__('Label Typography', "rtcl-elementor-builder")}
											value={formLabelTypo}
											onChange={(formLabelTypo) => setAttributes({ formLabelTypo })}
											resRequiredProps={resRequiredProps}
										/>
										<Color
											label={__("Label Color", "rtcl-elementor-builder")}
											color={formLabelColor}
											onChange={(formLabelColor) => setAttributes({ formLabelColor })}
										/>
										<Color
											label={__("Field Background", "rtcl-elementor-builder")}
											color={formFieldBgColor}
											onChange={(formFieldBgColor) => setAttributes({ formFieldBgColor })}
										/>
										<RangeDevice
											label={__('Field Height', "rtcl-elementor-builder")}
											responsive={true}
											value={formFieldHeight}
											min={1}
											max={200}
											step={1}
											onChange={(val) => setAttributes({ formFieldHeight: val })}
											resRequiredProps={resRequiredProps}
										/>
										<BorderDimension
											value={formFieldBorder}
											onChange={(value) => {
												setAttributes({ formFieldBorder: value })
											}}
											resRequiredProps={resRequiredProps}
										/>
										<Dimension
											label={__("Border Radius", "rtcl-elementor-builder")}
											type="borderRadius"
											responsive={true}
											value={formFieldRadius}
											onChange={(value) => {
												setAttributes({ formFieldRadius: value })
											}}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>

									<PanelBody title={__('Booking Button', 'rtcl-elementor-builder')} initialOpen={false}>
										<Typography
											label={__('Typography', 'rtcl-elementor-builder')}
											value={formBtnTypo}
											onChange={(formBtnTypo) => setAttributes({ formBtnTypo })}
											resRequiredProps={resRequiredProps}
										/>
										<Color
											label={__('Text Color', "rtcl-elementor-builder")}
											color={formBtnTextColor}
											onChange={(formBtnTextColor) => setAttributes({ formBtnTextColor })}
										/>
										<Color
											label={__('Hover Text Color', "rtcl-elementor-builder")}
											color={formBtnHoverTextColor}
											onChange={(formBtnHoverTextColor) => setAttributes({ formBtnHoverTextColor })}
										/>
										<Color
											label={__("Background", "rtcl-elementor-builder")}
											color={formBtnBgColor}
											onChange={(formBtnBgColor) => setAttributes({ formBtnBgColor })}
										/>
										<Color
											label={__("Hover Background", "rtcl-elementor-builder")}
											color={formBtnHoverBgColor}
											onChange={(formBtnHoverBgColor) => setAttributes({ formBtnHoverBgColor })}
										/>
										<Dimension
											label={__('Button Padding', "rtcl-elementor-builder")}
											type="padding"
											responsive={true}
											value={formBtnPadding}
											onChange={(formBtnPadding) => {
												setAttributes({ formBtnPadding })
											}}
											resRequiredProps={resRequiredProps}
										/>
										<BorderDimension
											value={formBtnBorder}
											onChange={(value) => {
												setAttributes({ formBtnBorder: value })
											}}
											resRequiredProps={resRequiredProps}
										/>
										<Dimension
											label={__("Border Radius", "rtcl-elementor-builder")}
											type="borderRadius"
											responsive={true}
											value={formBtnRadius}
											onChange={(value) => {
												setAttributes({ formBtnRadius: value })
											}}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>

									<PanelBody title={__('Booking Info', 'rtcl-elementor-builder')} initialOpen={false}>
										<Typography
											label={__('Typography', 'rtcl-elementor-builder')}
											value={infoTypo}
											onChange={(val) => setAttributes({ infoTypo: val })}
											resRequiredProps={resRequiredProps}
										/>
										<Color
											label={__("Color", "rtcl-elementor-builder")}
											color={infoColor}
											onChange={(infoColor) => setAttributes({ infoColor })}
										/>
										<Dimension
											label={__("Spacing", "rtcl-elementor-builder")}
											type="margin"
											responsive={true}
											value={infoSpacing}
											onChange={(infoSpacing) => {
												setAttributes({ infoSpacing })
											}}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>
								</>
							)}

							{tabs.name === 'advance' && (
								<>
									<AdvancedControl
										attributes={attributes}
										setAttributes={setAttributes}
									/>
								</>
							)}

						</div>
					)}
				</TabPanel>
			</div>
		</InspectorControls>
	)

}

export default Inspector;