import { useEffect } from '@wordpress/element';
import {
	rangeDeviceStyle,
	typographyStyle,
	mainWrapperStyle,
	softMinifyCssStrings,
	dimensionStyle
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		metaAlign,
		metaMargin,
		metaSpacing,
		metaTypo,
		metaIconColor,
		metaTextColor,
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});

	const {
		rangeDeviceDesk: metaAlignStyleDesk,
		rangeDeviceTab: metaAlignStyleTab,
		rangeDeviceMob: metaAlignStyleMob
	} = rangeDeviceStyle({
		controlValue: metaAlign,
		property: 'justify-content',
	});

	const {
		deskDimension: metaMarginStyleDesk,
		tabDimension: metaMarginStyleTab,
		mobDimension: metaMarginStyleMob
	} = dimensionStyle({
		controlValue: metaMargin,
		property: 'padding',
		important: true
	});

	const {
		rangeDeviceDesk: metaSpacingStyleDesk,
		rangeDeviceTab: metaSpacingStyleTab,
		rangeDeviceMob: metaSpacingStyleMob
	} = rangeDeviceStyle({
		controlValue: metaSpacing,
		property: 'gap',
	});

	const {
		deskTypography: metaTypoStyleDesk,
		tabTypography: metaTypoStyleTab,
		mobTypography: metaTypoStyleMob
	} = typographyStyle({
		controlValue: metaTypo
	});

	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}.rtcl-block-frontend{
			margin-top: 15px;
			margin-bottom: 15px;
		}
		.${blockId} .single-listing-meta-wrap{
			${metaAlignStyleDesk}
			${metaMarginStyleDesk}
		}

		.${blockId} .rtcl-listing-meta-data{
			${metaSpacingStyleDesk}
		}
		.${blockId} .rtcl-listing-meta-data .rtcl-icon{
			${metaIconColor ? `color:${metaIconColor} !important;` : ''}
		}

		.${blockId} .rtcl-listing-meta-data li{
			${metaTextColor ? `color:${metaTextColor};` : ''}
			${metaTypoStyleDesk}
		}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
		.${blockId} .single-listing-meta-wrap{
			${metaAlignStyleTab}
			${metaMarginStyleTab}
		}

		.${blockId} .rtcl-listing-meta-data{
			${metaSpacingStyleTab}
		}
		.${blockId} .rtcl-listing-meta-data li{
			${metaTypoStyleTab}
		}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
		.${blockId} .single-listing-meta-wrap{
			${metaAlignStyleMob}
		}
		.${blockId} .rtcl-listing-meta-data{
			${metaSpacingStyleMob}
			${metaMarginStyleMob}
		}
		.${blockId} .rtcl-listing-meta-data li{
			${metaTypoStyleMob}
		}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and (max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and (max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;