import { printHtmlStringMarkup } from '../../helpers';
import { __ } from '@wordpress/i18n';
function RenderView(props) {
	const {
		storeResults,
	} = props;
	const openingHour = storeResults.opening_hour.trim();
	return (
		(Object.keys(storeResults).length > 0 && openingHour) ? (
			<div class="store-hours-list-wrap store-hours-list" dangerouslySetInnerHTML={printHtmlStringMarkup(openingHour)} />
		) : (<h3>{__('Store Opening Hour does not insert', "rtcl-elementor-builder")}</h3>)
	)
}

export default RenderView; 