import { __ } from '@wordpress/i18n';
import { printHtmlStringMarkup } from '../../helpers'
function RenderView(props) {
	const {
		postResults,
		showType,
		showDate,
		showUser,
		showCategory,
		showLocation,
		showViews

	} = props;

	return (
		Object.keys(postResults).length > 0 ? (

			<div className="rtcl el-single-addon single-listing-meta-wrap">
				<ul className="rtcl-listing-meta-data">

					{showType && (
						<li className="rtin-type">
							<i className="rtcl-icon rtcl-icon-tags" aria-hidden="true"></i>
							{postResults?.ad_type}
						</li>
					)}

					{showDate && (
						<li className="date">
							<i className="rtcl-icon rtcl-icon-clock"></i>
							{postResults?.time}
						</li>
					)}

					{showUser && (
						<li className="author">
							<i className="rtcl-icon rtcl-icon-user" aria-hidden="true"></i>
							{postResults?.author_name}
						</li>
					)}

					{showCategory && (
						<li className="rt-categories">
							<i className="rtcl-icon rtcl-icon-tags"></i>
							{postResults?.category}
						</li>
					)}

					{showLocation && (
						<li className="rt-location">
							<i className="rtcl-icon rtcl-icon-location"></i>
							<span dangerouslySetInnerHTML={printHtmlStringMarkup(postResults?.locations)} />
						</li>
					)}

					{showViews && (
						<li className="rt-views">
							<i className="rtcl-icon rtcl-icon-eye"></i>
							{postResults?.view_counts} views
						</li>
					)}

				</ul>
			</div>
		) : (
			<h3>{__('Meta does not add to the listing')}</h3>
		)
	)
}

export default RenderView; 