import { registerBlockType } from "@wordpress/blocks";
import { __ } from "@wordpress/i18n";
import { Icons } from "../../components";

import BlockMetaData from '../../../app/Blocks/listing-page-header/block.json';
import Edit from "./edit.js";
import Example from "./example.js";

if (rtclBlockParams.builderSinglePage || rtclBlockParams.builderArchivePage) {
	registerBlockType(BlockMetaData, {
		category: rtclBlockParams.builderSinglePage ? 'rtcl-single-block' : 'rtcl-archive-block',
		icon: {
			src: Icons.rtclLogo
		},
		edit: Edit,
		save: () => null,
		example: Example
	});
}