import { InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, ToggleControl, SelectControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import {
	Alignment,
	Typography,
	Dimension,
	Color,
	AdvancedControl
} from '../../components';

import { TAG_NAMES } from '../../globals/Constants'

function Inspector({ attributes, setAttributes }) {
	const {
		resDevice,
		showPageTitle,
		titleTag,
		titleAlign,

		showBreadcrumb,
		style,
		breadcrumbAlign,

		titleSpacing,
		titleTypo,
		titleColor,

		breadcrumbTypo,
		breadcrumbColor,
		breadcrumbSpacing
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	return (
		<InspectorControls key={'controls'}>
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "styles",
							title: "Styles",
							className: "rtrb-tab-btn styles",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>
					{(tabs) => (
						<div className="rtrb-tab-content">
							{tabs.name === 'general' && (
								<>
									<PanelBody title={__('General', 'rtcl-elementor-builder')} initialOpen={true}>


										<ToggleControl
											label={__('Show Page Title', "rtcl-elementor-builder")}
											checked={showPageTitle}
											onChange={(showPageTitle) => setAttributes({ showPageTitle })}
										/>

										<ToggleControl
											label={__('Show Breadcrumb', "rtcl-elementor-builder")}
											checked={showBreadcrumb}
											onChange={(showBreadcrumb) => setAttributes({ showBreadcrumb })}
										/>

										{showPageTitle && (
											<>
												<SelectControl
													label={__('Title HTML Tag', "rtcl-elementor-builder")}
													options={TAG_NAMES}
													value={titleTag}
													onChange={(titleTag) => setAttributes({ titleTag })}
												/>

												<Alignment
													label={__("Title Alignment", "rtcl-elementor-builder")}
													value={titleAlign}
													responsive={true}
													options={['left', 'center', 'right']}
													onChange={val => setAttributes({ titleAlign: val })}
													resRequiredProps={resRequiredProps}
												/>
											</>
										)}

										{showBreadcrumb && (
											<>
												<SelectControl
													label={__('Breadcrumb Position', "rtcl-elementor-builder")}
													options={[{ label: 'Top', value: 'style-2' }, { label: 'Bottom', value: 'style-1' }, { label: 'Right', value: 'style-3' }]}
													value={style}
													onChange={(style) => setAttributes({ style })}
												/>

												<Alignment
													label={__("Breadcrumb Alignment", "rtcl-elementor-builder")}
													value={breadcrumbAlign}
													responsive={true}
													options={['left', 'center', 'right']}
													onChange={val => setAttributes({ breadcrumbAlign: val })}
													resRequiredProps={resRequiredProps}
												/>
											</>
										)}

									</PanelBody>
								</>
							)}

							{tabs.name === 'styles' && (
								<>
									{(showPageTitle) && (
										<PanelBody title={__("Title", "rtcl-elementor-builder")} initialOpen={true}>

											<Typography
												label={__('Typography', 'rtcl-elementor-builder')}
												value={titleTypo}
												onChange={(val) => setAttributes({ titleTypo: val })}
												resRequiredProps={resRequiredProps}
											/>

											<Color
												label={__("Color", "rtcl-elementor-builder")}
												color={titleColor}
												onChange={(titleColor) => setAttributes({ titleColor })}
											/>

											<Dimension
												label={__("Spacing", "rtcl-elementor-builder")}
												type="margin"
												responsive={true}
												value={titleSpacing}
												onChange={(titleSpacing) => {
													setAttributes({ titleSpacing })
												}}
												resRequiredProps={resRequiredProps}
											/>
										</PanelBody>
									)}

									{(showBreadcrumb) && (
										<PanelBody title={__('Breadcrumb', "rtcl-elementor-builder")}>
											<Dimension
												label={__("Spacing", "rtcl-elementor-builder")}
												type="margin"
												responsive={true}
												value={breadcrumbSpacing}
												onChange={(breadcrumbSpacing) => {
													setAttributes({ breadcrumbSpacing })
												}}
												resRequiredProps={resRequiredProps}
											/>

											<Typography
												label={__('Typography', 'rtcl-elementor-builder')}
												value={breadcrumbTypo}
												onChange={(val) => setAttributes({ breadcrumbTypo: val })}
												resRequiredProps={resRequiredProps}
											/>

											<Color
												label={__("Color", "rtcl-elementor-builder")}
												color={breadcrumbColor}
												onChange={(breadcrumbColor) => setAttributes({ breadcrumbColor })}
											/>
										</PanelBody>
									)}
								</>
							)}

							{tabs.name === 'advance' && (
								<>
									<AdvancedControl
										attributes={attributes}
										setAttributes={setAttributes}
									/>
								</>
							)}

						</div>
					)}
				</TabPanel>
			</div>
		</InspectorControls>
	)

}

export default Inspector;