import { useEffect } from '@wordpress/element';
import {
	rangeDeviceStyle,
	typographyStyle,
	mainWrapperStyle,
	softMinifyCssStrings,
	dimensionStyle
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		descAlignment,
		descSpacing,
		descTypo,
		descColor,
		dropCap,
		dropCapTypo,
		dropCapColor
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});

	const {
		rangeDeviceDesk: descAlignStyleDesk,
		rangeDeviceTab: descAlignStyleTab,
		rangeDeviceMob: descAlignStyleMob
	} = rangeDeviceStyle({
		controlValue: descAlignment,
		property: 'text-align',
	});

	const {
		deskDimension: descSpacingStyleDesk,
		tabDimension: descSpacingStyleTab,
		mobDimension: descSpacingStyleMob
	} = dimensionStyle({
		controlValue: descSpacing,
		property: 'margin',
		important: true
	});

	const {
		deskTypography: descTypoStyleDesk,
		tabTypography: descTypoStyleTab,
		mobTypography: descTypoStyleMob
	} = typographyStyle({
		controlValue: descTypo
	});

	const {
		deskTypography: dropCapTypoStyleDesk,
		tabTypography: dropCapTypoStyleTab,
		mobTypography: dropCapTypoStyleMob
	} = typographyStyle({
		controlValue: dropCapTypo
	});


	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}.rtcl-block-frontend{
			margin-top: 15px;
			margin-bottom: 15px;
		}
		.${blockId}{
			${descAlignStyleDesk}
		}
		.${blockId} p{
			margin:0;
			${descColor ? `color:${descColor};` : ''}
			${descSpacingStyleDesk}
			${descTypoStyleDesk}
		}
		.${blockId} .enabled-drop-cap p:first-child:first-letter{
			${dropCapColor ? `color:${dropCapColor};` : ''}
			${dropCapTypoStyleDesk}
		}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
	  .${blockId}{
			${descAlignStyleTab}
		}
		.${blockId} p{
			${descSpacingStyleTab}
			${descTypoStyleTab}
		}
		.${blockId} .enabled-drop-cap p:first-child:first-letter{
			${dropCapTypoStyleTab}
		}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
		.${blockId}{
			${descAlignStyleMob}
		}
		.${blockId} p{
			${descSpacingStyleMob}
			${descTypoStyleMob}
		}
		.${blockId} .enabled-drop-cap p:first-child:first-letter{
			${dropCapTypoStyleMob}
		}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and (max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and (max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;