import { __ } from '@wordpress/i18n';
import { Spinner } from "@wordpress/components";
import Inspector from './inspector';
import { useBlockProps } from '@wordpress/block-editor';
import RenderView from './render-view';
import { useEffect, useState } from '@wordpress/element';
import styles from './styles';
import { uniqueBlockId, blockEditorWrapperClass } from '../../helpers';
import axios from 'axios';
import Qs from 'qs';
import { Icons } from '../../components';
import './editor.scss';

function Edit({ isSelected, attributes, setAttributes, clientId }) {
	const {
		blockId,
		preview,
		showFilterTitle,
		filterTitle,
		showCategory,
		showLocation,
		showRadiusSearch,
		showAdType,
		showPrice,
		hideEmptyCatLocation,
		showCount,
		showAjaxLoad,
		showCatLocationLink,
		showCatImageIcon,
		showRating,
		showCustomField
	} = attributes;

	const [lastPostResult, setLastPostResult] = useState('');
	const [dataSuccess, setDataSuccess] = useState(true);

	const apiFetchData = async () => {
		let ajaxData = {
			action: 'rtcl_block_listing_filter_ajax',
			rtcl_block_nonce: rtclBlockParams.rtcl_block_nonce,
			attributes
		}
		try {
			const result = await axios.post(rtclBlockParams.ajaxurl, Qs.stringify(ajaxData))
				.then((response) => response.data);
			if (result.success) {
				setLastPostResult(result.data);
				setDataSuccess(result.success);
			} else {
				setLastPostResult({});
				setDataSuccess(result.success);
			}
		} catch (error) {
			console.error(error);
		}
	};

	useEffect(() => {
		const BLOCK_PREFIX = 'rtcl-filter-';
		uniqueBlockId(BLOCK_PREFIX, clientId, attributes, setAttributes);
		apiFetchData();
	}, [
		showFilterTitle,
		showCategory,
		showLocation,
		showRadiusSearch,
		showAdType,
		showPrice,
		hideEmptyCatLocation,
		showCount,
		showAjaxLoad,
		showCatLocationLink,
		showCatImageIcon,
		showRating,
		showCustomField,
		filterTitle
	]);

	const editorWrapClass = blockEditorWrapperClass(useBlockProps, attributes, 'elementor-widget rtcl-block-widget-filter');

	//generate styles
	const allStyle = styles({
		attributes,
		setAttributes
	})

	const hasPosts = lastPostResult.length;
	if (!hasPosts) {
		return [
			isSelected && (
				<Inspector
					attributes={attributes}
					setAttributes={setAttributes}
				/>
			),
			dataSuccess ? (<div className="rtrb-spinner"><Spinner /></div>) : (
				<p>{__('No filter field found.', 'rtcl-elementor-builder')}</p>
			)
		];
	}

	//set preview
	if (preview) {
		return Icons.title_preview;
	}

	return [
		isSelected && (
			<Inspector
				attributes={attributes}
				setAttributes={setAttributes}
			/>
		),

		<style>{`${allStyle}`}</style>,

		<div {...editorWrapClass}>
			<RenderView {...attributes} setAttributes={setAttributes} postResults={lastPostResult} />
		</div>
	]
}

export default Edit;