import { __ } from '@wordpress/i18n';
import { printHtmlStringMarkup } from '../../helpers';

function RenderView(props) {
	const {
		clientId,
		postResults,
	} = props;

	return (
		(Object.keys(postResults).length > 0 && postResults.review) ? (
			<div className="el-single-addon rtcl-Reviews rtcl displayed-comment-list displayed-comment-form" dangerouslySetInnerHTML={printHtmlStringMarkup(postResults.review)} />
		) : (
			<h3>{__('Review does not add to the listing')}</h3>
		)
	)
}

export default RenderView; 