import { useEffect } from '@wordpress/element';
import {
	mainWrapperStyle,
	softMinifyCssStrings,
	rangeDeviceStyle,
	typographyStyle,
	dimensionStyle,
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		showView,
		showFavourit,
		showAbuse,
		showSocialShare,
		actionTypo,
		actionColor,
		actionAlign,
		actionBorder,
		actionPadding,
		socialColor,
		socialBgColor,
		socialMargin,
		socialIconDimension,
		socialIconSize,
		socialIconGap,
		socialHoverColor,
		socialHoverBgColor,
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});

	const {
		deskTypography: actionTypoStyleDesk,
		tabTypography: actionTypoStyleTab,
		mobTypography: actionTypoStyleMob
	} = typographyStyle({
		controlValue: actionTypo
	});

	const {
		rangeDeviceDesk: actionAlignStyleDesk,
		rangeDeviceTab: actionAlignStyleTab,
		rangeDeviceMob: actionAlignStyleMob
	} = rangeDeviceStyle({
		controlValue: actionAlign,
		property: 'text-align',
	});

	const {
		rangeDeviceDesk: actionAlign2StyleDesk,
		rangeDeviceTab: actionAlign2StyleTab,
		rangeDeviceMob: actionAlign2StyleMob
	} = rangeDeviceStyle({
		controlValue: actionAlign,
		property: 'justify-content',
	});

	const {
		deskDimension: actionBorderStyleDesk,
		tabDimension: actionBorderStyleTab,
		mobDimension: actionBorderStyleMob
	} = dimensionStyle({
		controlValue: actionBorder,
		property: 'border-width',
		important: true
	});

	const {
		deskDimension: actionPaddingStyleDesk,
		tabDimension: actionPaddingStyleTab,
		mobDimension: actionPaddingStyleMob
	} = dimensionStyle({
		controlValue: actionPadding,
		property: 'padding',
		important: true
	});

	const {
		deskDimension: socialMarginStyleDesk,
		tabDimension: socialMarginStyleTab,
		mobDimension: socialMarginStyleMob
	} = dimensionStyle({
		controlValue: socialMargin,
		property: 'margin',
		important: true
	});

	const {
		rangeDeviceDesk: socialIconWidthStyleDesk,
		rangeDeviceTab: socialIconWidthStyleTab,
		rangeDeviceMob: socialIconWidthStyleMob
	} = rangeDeviceStyle({
		controlValue: socialIconDimension,
		property: 'width',
	});

	const {
		rangeDeviceDesk: socialIconHeightStyleDesk,
		rangeDeviceTab: socialIconHeightStyleTab,
		rangeDeviceMob: socialIconHeightStyleMob
	} = rangeDeviceStyle({
		controlValue: socialIconDimension,
		property: 'height',
	});

	const {
		rangeDeviceDesk: socialIconGapStyleDesk,
		rangeDeviceTab: socialIconGapStyleTab,
		rangeDeviceMob: socialIconGapStyleMob
	} = rangeDeviceStyle({
		controlValue: socialIconGap,
		property: 'gap',
	});

	const deskAllStyles = softMinifyCssStrings(`
		.${blockId}.rtcl-block-frontend{
			margin-top: 15px;
			margin-bottom: 15px;
		}
		.${blockId} .rtcl-single-listing-action li {
			padding: 10px 0;
		}
		.${blockId} .list-group-item{
			${actionPaddingStyleDesk}
		}
		.${blockId} .list-group-item:not(.rtcl-sidebar-social) {
			${actionTypoStyleDesk}
			${actionBorderStyleDesk}
		}
		.${blockId} .list-group-item:not(.rtcl-sidebar-social) a,
		.${blockId} .list-group-item:nth-child(1){
			${actionColor ? `color:${actionColor};` : ''}
		}
		.${blockId} .rtcl-single-listing-action,
		.${blockId} .rtcl-sidebar-social{
			display: inline-flex;
			width: 100%;
			${actionAlignStyleDesk}
			${actionAlign2StyleDesk}
		}
		.${blockId} .rtcl-sidebar-social .rtcl-icon{
			${socialMarginStyleDesk}
			${socialIconWidthStyleDesk}
			${socialIconHeightStyleDesk}
			${socialColor ? `color:${socialColor};` : ''}
			${socialBgColor ? `background-color:${socialBgColor} !important;` : ''}
			${socialIconSize ? `font-size:${socialIconSize}px;` : ''}
			transition: all .25s;
		}
		.${blockId} .rtcl-sidebar-social{
			${socialIconGapStyleDesk}
		}
		.${blockId} .rtcl-sidebar-social a:hover .rtcl-icon{
			${socialHoverColor ? `color:${socialHoverColor};` : ''}
			${socialHoverBgColor ? `background-color:${socialHoverBgColor} !important;` : ''}
		}
		${!showView ?
			`.${blockId} .rtcl-single-listing-action li:nth-child(1){display:none;}`
			: ''}
		${!showFavourit ?
			`.${blockId} .rtcl-single-listing-action li:nth-child(2){display:none;}`
			: ''}
		${!showAbuse ?
			`.${blockId} .rtcl-single-listing-action li:nth-child(3){display:none;}`
			: ''}
		${!showSocialShare ?
			`.${blockId} .rtcl-single-listing-action li:nth-child(4){display:none;}`
			: ''}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
		.${blockId} .list-group-item{
			${actionPaddingStyleTab}
		}
		.${blockId} .list-group-item:not(.rtcl-sidebar-social) {
			${actionTypoStyleTab}
			${actionBorderStyleTab}
		}
		.${blockId} .rtcl-single-listing-action,
		.${blockId} .rtcl-sidebar-social{
			${actionAlignStyleTab}
			${actionAlign2StyleTab}
		}
		.${blockId} .rtcl-sidebar-social .rtcl-icon{
			${socialMarginStyleTab}
			${socialIconWidthStyleTab}
			${socialIconHeightStyleTab}
		}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
		.${blockId} .list-group-item{
			${actionPaddingStyleMob}
		}
		.${blockId} .list-group-item:not(.rtcl-sidebar-social) {
			${actionTypoStyleMob}
			${actionBorderStyleMob}
		}
		.${blockId} .rtcl-single-listing-action,
		.${blockId} .rtcl-sidebar-social{
			${actionAlignStyleMob}
			${actionAlign2StyleMob}
		}
		.${blockId} .rtcl-sidebar-social .rtcl-icon{
			${socialMarginStyleMob}
			${socialIconWidthStyleMob}
			${socialIconHeightStyleMob}
		}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and(max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and(max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;