import { printHtmlStringMarkup } from '../../helpers';
import { __ } from '@wordpress/i18n';
function RenderView(props) {
	const {
		storeResults,
		titleTag
	} = props;

	return (
		(Object.keys(storeResults).length > 0 && storeResults?.contact_info) ? (
			<div class="store-information edit-mode">
				<div class="store-info" dangerouslySetInnerHTML={printHtmlStringMarkup(storeResults?.contact_info)} />
			</div>
		) : (<h3>{__('Store Contact Info does not insert', 'rtcl-elementor-builder')}</h3>)
	)
}

export default RenderView; 