import { InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, ToggleControl, SelectControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import {
	Alignment,
	Typography,
	Dimension,
	Color,
	AdvancedControl
} from '../../components';

function Inspector({ attributes, setAttributes }) {
	const {
		resDevice,
		style,
		showPUnit,
		showPType,
		priceAlignment,
		priceSpacing,
		priceTypo,
		priceColor,

		priceLabelTypo,
		priceLabelColor
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	const styleOptions = rtclBlockParams?.rtclPro
		? [
			{ label: 'Style-1', value: 'style-1' },
			{ label: 'Style-2', value: 'style-2' },
		]
		: [{ label: 'Style-1', value: 'style-1' }];

	return (
		<InspectorControls key={'controls'}>
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "styles",
							title: "Styles",
							className: "rtrb-tab-btn styles",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>
					{(tabs) => (
						<div className="rtrb-tab-content">
							{tabs.name === 'general' && (
								<>
									<PanelBody title={__('General', 'rtcl-elementor-builder')} initialOpen={true}>

										<SelectControl
											options={styleOptions}
											value={style}
											onChange={(style) => setAttributes({ style })}
										/>

										<ToggleControl
											label={__('Show Price Unit', "rtcl-elementor-builder")}
											checked={showPUnit}
											onChange={(showPUnit) => setAttributes({ showPUnit })}
										/>

										<ToggleControl
											label={__('Show Price Type', "rtcl-elementor-builder")}
											checked={showPType}
											onChange={(showPType) => setAttributes({ showPType })}
										/>

										<Alignment
											label={__("Text Alignment", "rtcl-elementor-builder")}
											value={priceAlignment}
											responsive={true}
											options={['left', 'center', 'right']}
											onChange={val => setAttributes({ priceAlignment: val })}
											resRequiredProps={resRequiredProps}
										/>

									</PanelBody>
								</>
							)}

							{tabs.name === 'styles' && (
								<>
									<PanelBody title={__('Price', "rtcl-elementor-builder")} initialOpen={true}>

										<Typography
											label={__('Typography', 'rtcl-elementor-builder')}
											value={priceTypo}
											onChange={(val) => setAttributes({ priceTypo: val })}
											resRequiredProps={resRequiredProps}
										/>

										<Color
											label={__("Color", "rtcl-elementor-builder")}
											color={priceColor}
											onChange={(priceColor) => setAttributes({ priceColor })}
										/>

										<Dimension
											label={__("Spacing", "rtcl-elementor-builder")}
											type="margin"
											responsive={true}
											value={priceSpacing}
											onChange={(priceSpacing) => {
												setAttributes({ priceSpacing })
											}}
											resRequiredProps={resRequiredProps}
										/>

									</PanelBody>

									{(showPType || showPUnit) && (
										<PanelBody title={__('Price Label', "rtcl-elementor-builder")}>
											<Typography
												label={__('Typography', 'rtcl-elementor-builder')}
												value={priceLabelTypo}
												onChange={(val) => setAttributes({ priceLabelTypo: val })}
												resRequiredProps={resRequiredProps}
											/>

											<Color
												label={__("Color", "rtcl-elementor-builder")}
												color={priceLabelColor}
												onChange={(priceLabelColor) => setAttributes({ priceLabelColor })}
											/>
										</PanelBody>
									)}
								</>
							)}

							{tabs.name === 'advance' && (
								<>
									<AdvancedControl
										attributes={attributes}
										setAttributes={setAttributes}
									/>
								</>
							)}

						</div>
					)}
				</TabPanel>
			</div>
		</InspectorControls>
	)

}

export default Inspector;