import { __ } from '@wordpress/i18n';
import {
	Button,
	ButtonGroup,
	PanelBody,
	ToggleControl
} from "@wordpress/components";

import {
	NORMAL_HOVER
} from "../globals/Constants";

import {
	Dimension,
	RangeDevice,
	Background,
	BorderDimension,
	BoxShadow,
	Range,
	CustomCss
} from '../components';

function AdvancedControl({ attributes, setAttributes, type = '' }) {
	const {
		resDevice,
		mainWrapMarginTop,
		mainWrapMarginBottom,
		mainWrapMargin,
		mainWrapPadding,
		mainWrapBGType,
		mainWrapBG,
		mainWrapHoverBG,
		mainWrapHoverBGTransition,
		mainWrapBGOverlayEnable,
		mainWrapBGOverlay,
		mainWrapBorderType,
		mainWrapBorder,
		mainWrapHoverBorder,
		mainWrapRadius,
		mainWrapHoverRadius,
		mainWrapShadowType,
		mainWrapShadow,
		mainWrapHoverShadow,
		mainWrapZindex,
		mainWrapShowHide
	} = attributes

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	return (
		<>

			<PanelBody title={__('Warpper Margin & Padding', 'rtcl-elementor-builder')} initialOpen={true}>

				{type === 'row' && (
					<>
						<RangeDevice
							label={__('Margin Top', 'rtcl-elementor-builder')}
							responsive={true}
							value={mainWrapMarginTop}
							min={-500}
							max={500}
							step={1}
							onChange={(val) => setAttributes({ mainWrapMarginTop: val })}
							resRequiredProps={resRequiredProps}
						/>

						<RangeDevice
							label={__('Margin Bottom', 'rtcl-elementor-builder')}
							responsive={true}
							value={mainWrapMarginBottom}
							min={-500}
							max={500}
							step={1}
							onChange={(val) => setAttributes({ mainWrapMarginBottom: val })}
							resRequiredProps={resRequiredProps}
						/>
					</>
				)}
				{type !== 'row' && (
					<Dimension
						onChange={(value) => {
							setAttributes({ mainWrapMargin: value })
						}}
						label={__("Margin", "rtcl-elementor-builder")}
						type="margin"
						responsive={true}
						value={mainWrapMargin}
						resRequiredProps={resRequiredProps}
					/>
				)}
				<Dimension
					label={__('Padding', 'rtcl-elementor-builder')}
					type="padding"
					responsive={true}
					value={mainWrapPadding}
					onChange={(value) => {
						setAttributes({ mainWrapPadding: value })
					}}
					resRequiredProps={resRequiredProps}
				/>

			</PanelBody>

			<PanelBody title={__('Background', 'rtcl-elementor-builder')} initialOpen={false}>

				<ButtonGroup className="rtrb-btn-group rtrb-btn-group-state">
					{NORMAL_HOVER.map((item, key) => (
						<Button
							key={key}
							variant={mainWrapBGType === item.value ? 'primary' : 'secondary'}
							onClick={() => setAttributes({ mainWrapBGType: item.value })}
						>
							{item.label}
						</Button>
					))}
				</ButtonGroup>

				{mainWrapBGType === "normal" && (
					<Background
						label={__("Background", "rtcl-elementor-builder")}
						value={mainWrapBG}
						onChange={val => setAttributes({ mainWrapBG: val })}
						resRequiredProps={resRequiredProps}
					/>

				)}
				{mainWrapBGType === "hover" && (
					<>
						<Background
							label={__("Background", "rtcl-elementor-builder")}
							value={mainWrapHoverBG}
							onChange={val => setAttributes({ mainWrapHoverBG: val })}
							resRequiredProps={resRequiredProps}
						/>
						<Range
							label={__("Background Transition", "rtcl-elementor-builder")}
							reset={true}
							value={mainWrapHoverBGTransition}
							onChange={(val) => setAttributes({ mainWrapHoverBGTransition: val })}
							min={0}
							max={5}
							step={0.1}
						/>
					</>
				)}

				<ToggleControl
					label={__("Enable Overlay", "rtcl-elementor-builder")}
					checked={mainWrapBGOverlayEnable}
					onChange={(mainWrapBGOverlayEnable) =>
						setAttributes({ mainWrapBGOverlayEnable })
					}
				/>

				{mainWrapBGOverlayEnable && (
					<>
						<Background
							label={__("Background", "rtcl-elementor-builder")}
							value={mainWrapBGOverlay}
							onChange={val => setAttributes({ mainWrapBGOverlay: val })}
							resRequiredProps={resRequiredProps}
						/>
					</>
				)}

			</PanelBody>

			<PanelBody title={__('Border', 'rtcl-elementor-builder')} initialOpen={false}>
				<ButtonGroup className="rtrb-btn-group rtrb-btn-group-state">
					{NORMAL_HOVER.map((item, key) => (
						<Button
							key={key}
							variant={mainWrapBorderType === item.value ? 'primary' : 'secondary'}
							onClick={() => setAttributes({ mainWrapBorderType: item.value })}
						>
							{item.label}
						</Button>
					))}
				</ButtonGroup>

				{mainWrapBorderType === "normal" && (
					<>
						<BorderDimension
							value={mainWrapBorder}
							onChange={(value) => {
								setAttributes({ mainWrapBorder: value })
							}}
							resRequiredProps={resRequiredProps}
						/>
						<Dimension
							label={__("Border Radius", "rtcl-elementor-builder")}
							type="borderRadius"
							responsive={true}
							value={mainWrapRadius}
							onChange={(value) => {
								setAttributes({ mainWrapRadius: value })
							}}
							resRequiredProps={resRequiredProps}
						/>
					</>

				)}

				{mainWrapBorderType === "hover" && (
					<>
						<BorderDimension
							value={mainWrapHoverBorder}
							onChange={(value) => {
								setAttributes({ mainWrapHoverBorder: value })
							}}
							resRequiredProps={resRequiredProps}
						/>
						<Dimension
							label={__("Border Radius", "rtcl-elementor-builder")}
							type="borderRadius"
							responsive={true}
							value={mainWrapHoverRadius}
							onChange={(value) => {
								setAttributes({ mainWrapHoverRadius: value })
							}}
							resRequiredProps={resRequiredProps}
						/>
					</>
				)}

			</PanelBody>

			<PanelBody title={__('Shadow', 'rtcl-elementor-builder')} initialOpen={false}>
				<ButtonGroup className="rtrb-btn-group rtrb-btn-group-state">
					{NORMAL_HOVER.map((item) => (
						<Button
							isLarge
							variant={mainWrapShadowType === item.value ? 'primary' : 'secondary'}
							onClick={() => setAttributes({
								mainWrapShadowType: item.value
							})}
						>
							{item.label}
						</Button>
					))}
				</ButtonGroup>

				{mainWrapShadowType === "normal" && (
					<BoxShadow
						value={mainWrapShadow}
						onChange={val => setAttributes({ mainWrapShadow: val })}
					/>
				)}

				{mainWrapShadowType === "hover" && (
					<BoxShadow
						value={mainWrapHoverShadow}
						onChange={val => setAttributes({ mainWrapHoverShadow: val })}
					/>
				)}
			</PanelBody>

			<PanelBody title={__('Show/Hide Condition', 'rtcl-elementor-builder')} initialOpen={false}>
				<ToggleControl
					label={__("Hide on desktop", "rtcl-elementor-builder")}
					className="rtrb-control-field"
					checked={mainWrapShowHide['lg']}
					onChange={(val) =>
						setAttributes({ mainWrapShowHide: { ...mainWrapShowHide, lg: val } })
					}
				/>
				<ToggleControl
					label={__("Hide on Tablet", "rtcl-elementor-builder")}
					className="rtrb-control-field"
					checked={mainWrapShowHide['md']}
					onChange={(val) =>
						setAttributes({ mainWrapShowHide: { ...mainWrapShowHide, md: val } })
					}
				/>
				<ToggleControl
					label={__("Hide on Mobile", "rtcl-elementor-builder")}
					className="rtrb-control-field"
					checked={mainWrapShowHide['sm']}
					onChange={(val) =>
						setAttributes({ mainWrapShowHide: { ...mainWrapShowHide, sm: val } })
					}
				/>
			</PanelBody>

			<PanelBody title={__('Z-Index', 'rtcl-elementor-builder')} initialOpen={false}>
				<Range
					label={__("Z-Index", "rtcl-elementor-builder")}
					reset={true}
					value={mainWrapZindex}
					onChange={(val) => setAttributes({ mainWrapZindex: val })}
					min={-10000}
					max={999999}
					step={1}
				/>
			</PanelBody>

			<PanelBody title={__('Coustom CSS', 'rtcl-elementor-builder')} initialOpen={false}>
				<CustomCss
					attributes={attributes}
					setAttributes={setAttributes}
				/>
			</PanelBody>

		</>
	)
}

export default AdvancedControl;