import { __ } from '@wordpress/i18n';
import classnames from 'classnames';
import { useEffect, useRef } from '@wordpress/element';
import { dispatch } from '@wordpress/data';
function StyleGrid3(props) {

	const {
		clientId,
		style,
		layout,
		listingPerPage,
		listingFilter,
		listingImageSize,
		contentVisibility,
		content_limit,
		data,
		sliderOptions,
		sliderSettings,
		slidesItem
	} = props;

	const gridContainerClassNames = classnames([
		'rtcl-listings rtcl-listings-slider-container swiper rtcl-carousel-slider',
		`rtcl-${layout}-view`,
		`rtcl-style-${style}-view`
	]);

	const sliderReff = useRef(null);
	if (sliderReff.current) {
		sliderReff.current.addEventListener("click", function () {
			dispatch("core/block-editor").selectBlock(clientId);
			dispatch("core/edit-post").openGeneralSidebar("edit-post/block");
		});
	}
	const rtcl_block_slider = function (sliderE, sliderOptions) {
		let options = Object.assign({}, sliderOptions);
		if (sliderE.swiper) {
			sliderE.swiper.destroy();
		}
		new Swiper(sliderE, options);
	};
	useEffect(() => {
		rtcl_block_slider(sliderReff.current, sliderSettings);
	}, [sliderOptions, slidesItem, contentVisibility, listingPerPage, listingFilter, listingImageSize]);

	return [
		data.length ? (
			<div className="rtcl-listings-wrapper rtcl-block-related-listing">
				<div ref={sliderReff} className={gridContainerClassNames} >

					<div className="swiper-wrapper">
						{data.map((listing, index) => (
							<div className={[Object.values(listing.classes).join(" ") + " swiper-slide"]} key={index}>
								{(contentVisibility["thumbnail"] && listing.thumbnail) ? (
									<div className="listing-thumb">
										<a href={listing.post_link} className="rtcl-media" dangerouslySetInnerHTML={{ __html: listing.thumbnail }}></a>
										{contentVisibility["sold"] && listing.sold ? <span dangerouslySetInnerHTML={{ __html: listing.sold }}></span> : ''}
									</div>
								) : ''}

								<div className="item-content">

									{(contentVisibility["badge"] && listing.badges) ? (
										<div className="rtcl-listing-badge-wrap" dangerouslySetInnerHTML={{ __html: listing.badges }}></div>
									) : ''}

									{(contentVisibility["category"] && listing.categories) ? (
										<div className='category' dangerouslySetInnerHTML={{ __html: listing.categories }}></div>
									) : ''}

									{(contentVisibility["title"] && listing.title) ? (
										<h3 className="listing-title"><a href={listing.post_link}>{listing.title}</a></h3>
									) : ''}

									<ul className="rtcl-listing-meta-data">
										{(contentVisibility["listing_type"] && listing.listing_type) ? (
											<li className="listing-type"><i className="rtcl-icon rtcl-icon-tags"></i>{listing.listing_type}</li>
										) : ''}
										{(contentVisibility["date"] && listing.time) ? (
											<li className="updated"><i className="rtcl-icon rtcl-icon-clock"></i>{listing.time}</li>
										) : ''}
										{(contentVisibility["author"] && listing.author) ? (
											<li className="author"><i className="rtcl-icon rtcl-icon-user"></i>{listing.author}</li>
										) : ''}
										{(contentVisibility["location"] && listing.locations) ? (
											<li className="rt-location"><i className="rtcl-icon rtcl-icon-location"></i><span dangerouslySetInnerHTML={{ __html: listing.locations }}></span></li>
										) : ''}
										{(contentVisibility["view"] && listing.views) ? (
											<li className="rt-views"><i className="rtcl-icon rtcl-icon-eye"></i>{listing.views} {__("views", "rtcl-elementor-builder")}</li>
										) : ''}
									</ul>

									{(contentVisibility["grid_content"] && listing.excerpt) ? (
										<p className="rtcl-excerpt" dangerouslySetInnerHTML={{ __html: listing.excerpt.split(' ', content_limit).join(' ') }}></p>
									) : ''}

									{(contentVisibility["price"] && listing.price) ? (
										<div className="item-price listing-price" dangerouslySetInnerHTML={{ __html: listing.price }}></div>
									) : ''}

									<div className="rtin-bottom button-count-3">
										{contentVisibility["phone_btn"] && listing.phone ? (
											<div className="rtin-phn rtin-el-button">
												<a className="rtcl-gb-phone-reveal not-revealed" href="#" data-phone={listing.phone}>
													<i className="rtcl-icon-phone"></i><span>{__("Show Phone No", "rtcl-elementor-builder")}</span>
												</a>
											</div>
										) : ""}

										{contentVisibility["favourit_btn"] && listing.favourite_link ?
											<div className="rtin-fav rtin-el-button" dangerouslySetInnerHTML={{ __html: listing.favourite_link }}></div> : ''}
										{(contentVisibility["quick_btn"] && listing.quick_view) ?
											<div className="rtin-el-button" dangerouslySetInnerHTML={{ __html: listing.quick_view }}></div> : ''}
										{/* {(contentVisibility["compare_btn"] && listing.compare) ?
											<div className="rtin-el-button" dangerouslySetInnerHTML={{ __html: listing.compare }}></div> : ''} */}

									</div>

								</div>
							</div>
						))}
					</div>

					{sliderOptions["arrowNavigation"] &&
						<div className="rtcl-swiper-navigation">
							<span className="rtcl-slider-btn button-left rtcl-icon-angle-left"></span>
							<span className="rtcl-slider-btn button-right rtcl-icon-angle-right"></span>
						</div>
					}
					{sliderOptions["dotNavigation"] && <div className="rtcl-slider-pagination"></div>}

				</div>
			</div>
		) : '',
	]
}

export default StyleGrid3;
