import { InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, SelectControl, ToggleControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import {
	Alignment,
	Typography,
	Dimension,
	Color,
	AdvancedControl,
	RangeDevice,
	BorderDimension
} from '../../components';
import { TAG_NAMES } from '../../globals/Constants'

function Inspector({ attributes, setAttributes }) {
	const {
		resDevice,
		showStatus,
		showAddress,
		showPhone,
		showSocialMedia,
		showEmail,
		contactTypo,
		contactMargin,
		contactPadding,
		contactSocialIconWidth,
		contactSocialIconSize,
		contactFieldHeight,
		btnTypo,
		btnTextColor,
		btnHoverTextColor,
		btnBgColor,
		btnHoverBgColor,
		btnPadding,
		btnBorder
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	return (
		<InspectorControls key={'controls'}>
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "styles",
							title: "Styles",
							className: "rtrb-tab-btn styles",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>
					{(tabs) => (
						<div className="rtrb-tab-content">
							{tabs.name === 'general' && (
								<>
									<PanelBody title={__('General', 'rtcl-elementor-builder')} initialOpen={true}>
										<ToggleControl
											label={__('Show Status')}
											checked={showStatus}
											onChange={(showStatus) => setAttributes({ showStatus })}
										/>
										<ToggleControl
											label={__('Show Address')}
											checked={showAddress}
											onChange={(showAddress) => setAttributes({ showAddress })}
										/>
										<ToggleControl
											label={__('Show Phone')}
											checked={showPhone}
											onChange={(showPhone) => setAttributes({ showPhone })}
										/>
										<ToggleControl
											label={__('Show Social Media')}
											checked={showSocialMedia}
											onChange={(showSocialMedia) => setAttributes({ showSocialMedia })}
										/>
										<ToggleControl
											label={__('Show Store Email')}
											checked={showEmail}
											onChange={(showEmail) => setAttributes({ showEmail })}
										/>
									</PanelBody>
								</>
							)}

							{tabs.name === 'styles' && (
								<>
									<PanelBody title={__('Contact')} initialOpen={true}>
										<Typography
											label={__('Typography', 'rtcl-elementor-builder')}
											value={contactTypo}
											onChange={(val) => setAttributes({ contactTypo: val })}
											resRequiredProps={resRequiredProps}
										/>
										<Dimension
											label={__("Margin", "rtcl-elementor-builder")}
											type="margin"
											responsive={true}
											value={contactMargin}
											onChange={(contactMargin) => {
												setAttributes({ contactMargin })
											}}
											resRequiredProps={resRequiredProps}
										/>
										<Dimension
											label={__('Padding', 'rtcl-elementor-builder')}
											type="padding"
											responsive={true}
											value={contactPadding}
											onChange={(contactPadding) => {
												setAttributes({ contactPadding })
											}}
											resRequiredProps={resRequiredProps}
										/>
										<RangeDevice
											label={__('Social Icon Width (Px)')}
											responsive={true}
											value={contactSocialIconWidth}
											min={1}
											max={200}
											step={1}
											onChange={(val) => setAttributes({ contactSocialIconWidth: val })}
											resRequiredProps={resRequiredProps}
										/>
										<RangeDevice
											label={__('Social Icon Font Size (Px)')}
											responsive={true}
											value={contactSocialIconSize}
											min={1}
											max={100}
											step={1}
											onChange={(val) => setAttributes({ contactSocialIconSize: val })}
											resRequiredProps={resRequiredProps}
										/>
										<RangeDevice
											label={__('Form Field Height (Px)')}
											responsive={true}
											value={contactFieldHeight}
											min={1}
											max={200}
											step={1}
											onChange={(val) => setAttributes({ contactFieldHeight: val })}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>

									<PanelBody title={__('Button')} initialOpen={false}>
										<Typography
											label={__('Typography', 'rtcl-elementor-builder')}
											value={btnTypo}
											onChange={(btnTypo) => setAttributes({ btnTypo })}
											resRequiredProps={resRequiredProps}
										/>
										<Color
											label={__('Text Color')}
											color={btnTextColor}
											onChange={(btnTextColor) => setAttributes({ btnTextColor })}
										/>
										<Color
											label={__('Hover Text Color')}
											color={btnHoverTextColor}
											onChange={(btnHoverTextColor) => setAttributes({ btnHoverTextColor })}
										/>
										<Color
											label={__("Background", "rtcl-elementor-builder")}
											color={btnBgColor}
											onChange={(btnBgColor) => setAttributes({ btnBgColor })}
										/>
										<Color
											label={__("Hover Background", "rtcl-elementor-builder")}
											color={btnHoverBgColor}
											onChange={(btnHoverBgColor) => setAttributes({ btnHoverBgColor })}
										/>
										<Dimension
											label={__('Button Padding')}
											type="padding"
											responsive={true}
											value={btnPadding}
											onChange={(btnPadding) => {
												setAttributes({ btnPadding })
											}}
											resRequiredProps={resRequiredProps}
										/>
										<BorderDimension
											value={btnBorder}
											onChange={(btnBorder) => {
												setAttributes({ btnBorder })
											}}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>
								</>
							)}

							{tabs.name === 'advance' && (
								<>
									<AdvancedControl
										attributes={attributes}
										setAttributes={setAttributes}
									/>
								</>
							)}

						</div>
					)}
				</TabPanel>
			</div>
		</InspectorControls>
	)

}

export default Inspector;