import { InspectorControls } from '@wordpress/block-editor';
import { TabPanel, PanelBody, ToggleControl, SelectControl, TextControl } from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import {
	Typography,
	Color,
	AdvancedControl,
	Dimension
} from '../../components';

function Inspector({ attributes, setAttributes }) {
	const {
		resDevice,
		dateFormate,
		showOpenStatus,
		openStatusText,
		closeStatusText,
		statusTextTypo,
		openStatusTextColor,
		closeStatusTextColor,
		tableDataTypo,
		tableLabelTypo,
		tableLabelColor,
		tableDataColor,
		tableBorderColor,
		tablePadding,
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	return (
		<InspectorControls key={'controls'}>
			<div className="rtrb-panel-control-wrapper">
				<TabPanel
					className="rtrb-tab-panel"
					activeClass="active-tab"
					tabs={[
						{
							name: "general",
							title: "General",
							className: "rtrb-tab-btn general",
						},
						{
							name: "styles",
							title: "Styles",
							className: "rtrb-tab-btn styles",
						},
						{
							name: "advance",
							title: "Advanced",
							className: "rtrb-tab-btn advance",
						},
					]}
				>
					{(tabs) => (
						<div className="rtrb-tab-content">
							{tabs.name === 'general' && (
								<>
									<PanelBody title={__('General', 'rtcl-elementor-builder')} initialOpen={true}>
										<SelectControl
											label={__('Date Formate', 'rtcl-elementor-builder')}
											options={[{ label: '24 Hours', value: '24' }, { label: '12 Hours', value: '12' }]}
											value={dateFormate}
											onChange={(dateFormate) => setAttributes({ dateFormate })}
										/>
										<ToggleControl
											label={__('Show Open Status?', 'rtcl-elementor-builder')}
											checked={showOpenStatus}
											onChange={(showOpenStatus) => setAttributes({ showOpenStatus })}
										/>

										{showOpenStatus && (
											<>
												<TextControl
													label={__('Open Status Text', 'rtcl-elementor-builder')}
													value={openStatusText}
													onChange={(openStatusText) => setAttributes({ openStatusText })}
												/>
												<TextControl
													label={__('Close Status Text', 'rtcl-elementor-builder')}
													value={closeStatusText}
													onChange={(closeStatusText) => setAttributes({ closeStatusText })}
												/>
											</>
										)}
									</PanelBody>
								</>
							)}

							{tabs.name === 'styles' && (
								<>
									<PanelBody title={__('Open Status')} initialOpen={true}>
										<Typography
											label={__('Typography', 'rtcl-elementor-builder')}
											value={statusTextTypo}
											onChange={(val) => setAttributes({ statusTextTypo: val })}
											resRequiredProps={resRequiredProps}
										/>

										<Color
											label={__('Open Status Text Color')}
											color={openStatusTextColor}
											onChange={(openStatusTextColor) => setAttributes({ openStatusTextColor })}
										/>

										<Color
											label={__('Close Status Text Color')}
											color={closeStatusTextColor}
											onChange={(closeStatusTextColor) => setAttributes({ closeStatusTextColor })}
										/>

									</PanelBody>

									<PanelBody title={__('Table')} initialOpen={true}>

										<Typography
											label={__('Label Typography', 'rtcl-elementor-builder')}
											value={tableLabelTypo}
											onChange={(val) => setAttributes({ tableLabelTypo: val })}
											resRequiredProps={resRequiredProps}
										/>

										<Typography
											label={__('Data Typography', 'rtcl-elementor-builder')}
											value={tableDataTypo}
											onChange={(val) => setAttributes({ tableDataTypo: val })}
											resRequiredProps={resRequiredProps}
										/>

										<Color
											label={__('Label Color', 'rtcl-elementor-builder')}
											color={tableLabelColor}
											onChange={(tableLabelColor) => setAttributes({ tableLabelColor })}
										/>

										<Color
											label={__('Data Color', 'rtcl-elementor-builder')}
											color={tableDataColor}
											onChange={(tableDataColor) => setAttributes({ tableDataColor })}
										/>

										<Color
											label={__('Border Color', 'rtcl-elementor-builder')}
											color={tableBorderColor}
											onChange={(tableBorderColor) => setAttributes({ tableBorderColor })}
										/>

										<Dimension
											label={__('Padding', 'rtcl-elementor-builder')}
											type="padding"
											responsive={true}
											value={tablePadding}
											onChange={(tablePadding) => {
												setAttributes({ tablePadding })
											}}
											resRequiredProps={resRequiredProps}
										/>
									</PanelBody>
								</>
							)}

							{tabs.name === 'advance' && (
								<>
									<AdvancedControl
										attributes={attributes}
										setAttributes={setAttributes}
									/>
								</>
							)}
						</div>
					)}
				</TabPanel>
			</div>
		</InspectorControls>
	)

}

export default Inspector;