import { useEffect } from '@wordpress/element';
import {
	mainWrapperStyle,
	softMinifyCssStrings,
	dimensionStyle,
	typographyStyle,
} from '../../helpers';

function styles({ attributes, setAttributes }) {
	const {
		blockId,
		blockCSS,
		labelTypo,
		dataTypo,
		labelColor,
		dataColor,
		customPadding,
		showTitle,
		titleTypo,
		titleColor,
		titleMargin,
	} = attributes;

	const {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss
	} = mainWrapperStyle({
		attributes,
		setAttributes
	});

	const {
		deskDimension: customPaddingStyleDesk,
		tabDimension: customPaddingStyleTab,
		mobDimension: customPaddingStyleMob
	} = dimensionStyle({
		controlValue: customPadding,
		property: 'padding',
		important: true
	});

	const {
		deskTypography: labelTypoStyleDesk,
		tabTypography: labelTypoStyleTab,
		mobTypography: labelTypoStyleMob
	} = typographyStyle({
		controlValue: labelTypo
	});

	const {
		deskTypography: dataTypoStyleDesk,
		tabTypography: dataTypoStyleTab,
		mobTypography: dataTypoStyleMob
	} = typographyStyle({
		controlValue: dataTypo
	});

	const {
		deskTypography: titleTypoStyleDesk,
		tabTypography: titleTypoStyleTab,
		mobTypography: titleTypoStyleMob
	} = typographyStyle({
		controlValue: titleTypo
	});

	const {
		deskDimension: titleMarginStyleDesk,
		tabDimension: titleMarginStyleTab,
		mobDimension: titleMarginStyleMob
	} = dimensionStyle({
		controlValue: titleMargin,
		property: 'margin',
		important: true
	});

	const deskAllStyles = softMinifyCssStrings(`
	.${blockId}.rtcl-block-frontend{
		margin-top: 15px;
		margin-bottom: 15px;
	}
	.${blockId} .rtin-specs-title{
		margin:0;
		margin-bottom:10px;
		font-size:22px;
		${titleMarginStyleDesk}
		${titleTypoStyleDesk}
		${titleColor ? `color:${titleColor};` : ''}
	}
	${!showTitle ? `.${blockId} .rtin-specs-title{display:none;}` : ''}
	
		.${blockId} .custom-field-content-area ul li .rtin-label{
			${labelColor ? `color:${labelColor};` : ''}
			${labelTypoStyleDesk}
		}
		.${blockId} .custom-field-content-area ul li .rtin-title{
			${dataColor ? `color:${dataColor};` : ''}
			${dataTypoStyleDesk}
		}
		.${blockId} .classima-custom-fields ul li::after{
			vertical-align: middle;
		}
		.${blockId} .custom-field-content-area ul li {
			${customPaddingStyleDesk}
		}
		${wrapperStylesDesk}
	`)

	const tabAllStyles = softMinifyCssStrings(`
	.${blockId} .rtin-specs-title{
		${titleMarginStyleTab}
		${titleTypoStyleTab}
	}
	.${blockId} .custom-field-content-area ul li .rtin-label{
		${labelTypoStyleTab}
	}
	.${blockId} .custom-field-content-area ul li .rtin-title{
		${dataTypoStyleTab}
	}
	.${blockId} .custom-field-content-area ul li{
		${customPaddingStyleTab}
	}
		${wrapperStylesTab}
	`)

	const mobAllStyles = softMinifyCssStrings(`
	.${blockId} .rtin-specs-title{
		${titleMarginStyleMob}
		${titleTypoStyleMob}
	}
	.${blockId} .custom-field-content-area ul li .rtin-label{
		${labelTypoStyleMob}
	}
	.${blockId} .custom-field-content-area ul li .rtin-title{
		${dataTypoStyleMob}
	}
	.${blockId} .custom-field-content-area ul li{
		${customPaddingStyleMob}
	}
		${wrapperStylesMob}
	`)

	useEffect(() => {
		const styleObject = {
			desktop: deskAllStyles,
			tablet: tabAllStyles,
			mobile: mobAllStyles,
			customCss: blockWriteCss
		};
		if (JSON.stringify(blockCSS) != JSON.stringify(styleObject)) {
			setAttributes({ blockCSS: styleObject });
		}
	}, [attributes]);

	const allStyle = `
		${deskAllStyles}
		${blockWriteCss}
		@media all and(max-width: 1024px) {
			${tabAllStyles}
		}
		@media all and(max-width: 767px) {
			${mobAllStyles}
		}
	`;

	return allStyle
}

export default styles;